import { lightPAs } from "../config";

export const GanyuBuffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        After firing a Frostflake Arrow, the <LGreen>CRIT Rate</LGreen> of
        subsequent <LGreen>Frostflake Arrows</LGreen> and their resulting{" "}
        <LGreen>bloom effects</LGreen> is increased by <Green>20%</Green> for
        5s.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "self"
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        Celestial Shower grants a <Green>20%</Green>{" "}
        <LGreen>Cryo DMG Bonus</LGreen> to active members in the AoE.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "party",
    addBnes: (ATRs) => {
      ATRs["Cryo DMG Bonus"] += 20;
    }
  },
  {
    src: "Constellation 4",
    desc: () => (
      <>
        Opponents within Celestial Shower take increased DMG. This effect
        strengthens over time. Increased <LGreen>DMG taken</LGreen>
        begins at <Green>5%</Green> and increases by <Green>5%</Green> every 3s,
        up to a maximum of <Green>25%</Green>. (This is actually a DMG bonus.)
      </>
    ),
    isGranted: (char) => char.constellation >= 4,
    affect: "party",
    maxStacks: 5,
    labels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [5],
    addBnes: (_, bonuses, __, stacks) => {
      bonuses.AllDmg += stacks * 5;
    }
  }
];

