import React, { useContext, useState, useMemo, useCallback } from "react";
import { UIContext, FixedContext, ArtsContext, deepCopy } from "../../App";
import { ColoredText } from "../../styledCpns/DataDisplay";
import { Pedestal } from "./styles";
import ArtsDetails from "../ArtsDetails";
import CopySection from "./CopySection";

function getCopyList(artsChest, shownSt, stNames) {
  let thisHasAny = false;
  const arts = artsChest[shownSt].pieces;
  for (let tp in arts) {
    if (arts[tp].set) {
      thisHasAny = true;
      break;
    }
  }
  let result = [];
  if (!thisHasAny) {
    result = stNames.filter((_, i) => {
      let hasNone = true;
      for (let type in artsChest[i].pieces) {
        if (artsChest[i].pieces[type].set) {
          hasNone = false;
          break;
        }
      }
      if (i === shownSt || hasNone) {
        return false;
      }
      return true;
    });
  }
  return result;
}

export default function ArtifactsCard(props) {
  const { setArtsChest } = useContext(FixedContext);
  const { artsChest } = useContext(ArtsContext);
  const { shownSt, stNames } = useContext(UIContext).ui;
  const { sets, pieces } = artsChest[shownSt];
  const [tab, setTab] = useState();
  const copyList = useMemo(() => getCopyList(artsChest, shownSt, stNames), [
    artsChest,
    shownSt,
    stNames
  ]);
  const copyArts = useCallback(
    (state) => {
      setArtsChest((prev) => {
        const pos = stNames.indexOf(state);
        const chest = deepCopy(prev);
        chest[shownSt] = deepCopy(prev[pos]);
        return chest;
      });
    },
    [stNames, shownSt]
  );

  return (
    <Pedestal>
      {copyList.length ? (
        <div className="mt-2 mb-4 px-3 flex justify-end">
          <CopySection copyList={copyList} copy={copyArts} />
        </div>
      ) : null}
      {sets.length ? (
        <div className="mb-2 flex-col align-center">
          {sets.map((set, i) => (
            <ColoredText key={i} variant="subtitle1" color="green">
              {set.name} ({(set.bonusLv + 1) * 2})
            </ColoredText>
          ))}
        </div>
      ) : null}
      <ArtsDetails
        arts={pieces}
        tab={tab}
        setTab={setTab}
        artsIndex={shownSt}
        modalType="change"
      />
    </Pedestal>
  );
}
