import { aHasB } from "../../../App";
import { Green, LGreen, Red } from "../../../styledCpns/DataDisplay";
import { mapToLevels } from "../../misc";
import { mediumPAs, multMult } from "../config";

export const BennettBuffs = [
  {
    src: "Elemental Burst",
    desc: (toSelf, _, { inputs }) => (
      <>
        The character within its AoE gains an <LGreen>ATK Bonus</LGreen> that
        scales based on Bennett's <LGreen>Base ATK</LGreen>.{" "}
        {!toSelf && <Red>ATK Bonus: {BennettBuffs[0].bnValue(...inputs)}.</Red>}
      </>
    ),
    isGranted: () => true,
    affect: "party",
    labels: ["Base ATK", "Elemental Burst Level", "Constellation 1"],
    inputs: [16, 1, false],
    inputTypes: ["text", "text", "check"],
    maxs: [9999, 13, null],
    addBnes: (ATRs, _, __, inputs, toSelf, char, charBC) => {
      const { bnValue } = BennettBuffs[0];
      ATRs.ATK += toSelf
        ? bnValue(
            ATRs["Base ATK"],
            char["Elemental Burst"] + (char.constellation >= 5 ? 3 : 0),
            BennettBuffs[1].isGranted(char) && charBC[1].activated
          )
        : bnValue(...inputs);
    },
    bnValue: (baseATK, lv, boosted) =>
      Math.round(baseATK * ((56 * multMult[2][lv] + (boosted ? 20 : 0)) / 100))
  },
  {
    src: "Constellation 1",
    desc: () => (
      <>
        Fantastic Voyage's <LGreen>ATK increase</LGreen> gains an additional{" "}
        <Green>20%</Green> of Bennett's <LGreen>Base ATK</LGreen>.
      </>
    ),
    isGranted: (char) => char.constellation >= 1,
    affect: "self"
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        Sword, Claymore, Polearm characters inside Fantastic Voyage's radius
        gain a <Green>15%</Green> <LGreen>Pyro DMG Bonus</LGreen> and their
        weapons are <LGreen>infused with Pyro</LGreen>.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "party",
    addBnes: (ATRs) => {
      ATRs["Pyro DMG Bonus"] += 15;
    },
    canInfuse: (charData) =>
      aHasB(["sword", "claymore", "polearm"], charData.weapon),
    infuseElmt: "Pyro",
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: true
  }
];

