import { Green, LGreen, Red } from "../../../styledCpns/DataDisplay";
import { mapToLevels } from "../../misc";
import { lightPAs } from "../config";

export const MonaBuffs = [
  {
    src: "Elemental Burst",
    desc: (toSelf, char, { inputs }) => (
      <>
        Omen increases{" "}
        <Green>{MonaBuffs[0].bnValue(toSelf, char, inputs)}%</Green>{" "}
        <LGreen>DMG taken</LGreen> by opponents. (This is actually a DMG bonus.)
      </>
    ),
    isGranted: () => true,
    affect: "party",
    labels: ["Elemental Burst Level"],
    inputs: [1],
    inputTypes: ["text"],
    maxs: [13],
    addBnes: (_, bonuses, __, inputs, toSelf, char) => {
      bonuses.AllDmg += MonaBuffs[0].bnValue(toSelf, char, inputs);
    },
    bnValue: (toSelf, char, inputs) => {
      const lv = toSelf
        ? char["Elemental Burst"] + (char.constellation >= 3 ? 3 : 0)
        : inputs[0];
      return Math.min(40 + lv * 2, 60);
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: (_, __, { ATRs }) => (
      <>
        Increases Mona's <LGreen>Hydro DMG Bonus</LGreen> by a degree equivalent
        to <Green>20%</Green> of her <LGreen>Energy Recharge</LGreen> rate.{" "}
        <Red>Hydro DMG Bonus: {MonaBuffs[1].bnValue(ATRs)}%.</Red>
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addBnes: (ATRs) => {
      ATRs["Hydro DMG Bonus"] += MonaBuffs[1].bnValue(ATRs);
    },
    bnValue: (ATRs) => Math.round(ATRs["Energy Recharge"] * 2) / 10
  },
  {
    src: "Constellation 1",
    desc: () => (
      <>
        When any of your own party members hits an opponent affected by an Omen,{" "}
        <LGreen>Electro-Charged</LGreen>, <LGreen>Vaporize</LGreen>, and{" "}
        <LGreen>Hydro Swirl DMG</LGreen> increases by <Green>15%</Green> for 8s.
      </>
    ),
    isGranted: (char) => char.constellation >= 1,
    affect: "party",
    addBnes: (_, __, rxnBnes) => {
      rxnBnes["Electro-Charged"] += 15;
      rxnBnes.Swirl += 15;
      rxnBnes.Vaporize += 15;
    }
  },
  {
    src: "Constellation 4",
    desc: () => (
      <>
        When any party member attacks an opponent affected by an Omen, their{" "}
        <LGreen>CRIT Rate</LGreen> is increased by <Green>15%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 4,
    affect: "party",
    addBnes: (ATRs) => {
      ATRs["CRIT Rate"] += 15;
    }
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        Upon entering Illusory Torrent, Mona gains a <Green>60%</Green>{" "}
        <LGreen>DMG increase</LGreen> of her next{" "}
        <LGreen>Charged Attack</LGreen> per second of movement (up to{" "}
        <Green>180%</Green>) for 8s.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    maxStacks: 3,
    addBnes: (_, bonuses, __, stacks) => {
      bonuses.CA.DMG += 60 * stacks;
    }
  }
];

