import React, { useContext, useState } from "react";
import { UIContext, CharContext, ArtsContext, ResultContext } from "../../App";
import { Typography } from "@material-ui/core";
import { ColoredText, Table } from "../../styledCpns/DataDisplay";
import { LimitedBox } from "../../styledCpns/Layout";
import { BtnOnCard } from "../../styledCpns/Inputs";
import ArtsDetails from "../ArtsDetails";
import { pctOf, iptSTypes } from "../../data/misc";
import artifacts from "../../data/artifacts";

export default function Artifacts(props) {
  const { curSt } = useContext(UIContext).ui;
  const { charData } = useContext(CharContext);
  const { artsChest } = useContext(ArtsContext);
  const { artSBnes } = useContext(ResultContext);
  const [tab, setTab] = useState();
  const iptATRs = [...iptSTypes];
  iptATRs.splice(7, 0, charData.vision + " DMG Bonus");

  return tab ? (
    <ArtsDetails
      arts={artsChest[curSt].pieces}
      tab={tab}
      setTab={setTab}
      artsIndex={curSt}
      modalType="equip"
    />
  ) : (
    <div className="px-2 flex-col">
      <LimitedBox height="7.5rem" className="custom-sb">
        <Table>
          {iptATRs.map((type) => (
            <div key={type} className="flex justify-between">
              <Typography variant="subtitle1" className="pl-3 pt-1">
                {type}
              </Typography>
              <Typography
                variant="subtitle1"
                align="right"
                className="pr-3 pt-1"
              >
                {Math.round((artSBnes[type] || 0) * 10) / 10 + pctOf(type)}
              </Typography>
            </div>
          ))}
        </Table>
      </LimitedBox>
      <BtnOnCard
        margin="0.75rem auto 0"
        bgColor="lightGold"
        color="black"
        onClick={() => props.setBoardType("artSBnes")}
      >
        Details
      </BtnOnCard>

      <LimitedBox className="mt-2 custom-sb" height="12.375rem">
        <ColoredText variant="body1" color="orange">
          <strong>Set Bonus</strong>
        </ColoredText>
        {artsChest[curSt].sets.map((set, i) => {
          const content = [];
          for (let i = 0; i <= set.bonusLv; i++) {
            content.push(
              <ColoredText
                key={i}
                component="li"
                variant="body1"
                className="mt-1"
              >
                <ColoredText component="span" color="green">
                  {artifacts[set.name].setBonus[i].type}:
                </ColoredText>{" "}
                {artifacts[set.name].setBonus[i].desc}
              </ColoredText>
            );
          }
          return (
            <div key={i} className="mt-1">
              <ColoredText variant="h6" color="green">
                <strong>{set.name}</strong>
              </ColoredText>
              <ul className="pl-4">{content}</ul>
            </div>
          );
        })}
      </LimitedBox>
      <BtnOnCard
        margin="0.75rem auto 0"
        bgColor="lightGold"
        color="black"
        onClick={() => setTab("flower")}
      >
        Switch
      </BtnOnCard>
    </div>
  );
}
