import React, { useContext } from "react";
import { FixedContext, ArtsContext, deepCopy } from "../../App";
import { Typography } from "@material-ui/core";
import { Option } from "../../styledCpns/Inputs";
import { Setter, SetterSelect, Section } from "../Modifiers.styled";
import debuffsFrArts from "../../data/artifacts/debuffsFrArts";

export default function ArtsDebuff(props) {
  const { setArtsChest, panel2 } = useContext(FixedContext);
  const { artsChest } = useContext(ArtsContext);
  const { activated, set, input } = artsChest[panel2].subDC[0];
  const debuff = debuffsFrArts[set][0];
  return (
    <Section
      checked={activated}
      handleCheck={() =>
        setArtsChest((prev) => {
          const chest = deepCopy(prev);
          chest[panel2].subDC[0].activated = !activated;
          return chest;
        })
      }
      heading={set}
      desc={debuff.desc}
      setters={
        <Setter>
          <Typography className="mr-3">Element</Typography>
          <SetterSelect
            value={input}
            onChange={(e) =>
              setArtsChest((prev) => {
                const chest = deepCopy(prev);
                chest[panel2].subDC[0].input = e.target.value;
                return chest;
              })
            }
          >
            {["Pyro", "Hydro", "Electro", "Cryo"].map((opt) => (
              <Option key={opt}>{opt}</Option>
            ))}
          </SetterSelect>
        </Setter>
      }
    />
  );
}
