import React, { useContext } from "react";
import {
  FixedContext,
  CharContext,
  WpContext,
  WpCsContext,
  ResultContext,
  deepCopy
} from "../../App";
import { Typography } from "@material-ui/core";
import { Option } from "../../styledCpns/Inputs";
import {
  Setter,
  SetterSelect,
  SetterCheckbox,
  Section,
  NoContent
} from "../Modifiers.styled";
import weapons from "../../data/weapons";

export default function WpBuff(props) {
  const { setWpsRack, setSubWpBCs, panel2 } = useContext(FixedContext);
  const { charData } = useContext(CharContext);
  const { wpsRack } = useContext(WpContext);
  const { subWpBCs } = useContext(WpCsContext);
  const { ATRs } = useContext(ResultContext);
  const { name, refinement, BC } = wpsRack[panel2];
  const content = [];

  const wpData = weapons[charData.weapon][name];
  BC.forEach((ctrl, i) => {
    const { activated, index, inputs } = ctrl;
    const buff = wpData.buffs[index];
    const setters =
      buff.labels &&
      buff.labels.map((label, j) => {
        const type = buff.inputTypes[j];
        let input;
        if (type === "stacks") {
          input = (
            <SetterSelect
              value={inputs[j]}
              onChange={(e) =>
                setWpsRack((prev) => {
                  const rack = deepCopy(prev);
                  rack[panel2].BC[i].inputs[j] = Number(e.target.value);
                  return rack;
                })
              }
            >
              {Array.from({ length: buff.values[j] }, (_, k) => k + 1).map(
                (opt) => (
                  <Option key={opt}>{opt}</Option>
                )
              )}
            </SetterSelect>
          );
        } else if (type === "check") {
          input = (
            <SetterCheckbox
              type="checkbox"
              checked={inputs[j]}
              onChange={() =>
                setWpsRack((prev) => {
                  const rack = deepCopy(prev);
                  rack[panel2].BC[i].inputs[j] = !inputs[j];
                  return rack;
                })
              }
            />
          );
        } else if (type === "choices") {
          input = (
            <SetterSelect
              value={inputs[j]}
              onChange={(e) =>
                setWpsRack((prev) => {
                  const rack = deepCopy(prev);
                  rack[panel2].BC[i].inputs[j] = e.target.value;
                  return rack;
                })
              }
            >
              {buff.values[j].map((opt) => (
                <Option key={opt}>{opt}</Option>
              ))}
            </SetterSelect>
          );
        }
        return (
          <Setter key={"j" + j}>
            <Typography className="mr-3">{label}</Typography>
            {input}
          </Setter>
        );
      });
    content.push(
      <Section
        key={"main" + i}
        checked={activated}
        handleCheck={() => {
          setWpsRack((prev) => {
            const rack = deepCopy(prev);
            rack[panel2].BC[i].activated = !activated;
            return rack;
          });
        }}
        heading={name}
        desc={
          wpData.pasvDescs(refinement, { ATRs, showMore: true })[index].buff
        }
        setters={setters}
      />
    );
  });

  for (let wpType in subWpBCs[panel2]) {
    subWpBCs[panel2][wpType].forEach((ctrl, i) => {
      const { activated, refinement, index, inputs } = ctrl;
      const data = weapons[wpType][ctrl.name];
      const setters = [
        <Setter key={0}>
          <Typography className="mr-3">Refinement</Typography>
          <SetterSelect
            value={refinement}
            onChange={(e) =>
              setSubWpBCs((prev) => {
                const BCs = deepCopy(prev);
                BCs[panel2][wpType][i].refinement = Number(e.target.value);
                return BCs;
              })
            }
          >
            {[1, 2, 3, 4, 5].map((opt) => (
              <Option key={opt}>{opt}</Option>
            ))}
          </SetterSelect>
        </Setter>
      ];
      const buff = data.buffs[index];
      if (buff.labels) {
        buff.labels.forEach((label, j) => {
          const type = buff.inputTypes[j];
          let input;
          if (type === "choices") {
            input = (
              <SetterSelect
                value={inputs[j]}
                onChange={(e) =>
                  setSubWpBCs((prev) => {
                    const BCs = deepCopy(prev);
                    BCs[panel2][wpType][i].inputs[j] = e.target.value;
                    return BCs;
                  })
                }
              >
                {buff.values[j].map((opt) => (
                  <Option key={opt}>{opt}</Option>
                ))}
              </SetterSelect>
            );
          }
          setters.push(
            <Setter key={"j" + j}>
              <Typography className="mr-3">{label}</Typography>
              {input}
            </Setter>
          );
        });
      }
      content.push(
        <Section
          key={wpType + i}
          checked={activated}
          handleCheck={() =>
            setSubWpBCs((prev) => {
              const BCs = deepCopy(prev);
              BCs[panel2][wpType][i].activated = !activated;
              return BCs;
            })
          }
          heading={ctrl.name}
          desc={data.pasvDescs(refinement, { showMore: true })[index].buff}
          setters={setters}
        />
      );
    });
  }
  return content.length ? content : <NoContent type="buffs" />;
}
