import { Green, LGreen, Red } from "../../styledCpns/DataDisplay";

const buffsFrArts = {
  "Gladiator's Finale": [
    {
      desc: () => (
        <>
          If the wielder of this artifact set uses a Sword, Claymore or Polearm,
          increases their <LGreen>Normal Attack DMG</LGreen> by{" "}
          <Green>35%</Green>.
        </>
      ),
      affect: "self",
      addBnes: (_, bonuses) => {
        bonuses.NA.DMG += 35;
      }
    }
  ],
  "Wanderer's Troupe": [
    {
      desc: () => (
        <>
          Increases <LGreen>Charged Attack DMG</LGreen> by <Green>35%</Green> if
          the character uses a Catalyst or Bow.
        </>
      ),
      affect: "self",
      addBnes: (_, bonuses) => {
        bonuses.CA.DMG += 35;
      }
    }
  ],
  "Noblesse Oblige": [
    {
      desc: () => (
        <>
          Using an Elemental Burst increases all party members'{" "}
          <LGreen>ATK</LGreen> by <Green>20%</Green> for 12s. This effect cannot
          stack.
        </>
      ),
      affect: "party",
      addBnes: (ATRs) => {
        ATRs["ATK%"] += 20;
      }
    }
  ],
  "Bloodstained Chivalry": [
    {
      desc: () => (
        <>
          After defeating an opponent, increases{" "}
          <LGreen>Charged Attack DMG</LGreen> by <Green>50%</Green>, and reduces
          its Stamina cost to 0 for 10s.
        </>
      ),
      affect: "self",
      addBnes: (_, bonuses) => {
        bonuses.CA.DMG += 50;
      }
    }
  ],
  Thundersoother: [
    {
      desc: () => (
        <>
          Increases <LGreen>DMG</LGreen> against opponents affected by Electro
          by <Green>35%</Green>.
        </>
      ),
      affect: "self",
      addBnes: (_, bonuses) => {
        bonuses.AllDmg += 35;
      }
    }
  ],
  "Crimson Witch of Flame": [
    {
      desc: () => (
        <>
          Using an Elemental Skill increases the{" "}
          <LGreen>2-Piece Set Bonus</LGreen> by <Green>50%</Green> of its{" "}
          <LGreen>starting value</LGreen> for 10s. Max <Green>3</Green> stacks.
        </>
      ),
      affect: "self",
      inputType: "stacks",
      maxStacks: 3,
      addBnes: (ATRs, _, stacks) => {
        ATRs["Pyro DMG Bonus"] += 7.5 * stacks;
      }
    }
  ],
  Lavawalker: [
    {
      desc: () => (
        <>
          Increases <LGreen>DMG</LGreen> against opponents affected by Pyro by{" "}
          <Green>35%</Green>.
        </>
      ),
      affect: "self",
      addBnes: (_, bonuses) => {
        bonuses.AllDmg += 35;
      }
    }
  ],
  "Archaic Petra": [
    {
      desc: () => (
        <>
          Upon obtaining an Elemental Shard created through a Crystallize
          Reaction, all party members gain <Green>35%</Green>{" "}
          <LGreen>DMG</LGreen> Bonus for that{" "}
          <LGreen>particular element</LGreen> for 10s.
        </>
      ),
      affect: "self",
      inputType: "crystallize",
      addBnes: (ATRs, _, choice) => {
        ATRs[choice + " DMG Bonus"] += 35;
      }
    }
  ],
  "Retracing Bolide": [
    {
      desc: () => (
        <>
          While protected by a shield, gain an additional <Green>40%</Green>{" "}
          <LGreen>Normal and Charged Attack DMG</LGreen>.
        </>
      ),
      affect: "self",
      addBnes: (_, bonuses) => {
        bonuses.NA.DMG += 40;
        bonuses.CA.DMG += 40;
      }
    }
  ],
  "Blizzard Strayer": [
    {
      desc: () => (
        <>
          When a character attacks an opponent affected by Cryo, their{" "}
          <LGreen>CRIT Rate</LGreen> is increased by <Green>20%</Green>.
        </>
      ),
      affect: "self",
      addBnes: (ATRs) => {
        ATRs["CRIT Rate"] += 20;
      }
    },
    {
      desc: () => (
        <>
          If the opponent is Frozen, <LGreen>CRIT Rate</LGreen> is increased by
          an additional <Green>20%</Green>.
        </>
      ),
      affect: "self",
      addBnes: (ATRs) => {
        ATRs["CRIT Rate"] += 20;
      }
    }
  ],
  "Heart of Depth": [
    {
      desc: () => (
        <>
          After using an Elemental Skill, increases{" "}
          <LGreen>Normal Attack and Charged Attack DMG</LGreen> by{" "}
          <Green>30%</Green> for 15s.
        </>
      ),
      affect: "self",
      addBnes: (_, bonuses) => {
        bonuses.NA.DMG += 30;
        bonuses.CA.DMG += 30;
      }
    }
  ],
  "Pale Flame": [
    {
      desc: () => (
        <>
          When an Elemental Skill hits an opponent, <LGreen>ATK</LGreen> is
          increased by <Green>9%</Green> for 7s. This effect stacks up to{" "}
          <Green>2</Green> times and can be triggered once every 0.3s. Once 2
          stacks are reached, the <LGreen>2-set effect</LGreen> is increased by{" "}
          <Green>100%</Green>.
        </>
      ),
      affect: "self",
      inputType: "stacks",
      maxStacks: 2,
      addBnes: (ATRs, _, stacks) => {
        ATRs["ATK%"] += 9 * stacks;
        if (Number(stacks) === 2) ATRs["Physical DMG Bonus"] += 25;
      }
    }
  ],
  "Tenacity of the Millelith": [
    {
      desc: () => (
        <>
          When an Elemental Skill hits an opponent, the <LGreen>ATK</LGreen> of
          all nearby party members is increased by <Green>20%</Green> and their
          Shield Strength is increased by 30% for 3s.
        </>
      ),
      affect: "party",
      addBnes: (ATRs) => {
        ATRs["ATK%"] += 20;
        ATRs["Shield Strength"] += 30;
      }
    }
  ],
  "Emblem of Severed Fate": [
    {
      desc: ({ ATRs }) => (
        <>
          Increases <LGreen>Elemental Burst DMG</LGreen> by <Green>25%</Green>{" "}
          of <LGreen>Energy Recharge</LGreen>, up to 75% bonus DMG.{" "}
          <Red>
            DMG Bonus: {buffsFrArts["Emblem of Severed Fate"][0].bnValue(ATRs)}
            %.
          </Red>
        </>
      ),
      affect: "self",
      final: true,
      addBnes: (ATRs, bonuses) => {
        bonuses.EB.DMG += buffsFrArts["Emblem of Severed Fate"][0].bnValue(
          ATRs
        );
      },
      bnValue: (ATRs) =>
        Math.min(Math.round(ATRs["Energy Recharge"] / 0.04) / 100, 75)
    }
  ],
  "Shimenawa's Reminiscence": [
    {
      desc: () => (
        <>
          When casting an Elemental Skill, the character loses 15 Energy and{" "}
          <LGreen>Normal/Charged/Plunging Attack DMG</LGreen> is increased by{" "}
          <Green>50%</Green> for 10s.
        </>
      ),
      affect: "self",
      addBnes: (_, bonuses) => {
        bonuses.NA.DMG += 50;
        bonuses.CA.DMG += 50;
        bonuses.PA.DMG += 50;
      }
    }
  ]
};

export default buffsFrArts;
