import "./styles.css";
import React, { createContext, useState, useCallback, useEffect } from "react";
import { CssBaseline } from "@material-ui/core";
import { StyledApp, StyledCol, StyledMobileUI } from "./styledCpns/Layout";
import Character from "./components/Character";
import Setups from "./components/Setups";
import Modifiers from "./components/Modifiers";
import DmgTable from "./components/DmgTable";
import Modal from "./components/Modal";
import Possessions from "./components/Possessions";
import Intro from "./components/Intro";

import characters from "./data/characters";
import {
  initChar,
  initCharCs,
  initWp,
  initArts,
  initParty,
  initElmtC,
  initTarget,
  getDfltWp
} from "./data/initConfig";
import { getSubWpBC, getRsns, getFinalInfs, getWpRange } from "./helpers";
import getStats from "./calculators/stats";
import { getAtkDmg } from "./calculators/dmg";

export function deepCopy(item) {
  return JSON.parse(JSON.stringify(item));
}

export function aHasB(arr, str) {
  return arr.indexOf(str) !== -1;
}

export const UIContext = createContext();
export const CharContext = createContext();
export const CharCsContext = createContext();
export const WpContext = createContext();
export const WpCsContext = createContext();
export const ArtsContext = createContext();
export const PartyContext = createContext();
export const ElmtCsContext = createContext();
export const TargetContext = createContext();
export const FixedContext = createContext();
export const ResultContext = createContext();

export default function App() {
  const [ui, setUI] = useState({
    modalName: null,
    modalType: "",
    curSt: 0,
    shownSt: 0,
    stNames: ["Set-up 1"],
    cmpSts: [0],
    introOn: true
  });
  const { curSt, shownSt, stNames } = ui;
  const [char, setChar] = useState(initChar("Albedo"));
  const [charCs, setCharCs] = useState([initCharCs(char.name)]);
  const charData = characters[char.name];
  const [wpsRack, setWpsRack] = useState([
    initWp({ name: getDfltWp(char.name) })
  ]);
  const [subWpBCs, setSubWpBCs] = useState([{}]);
  const [artsChest, setArtsChest] = useState([initArts()]);
  const [parties, setParties] = useState([initParty()]);
  const [elmtCs, setElmtCs] = useState([initElmtC()]);
  const [target, setTarget] = useState(initTarget());

  const initSession = useCallback((char, wp, arts) => {
    setChar(char);
    setCharCs([initCharCs(char.name)]);
    setWpsRack([wp]);
    setSubWpBCs([{}]);
    setArtsChest([arts]);
    setParties([initParty()]);
    setElmtCs([initElmtC()]);
    setTarget(initTarget());
    setUI((prev) => ({
      ...prev,
      curSt: 0,
      shownSt: 0,
      stNames: ["Set-up 1"],
      cmpSts: [0]
    }));
  }, []);

  const setTm = useCallback(
    (party, vision, wpRange, doAdd, weapon, oldWp) => {
      setElmtCs((prev) => {
        const elmtCs = deepCopy(prev);
        const elmtC = elmtCs[shownSt];
        const oldRsns = getRsns(parties[shownSt], charData.vision);
        const newRsns = getRsns(party, charData.vision);
        oldRsns.forEach((rsn) => {
          if (!aHasB(newRsns, rsn)) {
            for (let i in elmtC.rsns) {
              if (elmtC.rsns[i].name === rsn) {
                elmtC.rsns.splice(i, 1);
                break;
              }
            }
          }
        });
        newRsns.forEach((rsn) => {
          if (!aHasB(oldRsns, rsn)) {
            elmtC.rsns.push({ name: rsn, activated: vision === "Pyro" });
          }
        });
        return elmtCs;
      });
      setParties((prev) => {
        const parties = deepCopy(prev);
        parties[shownSt] = party;
        return parties;
      });
      setSubWpBCs((prev) => {
        const BCs = deepCopy(prev);
        if (!aHasB(wpRange, weapon)) {
          doAdd
            ? (BCs[shownSt][weapon] = getSubWpBC(weapon))
            : delete BCs[shownSt][weapon];
        }
        if (!aHasB(getWpRange(party), oldWp)) {
          delete BCs[shownSt][oldWp];
        }
        return BCs;
      });
    },
    [charData.vision, parties, shownSt]
  );

  const [tempSts, setTempSts] = useState();
  useEffect(() => {
    setTempSts(
      stNames.map((name, i) => ({
        name,
        isOld: true,
        index: i,
        checked: aHasB(ui.cmpSts, i)
      }))
    );
  }, [stNames, curSt, ui.cmpSts]);

  const stsATRs = [];
  const stsArtSBnes = [];
  const stsRxnBnes = [];
  const stsAtkDmg = [];
  for (let ldt = 0; ldt < stNames.length; ldt++) {
    const infusions = getFinalInfs(charCs[ldt], charData, parties[ldt]);
    const { ATRs, bonuses, artSBnes, rxnBnes } = getStats(
      char,
      charData,
      charCs[ldt].BC,
      wpsRack[ldt],
      subWpBCs[ldt],
      artsChest[ldt],
      elmtCs[ldt].rsns,
      parties[ldt]
    );
    stsATRs.push(ATRs);
    stsArtSBnes.push(artSBnes);
    stsRxnBnes.push(rxnBnes);
    stsAtkDmg.push(
      getAtkDmg(
        deepCopy(char),
        charCs[ldt],
        parties[ldt],
        charData,
        wpsRack[ldt],
        artsChest[ldt].subDC,
        ATRs,
        bonuses,
        rxnBnes,
        infusions,
        elmtCs[ldt],
        target
      )
    );
  }

  const [poss, setPoss] = useState(null);
  const [teapot, setTeapot] = useState([]);
  const [inv, setInv] = useState({
    Weapons: [],
    Artifacts: [],
    Loadouts: []
  });
  return (
    <>
      <CssBaseline />
      <FixedContext.Provider
        value={{
          setUI,
          setTempSts,
          setChar,
          setCharCs,
          setWpsRack,
          setSubWpBCs,
          setArtsChest,
          setParties,
          setElmtCs,
          setTarget,
          panel2: shownSt,
          handlers: { setTm, initSession },
          setPoss,
          setTeapot,
          setInv
        }}
      >
        <UIContext.Provider value={{ ui, tempSts }}>
          <CharContext.Provider value={{ char, charData }}>
            <CharCsContext.Provider value={{ charCs }}>
              <WpContext.Provider value={{ wpsRack }}>
                <WpCsContext.Provider value={{ subWpBCs }}>
                  <ArtsContext.Provider value={{ artsChest }}>
                    <PartyContext.Provider value={{ parties }}>
                      <ElmtCsContext.Provider value={{ elmtCs }}>
                        <TargetContext.Provider value={{ target }}>
                          <ResultContext.Provider
                            value={{
                              ATRs: stsATRs[curSt],
                              artSBnes: stsArtSBnes[curSt],
                              rxnBnes: stsRxnBnes
                            }}
                          >
                            {window.innerWidth <= 668 ? (
                              <MobileUI stsAtkDmg={stsAtkDmg} teapot={teapot} />
                            ) : (
                              <StyledApp>
                                <StyledCol>
                                  <Character teapot={teapot} />
                                  <Modifiers />
                                </StyledCol>
                                <StyledCol>
                                  <Setups />
                                  <DmgTable stsAtkDmg={stsAtkDmg} />
                                </StyledCol>
                              </StyledApp>
                            )}
                            {poss && (
                              <Possessions
                                poss={poss}
                                teapot={teapot}
                                inv={inv}
                              />
                            )}
                            {ui.introOn && <Intro />}
                            {ui.modalName && <Modal />}
                          </ResultContext.Provider>
                        </TargetContext.Provider>
                      </ElmtCsContext.Provider>
                    </PartyContext.Provider>
                  </ArtsContext.Provider>
                </WpCsContext.Provider>
              </WpContext.Provider>
            </CharCsContext.Provider>
          </CharContext.Provider>
        </UIContext.Provider>
      </FixedContext.Provider>
    </>
  );
}

function MobileUI({ stsAtkDmg, teapot }) {
  const [tab, setTab] = useState("Character");
  const tabs = ["Character", "Modifiers", "Set-ups", "DMG"];
  const slide = (name) => {
    document.getElementById("mobile_ui").scrollLeft =
      window.innerWidth * tabs.indexOf(name);
  };
  return (
    <StyledMobileUI id="mobile_ui">
      <div id="nav_bar" className="cmn-shadow">
        {tabs.map((name) => (
          <div
            key={name}
            className={"tab" + (name === tab ? " active" : "")}
            onClick={() => {
              setTab(name);
              slide(name);
            }}
          >
            <p>{name}</p>
          </div>
        ))}
      </div>
      <div className="slides">
        <Character teapot={teapot} />
      </div>
      <div className="slides">
        <Modifiers />
      </div>
      <div className="slides">
        <Setups />
      </div>
      <div className="slides">
        <DmgTable stsAtkDmg={stsAtkDmg} />
      </div>
    </StyledMobileUI>
  );
}
