export const DilucBuffs = [
  {
    src: "Elemental Burst",
    desc: () => (
      <>
        After Diluc releases Dawn, his blade is{" "}
        <LGreen>infused with Pyro</LGreen>.
      </>
    ),
    isGranted: () => true,
    affect: "self",
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: true
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        Diluc gains <Green>20%</Green> <LGreen>Pyro DMG Bonus</LGreen> during
        the duration of The Pyro Enchantment provided by Dawn.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addBnes: (ATRs) => {
      ATRs["Pyro DMG Bonus"] += 20;
    }
  },
  {
    src: "Constellation 1",
    desc: () => (
      <>
        Diluc deals <Green>15%</Green> <LGreen>more DMG</LGreen> to opponents
        whose HP is above 50%.
      </>
    ),
    isGranted: (char) => char.constellation >= 1,
    affect: "self",
    addBnes: (_, bonuses) => {
      bonuses.AllDmg += 15;
    }
  },
  {
    src: "Constellation 2",
    desc: () => (
      <>
        When Diluc takes DMG, his <LGreen>ATK</LGreen> increases by{" "}
        <Green>10%</Green> and <LGreen>ATK SPD</LGreen> increases by{" "}
        <Green>5%</Green> for 10s, up to <Green>3</Green> times.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    maxStacks: 3,
    addBnes: (ATRs, _, __, stacks) => {
      ATRs["ATK%"] += stacks * 10;
      ATRs["Normal ATK SPD"] += stacks * 5;
      ATRs["Charged ATK SPD"] += stacks * 5;
    }
  },
  {
    src: "Constellation 4",
    desc: () => (
      <>
        2s after casting Searing Onslaught, casting the next{" "}
        <LGreen>Searing Onslaught</LGreen> in the combo deals <Green>40%</Green>{" "}
        <LGreen>additional DMG</LGreen>. This effect lasts for 2s.
      </>
    ),
    isGranted: (char) => char.constellation >= 4,
    affect: "self",
    addBnes: (_, bonuses) => {
      bonuses.ES.DMG += 40;
    }
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        After casting Searing Onslaught, the next <Green>2</Green>{" "}
        <LGreen>Normal Attacks</LGreen> within the next 6s will have their{" "}
        <LGreen>DMG and ATK SPD</LGreen> increased by <Green>30%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    addBnes: (ATRs, bonuses) => {
      bonuses.NA.DMG += 30;
      ATRs["Normal ATK SPD"] += 30;
    }
  }
];

