import React, { useContext } from "react";
import { FixedContext, ElmtCsContext, deepCopy } from "../../App";
import { Green, LGreen } from "../../styledCpns/DataDisplay";
import { Section } from "../Modifiers.styled";

export default function ElmtDebuff(props) {
  const { setElmtCs, panel2 } = useContext(FixedContext);
  const { elmtCs } = useContext(ElmtCsContext);
  const { rsns, superconduct } = elmtCs[panel2];
  const content = [];
  let geoI = -1;
  for (let i in rsns) {
    if (rsns[i].name === "Enduring Rock") {
      geoI = i;
      break;
    }
  }
  if (geoI >= 0) {
    const { activated } = rsns[geoI];
    content.push(
      <Section
        key="rock"
        checked={activated}
        handleCheck={() =>
          setElmtCs((prev) => {
            const Cs = deepCopy(prev);
            Cs[panel2].rsns[geoI].activated = !activated;
            return Cs;
          })
        }
        heading="Enduring Rock"
        desc={
          <>
            Shielded characters dealing DMG to enemies will decrease their{" "}
            <LGreen>Geo RES</LGreen> by <Green>20%</Green> for 15s.
          </>
        }
      />
    );
  }
  content.push(
    <Section
      key="sc"
      checked={superconduct}
      handleCheck={() =>
        setElmtCs((prev) => {
          const Cs = deepCopy(prev);
          Cs[panel2].superconduct = !superconduct;
          return Cs;
        })
      }
      heading="Superconduct"
      desc={
        <>
          Reduces the <LGreen>Physical RES</LGreen> of enemies by{" "}
          <Green>40%</Green> for 12 seconds.
        </>
      }
    />
  );
  return content;
}
