import React, { useState, useContext } from "react";
import { deepCopy, FixedContext, UIContext } from "../../App";
import { Typography, Collapse } from "@material-ui/core";
import { CollapseHeading, ColoredBox } from "../../styledCpns/DataDisplay";
import { StyledOverview, StyledDetails } from "./styles";
import Forge from "./Forge";
import LdtFormat from "./LoadoutFormat";
import LdtShelf from "./LoadoutShelf";
import { initArts } from "../../data/initConfig";

export default function Loadouts({ data, teapot }) {
  const [coord, setCoord] = useState({ store: "", i: -1, type: "" });
  const { setInv, setArtsChest, setPoss, setTeapot } = useContext(FixedContext);
  const { shownSt } = useContext(UIContext).ui;
  return (
    <>
      <StyledOverview className="full-h">
        <div className="full-h overflow-auto hide-sb">
          <div className="pr-3">
            {data.map((ldt, i) => (
              <LdtFormat
                key={i}
                ldt={ldt}
                store="Inventory"
                i={i}
                coord={coord}
                setCoord={setCoord}
                remove={() => {
                  setInv((prev) => {
                    const inv = deepCopy(prev);
                    inv.Loadouts.splice(i, 1);
                    return inv;
                  });
                  setCoord({ i: -1 });
                }}
                pick={() => {
                  setArtsChest((prev) => {
                    const chest = deepCopy(prev);
                    chest[shownSt] = initArts(ldt.arts);
                    return chest;
                  });
                  setPoss(null);
                }}
              />
            ))}
            <LoadoutsInUse teapot={teapot} coord={coord} setCoord={setCoord} />
          </div>
        </div>
      </StyledOverview>
      <StyledDetails forChar={false}>
        <div className="inner">
          {coord.i !== -1 ? (
            coord.store === "Teapot" ? (
              <LdtShelf
                info={teapot[coord.i].arts[coord.type]}
                type={coord.type}
                remove={() => {
                  setTeapot((prev) => {
                    const chars = [...prev];
                    chars[coord.i].arts[coord.type].set = null;
                    return chars;
                  });
                  setCoord({ i: -1 });
                }}
              />
            ) : (
              <Forge
                forItems={false}
                isWp={false}
                info={data[coord.i].arts[coord.type]}
                index={coord.i}
                type={coord.type}
                setPosition={() => setCoord({ i: -1 })}
              />
            )
          ) : null}
        </div>
      </StyledDetails>
    </>
  );
}

function anyLdtInUse(teapot) {
  for (let char of teapot) {
    for (let type in char.arts) {
      if (char.arts[type].set) return true;
    }
  }
  return false;
}

function LoadoutsInUse({ teapot, coord, setCoord }) {
  const { setArtsChest, setPoss, setTeapot } = useContext(FixedContext);
  const { shownSt } = useContext(UIContext).ui;
  const [on, setOn] = useState(false);
  return (
    <ColoredBox className="mt-3" bgColor="cardBg3">
      <CollapseHeading
        className={on ? "open" : "close"}
        onClick={() => setOn(!on)}
      >
        Loadouts used by Characters
      </CollapseHeading>
      <Collapse in={on}>
        <div className="mt-2 px-2">
          {anyLdtInUse(teapot) ? (
            teapot.map(({ name, arts }, i) => {
              const ldt = { name: `${name}'s`, arts };
              let hasAny = false;
              for (let type in arts) {
                if (arts[type].set) {
                  hasAny = true;
                  break;
                }
              }
              return hasAny ? (
                <LdtFormat
                  key={i}
                  ldt={ldt}
                  store="Teapot"
                  i={i}
                  coord={coord}
                  setCoord={setCoord}
                  remove={() => {
                    setTeapot((prev) => {
                      const chars = [...prev];
                      chars[i].arts = initArts().pieces;
                      return chars;
                    });
                    setCoord({ i: -1 });
                  }}
                  pick={() => {
                    setArtsChest((prev) => {
                      const chest = deepCopy(prev);
                      chest[shownSt] = initArts(ldt.arts);
                      return chest;
                    });
                    setPoss(null);
                  }}
                />
              ) : null;
            })
          ) : (
            <Typography className="my-4" align="center">
              No Artifact is in use.
            </Typography>
          )}
        </div>
      </Collapse>
    </ColoredBox>
  );
}
