import React, { useState, useEffect } from "react";
import { Select, Option, IconBtn } from "../../styledCpns/Inputs";

export default function CopySec({ copyList, copy }) {
  const [state, setState] = useState();
  useEffect(() => {
    setState(copyList[0]);
  }, [copyList]);
  return (
    <div className="flex align-center">
      <Select
        className="p-1"
        fontWeight={700}
        bgColor="white"
        value={state}
        onChange={(e) => setState(e.target.value)}
      >
        {copyList.map((name, j) => (
          <Option key={j}>{name}</Option>
        ))}
      </Select>
      <IconBtn
        className="ml-3 glow-on-hover"
        icon="fas fa-copy"
        size="2rem"
        color="green"
        bgColor="transparent"
        fontSize="1.5rem"
        onClick={() => copy(state)}
      />
    </div>
  );
}
