import styled from "styled-components";
import { colorCodes } from "../../data/misc";

export const Table = styled.table`
  margin-bottom: 0.5rem;
  min-width: 100%;
  border-collapse: collapse;
  th,
  td {
    padding: 0.25rem 0.5rem;
    border-left: 1px solid ${colorCodes.cardBg1};
    border-right: 1px solid ${colorCodes.cardBg1};
    cursor: default;
  }
  td:not(:first-child) {
    text-align: right;
  }
  tr:nth-child(2n) {
    background-color: ${colorCodes.cardBg2};
  }
  tr:nth-child(2n + 1) {
    background-color: ${colorCodes.cardBg1};
  }
  tr:first-child {
    background-color: #01121b;
  }
  tr:not(:first-child):hover {
    background-color: #8a1200;
  }
  .sticky-col {
    position: sticky;
    z-index: 2;
    left: 0;
    min-width: 8.5rem;
    background-color: inherit;
  }
  .limited-width {
    width: 8.5rem;
  }
  .fas {
    position: absolute;
    top: 50%;
    right: 0.375rem;
    margin-top: -0.425rem;
    font-size: inherit;
  }
  .fa-long-arrow-alt-up {
    color: ${colorCodes["green"]};
  }
  .fa-long-arrow-alt-down {
    color: ${colorCodes["lightRed"]};
  }
  .tooltip_wrapper {
    padding-right: 1.125rem !important;
    min-width: 5rem;
  }
  .tooltip_wrapper:hover .tooltip {
    display: block;
  }
  .tooltip {
    position: absolute;
    bottom: calc(50% + 0.625rem);
    right: 1rem;
    z-index: 1;
    display: none;
    padding: 0.25rem 0.5rem 0.125rem;
    border-radius: 0.25rem;
    background: black;
  }
  .lower {
    color: #ff5d61;
  }
  .higher {
    color: ${colorCodes.green};
  }
`;
