import React, { useState, useContext, useMemo } from "react";
import { UIContext, CharContext, WpContext } from "../../App";
import { Box, Typography } from "@material-ui/core";
import { Select, Option } from "../../styledCpns/Inputs";
import { Table } from "./styles";
import { Card, LimitedBox } from "../../styledCpns/Layout";
import CmpTable from "./CmpTable";
import DfltTable from "./DfltTable";
import { getNamesForTables } from "../../helpers";
import { colorCodes } from "../../data/misc";

export default function DmgTable({ stsAtkDmg }) {
  const { cmpSts, curSt } = useContext(UIContext).ui;
  const { charData } = useContext(CharContext);
  const { wpsRack } = useContext(WpContext);
  const [focus, setFocus] = useState("Average");

  const namesForTables = useMemo(
    () => getNamesForTables(charData, wpsRack, cmpSts),
    [charData, wpsRack, cmpSts]
  );

  let cmpMode = cmpSts.length > 1;
  return (
    <Card className="px-4 pt-3 pb-4" bgColor="cardBg3">
      {cmpMode ? (
        <div className="mb-3 flex justify-center">
          <Typography className="mr-2">Choose a focus</Typography>
          <Select
            fontSize="1rem"
            align="center"
            color="lightGold"
            value={focus}
            onChange={(e) => setFocus(e.target.value)}
          >
            {["Non-crit", "Crit", "Average"].map((opt) => (
              <Option key={opt}>{opt}</Option>
            ))}
          </Select>
        </div>
      ) : (
        <Typography className="mt-1 mb-3 mx-3" variant="body2" align="center">
          Activate Compare Mode in Settings.
        </Typography>
      )}
      <LimitedBox height="34.4rem" className="hide-sb">
        {namesForTables.map((name, i) => {
          const curAtkVals = stsAtkDmg[curSt][name.atkName];
          return (
            <div
              key={i}
              className={
                "flex-col" + (i === namesForTables.length - 1 ? "" : " mb-3")
              }
            >
              <Box
                className="mx-auto px-3 mb-2"
                borderRadius="1rem"
                bgcolor={colorCodes.orange}
              >
                <Typography variant="h5" color="textSecondary">
                  <strong>{name.atkName}</strong>
                </Typography>
              </Box>
              <Box overflow="auto" className="custom-sb">
                <Table>
                  {cmpMode ? (
                    <CmpTable
                      name={name}
                      curAtkVals={curAtkVals}
                      stsAtkDmg={stsAtkDmg}
                      focus={focus}
                    />
                  ) : (
                    <DfltTable
                      fieldNames={name.fieldNames}
                      curAtkVals={curAtkVals}
                    />
                  )}
                </Table>
              </Box>
            </div>
          );
        })}
      </LimitedBox>
    </Card>
  );
}
