import { Typography } from "@material-ui/core";
import { ColoredText } from "../../styledCpns/DataDisplay";

export default function DfltTable({ curAtkVals, fieldNames }) {
  return (
    <tbody>
      <tr>
        <Typography
          component="th"
          variant="subtitle2"
          className="limited-width"
        />
        <Typography component="th" variant="subtitle2" className="opacity-9">
          Non-crit
        </Typography>
        <Typography component="th" variant="subtitle2">
          Crit
        </Typography>
        <ColoredText component="th" variant="subtitle2" color="lightGold">
          Avg.
        </ColoredText>
      </tr>
      {fieldNames.map((name, i) => {
        const NonCrit = curAtkVals[name]["Non-crit"];
        const { Crit, Average } = curAtkVals[name];
        return (
          <tr key={i}>
            <Typography
              className="limited-width"
              component="td"
              variant="subtitle2"
            >
              {name}
            </Typography>
            <Typography
              component="td"
              variant="subtitle2"
              className="opacity-9"
            >
              {Array.isArray(NonCrit)
                ? NonCrit.map((nc) => Math.round(nc)).join(" + ")
                : Math.round(NonCrit)}
            </Typography>
            <Typography component="td" variant="subtitle2">
              {Array.isArray(Crit)
                ? Crit.map((c) => Math.round(c)).join(" + ")
                : Math.round(Crit)}
            </Typography>
            <ColoredText component="td" variant="subtitle2" color="lightGold">
              {Array.isArray(Average)
                ? Average.map((a) => Math.round(a)).join(" + ")
                : Math.round(Average)}
            </ColoredText>
          </tr>
        );
      })}
    </tbody>
  );
}
