import React, { useState } from "react";
import { CollapseHeading } from "../../styledCpns/DataDisplay";
import SelfDebuff from "./SelfDebuff";
import PartyDebuff from "./PartyDebuff";
import ElmtDebuff from "./ElmtDebuff";
import ArtsDebuff from "./ArtsDebuff";
import { StyledCollapse } from "../Modifiers.styled";

export default function Debuff(props) {
  const [expanding, setExpanding] = useState([]);
  return (
    <div>
      {["Resonance & Reactions", "Self", "Party", "Artifacts"].map(
        (cate, i) => (
          <div key={cate} className={expanding[i] ? "mb-4" : ""}>
            <CollapseHeading
              className={expanding[i] ? "open" : "close"}
              mb="0.75rem"
              onClick={() =>
                setExpanding((prev) => {
                  const newEpd = [...prev];
                  newEpd[i] = !newEpd[i];
                  return newEpd;
                })
              }
            >
              From {cate}
            </CollapseHeading>
            <StyledCollapse in={expanding[i]}>
              {
                {
                  "Resonance & Reactions": <ElmtDebuff />,
                  Self: <SelfDebuff />,
                  Party: <PartyDebuff />,
                  Artifacts: <ArtsDebuff />
                }[cate]
              }
            </StyledCollapse>
          </div>
        )
      )}
    </div>
  );
}
