import React, { useState, useContext, useCallback } from "react";
import {
  deepCopy,
  aHasB,
  FixedContext,
  UIContext,
  CharContext,
  PartyContext,
  ArtsContext
} from "../../App";
import ModalContent from "./ModalContent";
import { ModalCarpet } from "./styles";
import characters from "../../data/characters";
import weapons from "../../data/weapons";
import artifacts from "../../data/artifacts";
import { initChar, initWp, initArts, getDfltWp } from "../../data/initConfig";
import {
  getCharsData,
  addTm,
  addTempSt,
  setWp,
  setArt,
  createWp,
  createArt,
  archiveArt
} from "./handlers";

export default function Modal(props) {
  const {
    setUI,
    setWpsRack,
    setArtsChest,
    setTempSts,
    setInv,
    handlers
  } = useContext(FixedContext);
  const { ui } = useContext(UIContext);
  const { char, charData } = useContext(CharContext);
  const { parties } = useContext(PartyContext);
  const { artsChest } = useContext(ArtsContext);
  const { modalName, modalType, shownSt } = ui;
  const [filter, setFilter] = useState({ type: "", value: "" });

  let data = [];
  switch (modalName) {
    case "Characters":
      data = getCharsData(modalType, char.name, parties[shownSt], filter);
      break;
    case "Weapons":
      const type =
        modalType.slice(0, 6) === "create"
          ? modalType.slice(7)
          : charData.weapon;
      for (let name in weapons[type]) {
        const { icon, rarity } = weapons[type][name];
        data.push({ name, icon, rarity });
      }
      break;
    default:
      for (let name in artifacts) {
        const { icon } = artifacts[name][modalName];
        data.push({ name, icon, rarity: "gold" });
      }
      break;
  }
  const index = aHasB(["switch", "equip"], modalType) ? ui.curSt : shownSt;

  const choose = useCallback(
    (name) => {
      switch (modalName) {
        case "Characters":
          const { vision, weapon } = characters[name];
          modalType === "switch"
            ? handlers.initSession(
                initChar(name),
                initWp({ name: getDfltWp(name) }),
                initArts()
              )
            : addTm(
                deepCopy(parties[shownSt]),
                modalType,
                { name, vision, weapon },
                handlers.setTm
              );
          break;
        case "Weapons":
          if (modalType === "add") {
            addTempSt(name, setTempSts);
          } else if (aHasB(["equip", "change"], modalType)) {
            setWp(name, index, setWpsRack);
          } else {
            createWp(name, modalType.slice(7), setInv);
          }
          break;
        default:
          const mainSType =
            modalName === "flower"
              ? "HP"
              : modalName === "plume"
              ? "ATK"
              : "ATK%";
          if (aHasB(["equip", "change"], modalType)) {
            setArt(index, modalName, setArtsChest, { set: name, mainSType });
          } else if (modalType === "create") {
            createArt(name, mainSType, modalName, setInv);
          } else {
            archiveArt(
              modalType.split("/")[1],
              name,
              mainSType,
              modalName,
              setInv
            );
          }
          break;
      }
      setUI((prev) => ({ ...prev, modalName: null }));
    },
    [charData.weapon, index, modalName, modalType, parties, shownSt]
  );

  const copyArt = useCallback(
    (stName) => {
      const pos = ui.stNames.indexOf(stName);
      if (aHasB(["equip", "change"], modalType)) {
        setArt(
          index,
          modalName,
          setArtsChest,
          artsChest[pos].pieces[modalName]
        );
      } else {
        setInv((prev) => {
          const inv = deepCopy(prev);
          if (modalType === "create") {
            inv.Artifacts.unshift({
              ...deepCopy(artsChest[pos].pieces[modalName]),
              type: modalName
            });
          } else {
            inv.Loadouts[modalType.split("/")[1]].arts[modalName] = deepCopy(
              artsChest[pos].pieces[modalName]
            );
          }
          return inv;
        });
      }
      setUI((prev) => ({ ...prev, modalName: null }));
    },
    [index, modalName, ui.stNames]
  );

  return (
    <ModalCarpet className="not-center-y">
      <ModalContent
        data={data}
        choose={choose}
        copyArt={copyArt}
        index={index}
        filter={filter}
        setFilter={(chosen, newFilter) =>
          setFilter(chosen ? { type: "", value: "" } : newFilter)
        }
      />
    </ModalCarpet>
  );
}
