import React, { useState, useContext, useEffect, useCallback } from "react";
import {
  FixedContext,
  UIContext,
  CharContext,
  WpContext,
  ArtsContext
} from "../../App";
import { Box } from "@material-ui/core";
import { ColoredText, Img } from "../../styledCpns/DataDisplay";
import { Card, LimitedBox } from "../../styledCpns/Layout";
import { MainSelect, BtnOnCard } from "../../styledCpns/Inputs";
import GeneralInfo from "./GeneralInfo";
import Attributes from "./Attributes";
import Weapons from "./Weapons";
import Artifacts from "./Artifacts";
import Constellation from "./Constellation";
import Talents from "./Talents";
import StatsDetails from "./StatsDetails";
import { wikia } from "../../data/misc";

function createChar(char, { BC, ...wp }, { pieces: arts }) {
  return { ...char, wp, arts };
}

export default function Character({ teapot }) {
  const [tab, setTab] = useState("Attributes");
  const [boardType, setBoardType] = useState(null);
  const [msg, setMsg] = useState(null);
  const { setPoss, setTeapot } = useContext(FixedContext);
  const { curSt } = useContext(UIContext).ui;
  const { char } = useContext(CharContext);
  const { wpsRack } = useContext(WpContext);
  const { artsChest } = useContext(ArtsContext);
  const contentAt = {
    Attributes: <Attributes setBoardType={setBoardType} />,
    Weapons: <Weapons />,
    Artifacts: <Artifacts setBoardType={setBoardType} />,
    Constellation: <Constellation />,
    Talents: <Talents />
  };
  useEffect(() => {
    setMsg(null);
  }, [char.name]);
  const pending = msg && msg.slice(-5) === "Info?";

  const tryAdd = useCallback(() => {
    let pos = -1;
    for (let i in teapot) {
      if (teapot[i].name === char.name) {
        pos = i;
        break;
      }
    }
    if (pos === -1) {
      setTeapot((prev) => {
        const chars = [...prev];
        chars.push(createChar(char, wpsRack[curSt], artsChest[curSt]));
        return chars;
      });
      setMsg(`${char.name} has come Home!`);
    } else {
      if (pending) {
        setTeapot((prev) => {
          const chars = [...prev];
          chars[pos] = createChar(char, wpsRack[curSt], artsChest[curSt]);
          return chars;
        });
        setMsg(`${char.name}'s Info has been changed.`);
      } else {
        setMsg(
          `${char.name} is already in your Teapot. Do you want to change their Info?`
        );
      }
    }
  }, [msg, teapot, char, artsChest, wpsRack, curSt]);

  return (
    <Card className="p-4" bgColor="cardBg1">
      <div className="relative">
        <GeneralInfo />
        <MainSelect
          className="mt-2"
          padding="ms-pad"
          tab={tab}
          setTab={(e) => setTab(e.target.value)}
          options={[
            "Attributes",
            "Weapons",
            "Artifacts",
            "Constellation",
            "Talents"
          ]}
        />
        <LimitedBox className="mt-2 hide-sb" height="25.5rem">
          {contentAt[tab]}
        </LimitedBox>
        {tab === "Attributes" && (
          <Box
            position="absolute"
            bottom="-0.5rem"
            left={0}
            width="100%"
            className="flex justify-between align-end"
          >
            <div>
              <ColoredText variant="body2" color="lightGold">
                {msg}
              </ColoredText>
              <BtnOnCard className="my-2" onClick={tryAdd}>
                {pending ? "Change" : "Invite >>"}
              </BtnOnCard>
            </div>
            <Img
              className="pointer"
              width="5rem"
              src={wikia + "5/5a/Item_Serenitea_Pot.png"}
              alt=""
              onClick={() => setPoss("Teapot")}
            />
          </Box>
        )}
        {boardType && (
          <StatsDetails boardType={boardType} setBoardType={setBoardType} />
        )}
      </div>
    </Card>
  );
}
