import React, { useState, useContext, useEffect } from "react";
import { deepCopy, FixedContext, CharContext } from "../../App";
import { BtnOnCard } from "../../styledCpns/Inputs";
import { WeaponDetails } from "../Character/Weapons";
import { PieceDetails } from "../ArtsDetails/ArtPiece";
import { initWp } from "../../data/initConfig";
import { processNumInp } from "../../helpers";
import { setArt } from "../Modal/handlers";

export default function Forge(props) {
  const { forItems, isWp, info, index, type, setPosition } = props;
  const { setInv, setWpsRack, setArtsChest, setPoss, panel2 } = useContext(
    FixedContext
  );
  const [picked, setPicked] = useState(false);
  useEffect(() => {
    if (picked) setPicked(false);
  }, [info]);

  const { weapon } = useContext(CharContext).charData;
  return (
    <>
      <div className="embryo overflow-auto hide-sb">
        {isWp ? (
          <WeaponDetails
            wp={info}
            openModal={() => {}}
            upgrade={(e) =>
              setInv((prev) => {
                const inv = deepCopy(prev);
                inv.Weapons[index].level = e.target.value;
                return inv;
              })
            }
            refine={(e) =>
              setInv((prev) => {
                const inv = deepCopy(prev);
                inv.Weapons[index].refinement = Number(e.target.value);
                return inv;
              })
            }
            showMore={false}
          />
        ) : (
          <PieceDetails
            pieceInfo={info}
            artType={type}
            enhance={(e) =>
              setInv((prev) => {
                const inv = deepCopy(prev);
                const rcpt = forItems
                  ? inv.Artifacts[index]
                  : inv.Loadouts[index].arts[type];
                rcpt.level = Number(e.target.value.slice(1));
                return inv;
              })
            }
            change4={(direction) =>
              setInv((prev) => {
                const inv = deepCopy(prev);
                const rcpt = forItems
                  ? inv.Artifacts[index]
                  : inv.Loadouts[index].arts[type];
                rcpt.level =
                  direction === "up"
                    ? Math.min(20, rcpt.level + 4)
                    : Math.max(0, rcpt.level - 4);
                return inv;
              })
            }
            toMax={() =>
              setInv((prev) => {
                const inv = deepCopy(prev);
                const rcpt = forItems
                  ? inv.Artifacts[index]
                  : inv.Loadouts[index].arts[type];
                rcpt.level = 20;
                return inv;
              })
            }
            changeMSType={(e) =>
              setInv((prev) => {
                const inv = deepCopy(prev);
                const rcpt = forItems
                  ? inv.Artifacts[index]
                  : inv.Loadouts[index].arts[type];
                rcpt.mainSType = e.target.value;
                return inv;
              })
            }
            changeSSType={(e, i) =>
              setInv((prev) => {
                const inv = deepCopy(prev);
                const rcpt = forItems
                  ? inv.Artifacts[index]
                  : inv.Loadouts[index].arts[type];
                rcpt.subS[i].type = e.target.value;
                return inv;
              })
            }
            changeSSVal={(e, i, { val }) =>
              setInv((prev) => {
                const inv = deepCopy(prev);
                const rcpt = forItems
                  ? inv.Artifacts[index]
                  : inv.Loadouts[index].arts[type];
                rcpt.subS[i].val = processNumInp(e.target.value, val, 9999);
                return inv;
              })
            }
          />
        )}
      </div>
      <div className="mt-3 flex justify-center">
        <BtnOnCard
          color="white"
          bgColor="red"
          onClick={() => {
            setInv((prev) => {
              const inv = deepCopy(prev);
              if (forItems) {
                inv[isWp ? "Weapons" : "Artifacts"].splice(index, 1);
              } else {
                const { arts } = inv.Loadouts[index];
                arts[type].set = null;
                let hasNone = true;
                for (let type in arts) {
                  if (arts[type].set) {
                    hasNone = false;
                    break;
                  }
                }
                if (hasNone) inv.Loadouts.splice(index, 1);
              }
              return inv;
            });
            setPosition();
          }}
        >
          Remove
        </BtnOnCard>
        {(!isWp || weapon === type) && (
          <BtnOnCard
            className="ml-3"
            bgColor="lightGold"
            onClick={() => {
              if (isWp) {
                setWpsRack((prev) => {
                  const rack = deepCopy(prev);
                  rack[panel2] = initWp(info);
                  return rack;
                });
                setPoss(null);
              } else {
                setArt(panel2, type, setArtsChest, info);
                setPicked(true);
              }
            }}
          >
            {!isWp && picked ? "Picked" : "Pick"}
          </BtnOnCard>
        )}
      </div>
    </>
  );
}
