import React, { useContext, useState, useCallback } from "react";
import {
  UIContext,
  FixedContext,
  CharContext,
  deepCopy,
  aHasB
} from "../../App";
import { Box, Typography } from "@material-ui/core";
import { BtnOnCard, IconBtn } from "../../styledCpns/Inputs";
import { LimitedBox } from "../../styledCpns/Layout";
import { ColoredText } from "../../styledCpns/DataDisplay";
import { InputText, CheckBox, AddBtn } from "./styles";
import { applySettings } from "./handlers";

export default function Settings({ setSettingsOn }) {
  const {
    setTempSts,
    setCharCs,
    setWpsRack,
    setSubWpBCs,
    setArtsChest,
    setParties,
    setInfusions,
    setElmtCs,
    setUI
  } = useContext(FixedContext);
  const { char } = useContext(CharContext);
  const { ui, tempSts } = useContext(UIContext);
  const { curSt, shownSt } = ui;
  const [err, setErr] = useState(null);

  const tryApply = useCallback(() => {
    let ok = true;
    const nameCount = [];
    for (let st of tempSts) {
      if (!st.name.length || st.name.length > 14 || aHasB(nameCount, st.name)) {
        ok = false;
        break;
      } else {
        nameCount.push(st.name);
      }
    }
    if (ok) {
      applySettings(char.name, tempSts, curSt, shownSt, {
        setCharCs,
        setWpsRack,
        setSubWpBCs,
        setArtsChest,
        setParties,
        setInfusions,
        setElmtCs,
        setUI
      });
      setErr(null);
      setSettingsOn(false);
    } else {
      setErr("name");
    }
  }, [char.name, curSt, tempSts, shownSt]);

  const remove = (i) => {
    if (i !== curSt) {
      setTempSts((prev) => {
        const sts = deepCopy(prev);
        sts.splice(i, 1);
        return sts;
      });
    }
  };

  return (
    <Box
      className="p-3 flex-col relative"
      borderRadius="0.5rem"
      bgcolor="background.default"
    >
      <Typography variant="h3" color="textSecondary" align="center">
        SETTINGS
      </Typography>
      <IconBtn
        icon="fas fa-times"
        position="absolute"
        top="0.25rem"
        right="0.25rem"
        color="white"
        bgColor="red"
        size="1.75rem"
        fontSize="1.125rem"
        className="opacity-9"
        onClick={() => {
          setTempSts(
            ui.stNames.map((name, i) => ({
              name,
              isOld: true,
              index: i,
              checked: aHasB(ui.cmpSts, i)
            }))
          );
          setSettingsOn(false);
        }}
      />
      <LimitedBox height="30.9rem" className="flex-col hide-sb">
        {tempSts.map((st, i) => (
          <Setup key={i} st={st} i={i} remove={remove} />
        ))}
        <AddBtn
          className="glow-on-hover"
          onClick={() => {
            tempSts.length < 4
              ? setUI((prev) => ({
                  ...prev,
                  modalName: "Weapons",
                  modalType: "add"
                }))
              : setErr("amount");
          }}
        />
        {err && (
          <ColoredText
            className="mt-3"
            variant="body1"
            color="red"
            align="center"
          >
            <i>
              {err === "name"
                ? "Please choose unique names containing from 1 to 14 characters for Set-ups."
                : "For better performance, the number of Set-ups is limited to 4."}
            </i>
          </ColoredText>
        )}
      </LimitedBox>
      <BtnOnCard
        className="mt-2 mx-auto"
        bgColor="lightGold"
        onClick={tryApply}
      >
        Apply
      </BtnOnCard>
    </Box>
  );
}

function Setup({ st, i, remove }) {
  const { index } = st;
  const { setTempSts } = useContext(FixedContext);
  const { curSt } = useContext(UIContext).ui;
  return (
    <div className="mt-4 pr-2 flex align-center">
      <IconBtn
        icon="fas fa-trash-alt"
        className={
          "p-2 mr-2 " + (index === curSt ? "unavailable" : "glow-on-hover")
        }
        color="white"
        bgColor="red"
        size="2rem"
        fontSize="1rem"
        onClick={() => remove(i)}
      />
      <InputText
        type="text"
        className="grow-1"
        value={st.name}
        onChange={(e) =>
          setTempSts((prev) => {
            const sts = deepCopy(prev);
            sts[i].name = e.target.value;
            return sts;
          })
        }
      />
      <CheckBox
        type="checkbox"
        className={index === curSt ? "unavailable" : ""}
        checked={st.checked}
        onChange={() => {
          if (index !== curSt) {
            setTempSts((prev) => {
              const sts = deepCopy(prev);
              sts[i].checked = !prev[i].checked;
              return sts;
            });
          }
        }}
      />
    </div>
  );
}
