import characters from "./characters";
import { elmts } from "./elements";
import {
  getSelfBC,
  getCharDC,
  getMainWpBC,
  getArtSets,
  getSubArtsBC,
  getSubArtsDC,
  getMainArtsBC
} from "../helpers";

export const getDfltWp = (name) => {
  return {
    sword: "Skyward Blade",
    claymore: "Skyward Pride",
    polearm: "Skyward Spine",
    catalyst: "Skyward Atlas",
    bow: "Skyward Harp"
  }[characters[name].weapon];
};

export const initChar = (name) => ({
  name,
  level: "1/20",
  ascension: 0,
  "Normal Attack": 1,
  "Elemental Skill": 1,
  "Elemental Burst": 1,
  constellation: 0
});

export const initCharCs = (name) => ({
  BC: getSelfBC(name),
  DC: getCharDC(name, true),
  infs: []
});

export const initWp = ({ name, level = "1/20", refinement = 1 }) => ({
  name,
  level,
  refinement,
  BC: getMainWpBC(name)
});

export const initArtPiece = ({
  set,
  level = 0,
  mainSType,
  subS = [
    {
      type: "DEF",
      val: 0
    },
    {
      type: "DEF%",
      val: 0
    },
    {
      type: "CRIT Rate",
      val: 0
    },
    {
      type: "CRIT DMG",
      val: 0
    }
  ]
}) => ({
  set,
  level,
  mainSType,
  subS
});

export const initArts = (arts) => {
  const sets = getArtSets(arts);
  return {
    sets,
    pieces: arts || {
      flower: initArtPiece({ mainSType: "HP" }),
      plume: initArtPiece({ mainSType: "ATK" }),
      sands: initArtPiece({ mainSType: "ATK%" }),
      goblet: initArtPiece({ mainSType: "ATK%" }),
      circlet: initArtPiece({ mainSType: "ATK%" })
    },
    BC: sets[0]?.bonusLv ? getMainArtsBC(sets[0].name) : [],
    subBC: getSubArtsBC(sets[0]?.name),
    subDC: getSubArtsDC()
  };
};

export const initParty = () => [
  { name: null, BC: [], DC: [], inf: null },
  { name: null, BC: [], DC: [], inf: null },
  { name: null, BC: [], DC: [], inf: null }
];

export const initElmtC = () => ({
  ampRxn: null,
  superconduct: false,
  rsns: []
});

export const initTarget = () => {
  const result = { Level: 1, "Physical RES": 10 };
  for (let elmt of elmts) {
    result[elmt + " RES"] = 10;
  }
  return result;
};
