import React, { useContext, useState, useRef, useEffect, useMemo } from "react";
import { deepCopy, FixedContext } from "../../App";
import { Box, Collapse } from "@material-ui/core";
import { MainSelect, CloseBtn, IconBtn } from "../../styledCpns/Inputs";
import { StyledMenu } from "./styles";
import Items from "./Items";
import Loadouts from "./Loadouts";
import weapons from "../../data/weapons";
import { wpIcons, artsIcons } from "../../data/misc";

export function cmpLv(a, b) {
  const [fA, sA] = a.level.split("/").map((lv) => Number(lv));
  const [fB, sB] = b.level.split("/").map((lv) => Number(lv));
  if (fA !== fB) {
    return fB - fA;
  }
  return sB - sA;
}

function sortWps(setInv) {
  setInv((prev) => {
    const inv = deepCopy(prev);
    inv.Weapons.sort((a, b) => {
      const rA = weapons[a.type][a.name].rarity;
      const rB = weapons[b.type][b.name].rarity;
      const rank = { gold: 3, purple: 2, blue: 1 };
      if (rA !== rB) return rank[rB] - rank[rA];
      return cmpLv(a, b);
    });
    return inv;
  });
}

function sortArts(setInv) {
  setInv((prev) => {
    const inv = deepCopy(prev);
    inv.Artifacts.sort((a, b) => b.level - a.level);
    inv.Artifacts.sort((a, b) => {
      if (b.level === a.level) {
        if (b.set > a.set) {
          return 1;
        } else if (b.set < a.set) {
          return -1;
        }
        const rank = { flower: 5, plume: 4, sands: 3, goblet: 2, circlet: 1 };
        return rank[b.type] - rank[a.type];
      }
      return 0;
    });
    return inv;
  });
}

function getItems(inv, tab, filter) {
  if (tab === "Loadouts") {
    return;
  } else {
    let result = inv[tab].map((item, i) => {
      item.index = i;
      return item;
    });
    if (filter) {
      result = result.filter((item) => item.type === filter);
    }
    return result;
  }
}

export default function Inventory({ inv, teapot, setLoadCtrl }) {
  const { setPoss, setInv } = useContext(FixedContext);
  const [tab, setTab] = useState("Weapons");
  const [dropped, setDropped] = useState(false);
  const [filter, setFilter] = useState(null);
  const ddCpns = [
    { desc: "upload", handler: () => setLoadCtrl("up") },
    { desc: "download", handler: () => setLoadCtrl("down") }
  ];
  const isWp = tab === "Weapons";
  if (tab !== "Loadouts") {
    ddCpns.unshift(
      {
        desc: "sort",
        handler: () => (isWp ? sortWps(setInv) : sortArts(setInv))
      },
      ...Object.keys(isWp ? wpIcons : artsIcons).map((desc) => ({
        desc,
        handler: () => setFilter(desc === filter ? null : desc)
      }))
    );
  }
  const items = useMemo(() => getItems(inv, tab, filter), [inv, tab, filter]);
  useEffect(() => {
    setFilter(null);
  }, [tab]);

  const ddRef = useRef();
  useEffect(() => {
    const closeDD = (e) => {
      if (ddRef.current && !ddRef.current.contains(e.target) && dropped) {
        setDropped(false);
      }
    };
    document.addEventListener("click", closeDD);
    return () => {
      document.removeEventListener("click", closeDD);
    };
  }, [dropped]);

  return (
    <div className="full-h">
      <div className="heading p-2 flex align-center">
        <div className="relative ml-1 mr-3" ref={ddRef}>
          <IconBtn
            className="cmn-shadow"
            icon="fas fa-bars"
            size="2.125rem"
            fontSize="1.125rem"
            bgColor="lightGold"
            onClick={() => setDropped(!dropped)}
          />
          <StyledMenu>
            <Collapse in={dropped}>
              <div className="dropdown cmn-shadow">
                {ddCpns.map((item, i) => (
                  <p
                    key={i}
                    className={"desc" + (item.desc === filter ? " active" : "")}
                    onClick={() => {
                      item.handler();
                      setDropped(false);
                    }}
                  >
                    <span className="capitalize">{item.desc}</span>
                  </p>
                ))}
              </div>
            </Collapse>
          </StyledMenu>
        </div>
        <Box width="9.5rem">
          <MainSelect
            className="cmn-shadow"
            padding="poss-pad"
            tab={tab}
            setTab={(e) => setTab(e.target.value)}
            options={["Weapons", "Artifacts", "Loadouts"]}
          />
        </Box>
        <CloseBtn color="white" bgColor="red" onClick={() => setPoss(null)} />
      </div>
      <div className="body px-3">
        <div className="full-h overflow-auto">
          <div className="pb-3 full-h flex align-start">
            {tab === "Loadouts" ? (
              <Loadouts data={inv.Loadouts} teapot={teapot} />
            ) : (
              <Items data={items} isWp={isWp} />
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
