import styled from "styled-components";
import React, { useContext } from "react";
import { ResultContext, aHasB } from "../../App";
import { Box, Typography } from "@material-ui/core";
import { LimitedBox } from "../../styledCpns/Layout";
import { IconBtn } from "../../styledCpns/Inputs";
import { Table, ColoredText } from "../../styledCpns/DataDisplay";
import { colorCodes, pctOf, statsTypes } from "../../data/misc";
import { getRxnBnFrEM } from "../../calculators/stats";

const Row = styled.div`
  margin-right: ${(props) => props.marginRight};
`;

const XtraCol = styled.p`
  position: absolute;
  left: 100%;
  padding-left: 0.375rem;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1.5;
  color: ${colorCodes.green};
`;

export default function StatsDetails(props) {
  const { boardType } = props;
  const boardContent = useContext(ResultContext)[boardType];
  const detailsBoard = [];

  for (let type of statsTypes) {
    const content =
      boardType === "ATRs" && aHasB(["HP", "ATK", "DEF"], type)
        ? boardContent["Base " + type]
        : Math.round((boardContent?.[type] || 0) * 10) / 10;
    let xtra = boardContent[type] - boardContent["Base " + type];
    if (boardType !== "ATRs" || isNaN(xtra) || !xtra) {
      xtra = null;
    } else {
      xtra = "+" + xtra;
    }
    detailsBoard.push(
      <div key={type} className="pt-1 px-2">
        <Row
          className="relative flex justify-between"
          marginRight={boardType === "artSBnes" ? "0.5rem" : "4rem"}
        >
          <Typography component="p" variant="subtitle1">
            {type}
          </Typography>
          <Typography component="p" variant="subtitle1" align="right">
            {content + pctOf(type)}
          </Typography>
          <XtraCol>{xtra}</XtraCol>
        </Row>
      </div>
    );
  }
  const rxnBnFrEM = getRxnBnFrEM(boardContent?.["Elemental Mastery"] || 0);
  detailsBoard.splice(
    4,
    0,
    <div key="none"></div>,
    <div key="xtra" className="pb-1 px-2">
      <Typography variant="body2" className="opacity-9">
        Increases damage caused by Vaporize and Melt by{" "}
        <ColoredText component="span" variant="body2" color="green">
          {rxnBnFrEM.amplifying}%
        </ColoredText>
        .
      </Typography>
      <Typography variant="body2" className="opacity-9">
        Increases damage caused by Overloaded, Superconduct, Electro-Charged,
        Shattered, and Swirl by{" "}
        <ColoredText component="span" variant="body2" color="green">
          {rxnBnFrEM.transformative}%
        </ColoredText>
        .
      </Typography>
      <Typography variant="body2" className="opacity-9">
        Increases damage absorption power of shields created through Crystallize
        by{" "}
        <ColoredText component="span" variant="body2" color="green">
          {rxnBnFrEM.shield}%
        </ColoredText>
        .
      </Typography>
    </div>
  );

  return (
    <Box position="absolute" top={0} width="100%" bgcolor={colorCodes.cardBg1}>
      <LimitedBox className="mb-1 custom-sb" height="35rem">
        <Table>{detailsBoard}</Table>
      </LimitedBox>
      <IconBtn
        icon="fas fa-times"
        size="1.625rem"
        color="white"
        bgColor="red"
        className="mt-3 mx-auto"
        onClick={() => props.setBoardType(null)}
      />
    </Box>
  );
}
