import { Green, LGreen } from "../../../styledCpns/DataDisplay";
import { mapToLevels } from "../../misc";

export const RazorBuffs = [
  {
    src: "Elemental Burst",
    desc: (_, char) => (
      <>
        Raises Razor's <LGreen>ATK SPD</LGreen> by{" "}
        <Green>{RazorBuffs[0].bnValue(char)}%</Green>.
      </>
    ),
    isGranted: () => true,
    affect: "self",
    addBnes: (ATRs, _, __, ___, ____, char) => {
      ATRs["Normal ATK SPD"] += RazorBuffs[0].bnValue(char);
    },
    bnValue: (char) => {
      const level = char["Elemental Burst"] + (char.constellation >= 3 ? 3 : 0);
      return Math.min(24 + level * 2 - Math.max(level - 6, 0), 40);
    }
  },
  {
    src: "Constellation 1",
    desc: () => (
      <>
        Picking up an Elemental Orb or Particle increases Razor's{" "}
        <LGreen>DMG</LGreen> by <Green>10%</Green> for 8s.
      </>
    ),
    isGranted: (char) => char.constellation >= 1,
    affect: "self",
    addBnes: (_, bonuses) => {
      bonuses.AllDmg += 10;
    }
  },
  {
    src: "Constellation 2",
    desc: () => (
      <>
        Increases <LGreen>CRIT Rate</LGreen> against opponents with less than
        30% HP by <Green>10%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addBnes: (ATRs) => {
      ATRs["CRIT Rate"] += 10;
    }
  }
];

