export const AyakaBuffs = [
  {
    src: "Alternate Sprint",
    desc: () => (
      <>
        When Ayaka reappears from Kamisato Art: Senho, her attacks are{" "}
        <LGreen>infused with Cryo</LGreen> for a brief period.
      </>
    ),
    isGranted: () => true,
    affect: "self",
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: true
  },
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        After using Kamisato Art: Hyouka, Ayaka's{" "}
        <LGreen>Normal and Charged attacks</LGreen> deal <Green>30%</Green>{" "}
        increased <LGreen>DMG</LGreen> for 6s.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "self",
    addBnes: (_, bonuses) => {
      bonuses.NA.DMG += 30;
      bonuses.CA.DMG += 30;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        When the Cryo application at the end of Kamisato Art: Senho hits an
        opponent, Ayaka gains <Green>18%</Green> <LGreen>Cryo DMG Bonus</LGreen>{" "}
        for 10s.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addBnes: (ATRs) => {
      ATRs["Cryo DMG Bonus"] += 18;
    }
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        Ayaka gains Usurahi Butou every 10s, increasing her{" "}
        <LGreen>Charged Attack DMG</LGreen> by <Green>298%</Green>. This buff
        will be cleared 0.5s after Ayaka's Charged ATK hits an opponent.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    addBnes: (_, bonuses) => {
      bonuses.CA.DMG += 298;
    }
  }
];

