import React, { useContext } from "react";
import { UIContext } from "../../App";
import { Typography } from "@material-ui/core";
import { ColoredText } from "../../styledCpns/DataDisplay";

export default function CmpTable(props) {
  const { curAtkVals, stsAtkDmg, focus } = props;
  const { atkName, fieldNames } = props.name;
  const { cmpSts, curSt, stNames } = useContext(UIContext).ui;
  const otherSts = [...cmpSts];
  otherSts.splice(cmpSts.indexOf(curSt), 1);
  return (
    <tbody>
      <tr>
        <Typography component="th" variant="subtitle2" className="sticky-col" />
        <ColoredText component="th" variant="subtitle2" color="lightGold">
          {stNames[curSt]}
        </ColoredText>
        {otherSts.map((st, i) => (
          <Typography key={i} component="th" variant="subtitle2">
            {stNames[st]}
          </Typography>
        ))}
      </tr>
      {fieldNames.map((name, i) => {
        const curVal = curAtkVals[name][focus];
        const isArr = Array.isArray(curVal);
        return (
          <tr key={i}>
            <Typography
              className="sticky-col"
              component="td"
              variant="subtitle2"
            >
              {name}
            </Typography>
            <ColoredText component="td" variant="subtitle2" color="lightGold">
              {isArr
                ? curVal.map((cV) => Math.round(cV)).join(" + ")
                : Math.round(curVal)}
            </ColoredText>
            {otherSts.map((st, j) => {
              const value = stsAtkDmg[st][atkName][name][focus];
              const diff = isArr ? value[0] - curVal[0] : value - curVal;
              const pctDiff =
                Math.round(
                  (Math.abs(diff) * 1000) / (isArr ? curVal[0] : curVal)
                ) / 10;
              return (
                <OtherVals
                  key={j}
                  value={
                    isArr
                      ? value.map((v) => Math.round(v)).join(" + ")
                      : Math.round(value)
                  }
                  diff={diff}
                  pctDiff={pctDiff}
                />
              );
            })}
          </tr>
        );
      })}
    </tbody>
  );
}

function OtherVals({ value, diff, pctDiff }) {
  return (
    <Typography
      component="td"
      variant="subtitle2"
      className={"relative" + (pctDiff ? " tooltip_wrapper" : "")}
    >
      {value}
      {pctDiff !== 0 && (
        <>
          <Typography component="span" variant="body2">
            <i
              className={"fas fa-long-arrow-alt-" + (diff > 0 ? "up" : "down")}
            />
          </Typography>
          <Typography
            component="span"
            variant="body2"
            className={"tooltip " + (diff > 0 ? "higher" : "lower")}
          >
            {(diff > 0 ? "+" : "-") + pctDiff + "%"}
          </Typography>
        </>
      )}
    </Typography>
  );
}
