import { Green, LGreen, Red } from "../../../styledCpns/DataDisplay";
import { incAllNAs } from "../../../helpers";
import { mapToLevels } from "../../misc";

export const KazuhaBuffs = [
  {
    src: "Elemental Skill",
    desc: () => (
      <>
        Within 10s of remaining airborne after casting Chihayaburu, Kazuha's
        next <LGreen>Plunging Attack DMG</LGreen> is converted to{" "}
        <LGreen>Anemo DMG</LGreen>.
      </>
    ),
    isGranted: () => true,
    affect: "self",
    canInfuse: () => true,
    infuseRange: ["PA"],
    canBeOverrided: false
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: (_, __, { inputs: [elmt, EM] }) => (
      <>
        Upon triggering a Swirl, Kazuha will grant all party members a{" "}
        <Green>0.04%</Green> <LGreen>Elemental DMG Bonus</LGreen> to the element
        absorbed by Swirl for every point of <LGreen>Elemental Mastery</LGreen>{" "}
        he has for 8s.{" "}
        <Red>
          {elmt} DMG Bonus: {KazuhaBuffs[1].bnValue(EM)}%.
        </Red>
      </>
    ),
    affect: "teammates",
    labels: ["Element Absorbed", "Elemental Mastery"],
    inputs: ["Pyro", 0],
    inputTypes: ["swirl", "text"],
    maxs: [null, 9999],
    addBnes: (ATRs, _, __, [elmt, EM]) => {
      ATRs[elmt + " DMG Bonus"] += KazuhaBuffs[1].bnValue(EM);
    },
    bnValue: (EM) => Math.round(EM * 0.4) / 10
  },
  {
    src: "Constellation 2",
    desc: () => (
      <>
        The Autumn Whirlwind field created by Kazuha Slash increases the{" "}
        <LGreen>Elemental Mastery</LGreen> of him and characters within the
        field by <Green>200</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "party",
    addBnes: (ATRs) => {
      ATRs["Elemental Mastery"] += 200;
    }
  },
  {
    src: "Constellation 6",
    desc: (_, __, { ATRs }) => (
      <>
        After using Chihayaburu or Kazuha Slash, Kazuha gains an{" "}
        <LGreen>Anemo Infusion</LGreen> for 5s, and each point of{" "}
        <LGreen>Elemental Mastery</LGreen> will increase the DMG dealt by
        Kazuha's <LGreen>Normal, Charged, and Plunging Attack</LGreen> by{" "}
        <Green>0.2%</Green>.{" "}
        <Red>DMG Bonus: {KazuhaBuffs[3].bnValue(ATRs)}%.</Red>
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    final: true,
    addBnes: (ATRs, bonuses) => {
      incAllNAs(bonuses, KazuhaBuffs[3].bnValue(ATRs));
    },
    bnValue: (ATRs) => Math.round(ATRs["Elemental Mastery"] * 2) / 10,
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: true
  }
];

