import styled from "styled-components";
import { Box } from "@material-ui/core";
import { colorCodes } from "../data/misc";

export const Select = styled(
  ({ borderRadius, bgColor, color, fontSize, fontWeight, align, ...other }) => (
    <div>
      <select {...other} />
    </div>
  )
)`
  height: 100%;
  width: 100%;
  display: block;
  border: none;
  border-radius: ${(props) => props.borderRadius};
  background-color: ${(props) => props.bgColor || "transparent"};
  color: ${(props) => colorCodes[props.color]};
  font-family: "Nunito", "Helvetica", "Roboto", "Arial", sans-serif;
  font-size: ${(props) => props.fontSize || "1rem"};
  font-weight: ${(props) => props.fontWeight};
  outline: none;
  text-align-last: ${(props) => props.align};
`;

export const Option = styled.option`
  color: black;
`;

export function MainSelect({ tab, setTab, options, padding, ...others }) {
  return (
    <Box borderRadius="1.25rem" bgcolor={colorCodes.orange} {...others}>
      <Select
        className={"no-apr bg-icon arrow-black main-select " + padding || ""}
        fontSize="1.25rem"
        fontWeight={700}
        align={padding === "ms-pad" ? "center" : "left"}
        value={tab}
        onChange={setTab}
      >
        {options.map((opt) => (
          <Option key={opt}>{opt}</Option>
        ))}
      </Select>
    </Box>
  );
}

export const BtnOnCard = styled.button`
  margin: ${(props) => props.margin};
  padding: ${(props) => props.padding || "0.3rem 1.2rem"};
  border: none;
  border-radius: 1rem;
  font-size: ${(props) => props.fontSize || "1rem"};
  font-weight: 700;
  background-color: ${(props) => colorCodes[props.bgColor]};
  color: ${(props) => colorCodes[props.color]};
`;

export const IconBtn = styled(
  ({
    position,
    top,
    bottom,
    left,
    right,
    centerX,
    centerY,
    bgColor,
    color,
    icon,
    size,
    fontSize,
    ...other
  }) => (
    <button {...other}>
      <i className={icon}></i>
    </button>
  )
)`
  position: ${(props) => props.position};
  top: ${(props) => props.top};
  bottom: ${(props) => props.bottom};
  left: ${(props) => props.left};
  right: ${(props) => props.right};
  width: ${(props) => props.size};
  height: ${(props) => props.size};
  font-size: ${(props) => props.fontSize};
  border: none;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: ${(props) =>
    colorCodes[props.bgColor] || props.bgColor || "rgba(0, 0, 0, 0.5)"};
  color: ${(props) => colorCodes[props.color] || props.color};
`;

export const CloseBtn = (props) => (
  <IconBtn
    icon="fas fa-times"
    position="absolute"
    top="0.5rem"
    right="0.5rem"
    size="1.75rem"
    fontSize="1rem"
    {...props}
  />
);
