import { Green, LGreen, Red } from "../../../styledCpns/DataDisplay";
import { mapToLevels } from "../../misc";
import { mediumPAs } from "../config";

export const RosariaBuffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        When Rosaria strikes an opponent from behind using Ravaging Confession,
        her <LGreen>CRIT Rate</LGreen> increased by <Green>12%</Green> for 5s.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "self",
    addBnes: (ATRs) => {
      ATRs["CRIT Rate"] += 12;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: (_, __, { inputs }) => (
      <>
        Casting Rites of Termination increases <LGreen>CRIT Rate</LGreen> of all
        nearby party members by <Green>15%</Green> of Rosaria's{" "}
        <LGreen>CRIT Rate</LGreen> for 10s.{" "}
        <Red>CRIT Rate Bonus: {RosariaBuffs[1].bnValue(inputs)}%.</Red>
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "teammates",
    labels: ["CRIT Rate"],
    inputs: [5],
    inputTypes: ["text"],
    maxs: [100],
    addBnes: (ATRs, _, __, inputs) => {
      ATRs["CRIT Rate"] += RosariaBuffs[1].bnValue(inputs);
    },
    bnValue: ([CR]) => Math.round(CR * 1.5) / 10
  },
  {
    src: "Constellation 1",
    desc: () => (
      <>
        When Rosaria deals a CRIT Hit, her <LGreen>ATK SPD</LGreen> and{" "}
        <LGreen>Normal Attack DMG</LGreen> increases by <Green>10%</Green> for
        4s.
      </>
    ),
    isGranted: (char) => char.constellation >= 1,
    affect: "self",
    addBnes: (ATRs, bonuses) => {
      bonuses.NA.DMG += 10;
      ATRs["Normal ATK SPD"] += 10;
      ATRs["Charged ATK SPD"] += 10;
    }
  }
];

