import styled from "styled-components";
import React, { useContext } from "react";
import {
  deepCopy,
  FixedContext,
  TargetContext,
  CharContext,
  CharCsContext,
  PartyContext
} from "../App";
import { Typography } from "@material-ui/core";
import { ColoredText, Link } from "../styledCpns/DataDisplay";
import { colorCodes } from "../data/misc";
import { getFinalInfs } from "../helpers";

const Line = styled.div`
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const Label = ({ E, ...others }) => (
  <ColoredText
    variant="h6"
    color={E === "Physical" ? "white" : colorCodes[E] || "lightGold"}
    {...others}
  />
);

const Input = styled.input`
  padding: 0.5rem 0.5rem 0.25rem;
  width: 5rem;
  border: none;
  border-radius: 0.25rem;
  text-align: right;
  font-size: 1.125rem;
  outline: none;
  &:focus {
    box-shadow: 0 0 0 3px ${colorCodes.red};
  }
`;

const destructedTypes = {
  NA: "Normal Attacks",
  CA: "Charged Attacks",
  AS: "Aimed Shot",
  CAS: "Fully-charged Aimed Shot",
  PA: "Plunging Attacks"
};

export default function Target(props) {
  const { setTarget, panel2 } = useContext(FixedContext);
  const { target } = useContext(TargetContext);
  const { charCs } = useContext(CharCsContext);
  const { charData } = useContext(CharContext);
  const { parties } = useContext(PartyContext);
  const infs = getFinalInfs(charCs[panel2], charData, parties[panel2]);
  const { vision, weapon } = charData;
  const notes =
    weapon === "catalyst"
      ? [
          ["NA", vision],
          ["CA", vision],
          ["PA", vision]
        ]
      : Object.entries(infs);
  if (weapon === "bow") {
    notes[1][0] = "AS";
    notes.splice(2, 0, ["CAS", vision]);
  }
  return (
    <div className="pl-2 pr-4">
      {Object.entries(target).map((pair, i) => (
        <Line key={i}>
          <Label E={pair[0].split(" ")[0]}>{pair[0]}</Label>
          <Input
            type="text"
            value={pair[1]}
            onChange={(e) => {
              if (!isNaN(e.target.value)) {
                setTarget((prev) => {
                  const tg = deepCopy(prev);
                  tg[pair[0]] = Math.round(e.target.value);
                  return tg;
                });
              }
            }}
          />
        </Line>
      ))}
      <Typography>
        You can search for your target's Resistances on{" "}
        <Link
          href="https://genshin-impact.fandom.com/wiki/Resistance#Base_Enemy_Resistances"
          rel="noreferrer"
          target="_blank"
        >
          this page of the Genshin Impact Wiki
        </Link>
        .
      </Typography>
      <div className="mt-2">
        <ColoredText variant="h6" color="lightGold">
          Notes:
        </ColoredText>
        {notes.map((pair, i) => (
          <Typography key={i} className="mt-1">
            <b>{destructedTypes[pair[0]]}</b> deal{" "}
            <ColoredText component="span" color={pair[1]}>
              {pair[1]} DMG
            </ColoredText>
          </Typography>
        ))}
      </div>
    </div>
  );
}
