export const FischlBuffs = [
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        If your active character triggers an Electro-related Elemental Reaction
        when Oz is on the field, the opponent shall be dealt{" "}
        <LGreen>Electro DMG</LGreen> equal to <Green>80%</Green> of Fischl's{" "}
        <LGreen>ATK</LGreen>.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self"
  },
  {
    src: "Constellation 1",
    desc: () => (
      <>
        When Fischl performs a Normal Attack against an opponent, Oz fires a
        joint attack, dealing <LGreen>Physical DMG</LGreen> equal to{" "}
        <Green>22%</Green> of Fischl's <LGreen>ATK</LGreen>.
      </>
    ),
    isGranted: (char) => char.constellation >= 1,
    affect: "self"
  },
  {
    src: "Constellation 2",
    desc: () => (
      <>
        When <LGreen>Nightrider</LGreen> is used, it deals an{" "}
        <LGreen>additional</LGreen> <Green>200%</Green> <LGreen>ATK</LGreen> as
        DMG.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self"
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        Oz performs joint attacks with your active character when present,
        dealing <Green>30%</Green> of Fischl's <LGreen>ATK</LGreen> as{" "}
        <LGreen>Electro DMG</LGreen>.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "self"
  }
];

