import styled from "styled-components";
import { colorCodes } from "../data/misc";

export const Card = styled.div`
  margin: 0.25rem 0;
  width: 100%;
  border-radius: 0.625rem;
  background-color: ${(props) => colorCodes[props.bgColor]};
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.2), 0 3px 10px rgba(0, 0, 0, 0.2);
  @media (min-width: 372px) {
    width: 21.75rem;
  }
  @media (min-width: 704px) {
    &:first-child {
      margin-right: 0.5rem;
    }
  }
  @media (min-width: 1456px) {
    margin: 0;
  }
`;

export const LimitedBox = styled.div`
  height: ${(props) => props.height || "auto"};
  width: ${(props) => props.width || "auto"};
  overflow: auto;
`;

export const StyledApp = styled.div`
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  @media (min-width: 728px) {
    width: 704px;
  }
  @media (min-width: 1456px) {
    width: 1416px;
    justify-content: space-between;
  }
`;

export const StyledCol = styled.div`
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  @media (min-width: 1456px) {
    width: 704px;
  }
`;

export const StyledMobileUI = styled.div`
  width: 100vw;
  height: 100vh;
  padding-top: 3.75rem;
  display: flex;
  overflow-x: hidden;
  scroll-behavior: smooth;
  #nav_bar {
    display: flex;
    position: fixed;
    z-index: 3;
    top: 0;
    left: 0;
    width: 100%;
  }
  .tab {
    flex-grow: 1;
    padding: 0.75rem 0 0.5rem;
    border-bottom: 2px solid black;
    background: ${colorCodes.lightRed};
    color: black;
    font-size: 1rem;
    font-weight: 700;
    text-align: center;
  }
  .tab:not(:last-child) {
    border-right: 2px solid black;
  }
  .tab.active {
    background: ${colorCodes.red};
    color: white;
  }
  .slides {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    min-width: 100vw;
    height: calc(100vh - 3.5rem);
  }
  @media (min-width: 312px) {
    .tab {
      font-size: 1.125rem;
    }
  }
`;
