import React, { useContext } from "react";
import {
  FixedContext,
  UIContext,
  CharContext,
  WpContext,
  deepCopy
} from "../../App";
import { Typography } from "@material-ui/core";
import { Select, Option } from "../../styledCpns/Inputs";
import { ColoredText, Img } from "../../styledCpns/DataDisplay";
import { Pedestal } from "./styles";
import { levels, wikia } from "../../data/misc";
import weapons from "../../data/weapons";

export default function WeaponCard(props) {
  const { setWpsRack, setUI } = useContext(FixedContext);
  const { charData } = useContext(CharContext);
  const { wpsRack } = useContext(WpContext);
  const { shownSt } = useContext(UIContext).ui;
  const wps = wpsRack[shownSt];
  const wpData = weapons[charData.weapon][wps.name];

  return (
    <Pedestal>
      {window.innerWidth < 312 && (
        <ColoredText className="px-3" variant="h4" color={wpData.rarity}>
          <strong>{wps.name}</strong>
        </ColoredText>
      )}
      <div className="px-2 flex justify-center align-center">
        <Img
          src={wikia + wpData.icon + ".png"}
          alt=""
          width="calc(80/264*100%)"
          className="pointer"
          onClick={() =>
            setUI((prev) => ({
              ...prev,
              modalName: "Weapons",
              modalType: "change"
            }))
          }
        />
        <div className="ml-2">
          {window.innerWidth >= 312 && (
            <ColoredText variant="h4" color={wpData.rarity}>
              <strong>{wps.name}</strong>
            </ColoredText>
          )}
          <div className="mt-1 flex-wrap">
            <Typography variant="body1" className="mr-1">
              Level
            </Typography>
            <Select
              color={wpData.rarity}
              fontSize="1rem"
              align="right"
              value={wps.level}
              onChange={(e) =>
                setWpsRack((prev) => {
                  const rack = deepCopy(prev);
                  rack[shownSt].level = e.target.value;
                  return rack;
                })
              }
            >
              {levels.map((level) => (
                <Option key={level}>{level}</Option>
              ))}
            </Select>
          </div>
          <div className="mt-1 flex-wrap">
            <Typography variant="body1" className="mr-2">
              Refinement Rank
            </Typography>
            <Select
              color={wpData.rarity}
              fontSize="1rem"
              value={wps.refinement}
              onChange={(e) =>
                setWpsRack((prev) => {
                  const rack = deepCopy(prev);
                  rack[shownSt].refinement = Number(e.target.value);
                  return rack;
                })
              }
            >
              {[1, 2, 3, 4, 5].map((level) => (
                <Option key={level}>{level}</Option>
              ))}
            </Select>
          </div>
        </div>
      </div>
    </Pedestal>
  );
}
