import React, { useContext } from "react";
import { FixedContext, deepCopy } from "../../App";
import { Typography } from "@material-ui/core";
import { Select, Option, BtnOnCard } from "../../styledCpns/Inputs";
import { ColoredText, ColoredBox } from "../../styledCpns/DataDisplay";
import { ArrowBtn, MaxBtn } from "./styles";
import SSLine from "./SSLine";
import { pctOf } from "../../data/misc";
import { artLvs, artMS } from "../../data/artifacts/config";
import artifacts from "../../data/artifacts";
import { setArt } from "../Modal/handlers";
import { processNumInp } from "../../helpers";

export default function ArtPiece(props) {
  const { index, artType, modalType } = props;
  const { setArtsChest, setUI } = useContext(FixedContext);
  return (
    <div>
      <PieceDetails
        pieceInfo={props.pieceInfo}
        artType={artType}
        enhance={(e) =>
          setArtsChest((prev) => {
            const chest = deepCopy(prev);
            const piece = chest[index].pieces[artType];
            piece.level = Number(e.target.value.slice(1));
            return chest;
          })
        }
        change4={(direction) => {
          setArtsChest((prev) => {
            const chest = deepCopy(prev);
            const piece = chest[index].pieces[artType];
            piece.level =
              direction === "up"
                ? Math.min(20, piece.level + 4)
                : Math.max(0, piece.level - 4);
            return chest;
          });
        }}
        toMax={() => {
          setArtsChest((prev) => {
            const chest = deepCopy(prev);
            chest[index].pieces[artType].level = 20;
            return chest;
          });
        }}
        changeMSType={(e) =>
          setArtsChest((prev) => {
            const chest = deepCopy(prev);
            chest[index].pieces[artType].mainSType = e.target.value;
            return chest;
          })
        }
        changeSSType={(e, i) =>
          setArtsChest((prev) => {
            const newChest = deepCopy(prev);
            newChest[index].pieces[artType].subS[i].type = e.target.value;
            return newChest;
          })
        }
        changeSSVal={(e, i, { val }) =>
          setArtsChest((prev) => {
            const newChest = deepCopy(prev);
            newChest[index].pieces[artType].subS[i].val = processNumInp(
              e.target.value,
              val,
              9999
            );
            return newChest;
          })
        }
      />
      <div className="mt-3 flex justify-center">
        <BtnOnCard
          margin="0 0.75rem"
          color="white"
          bgColor="red"
          onClick={() => setArt(index, artType, setArtsChest, { set: null })}
        >
          Remove
        </BtnOnCard>
        <BtnOnCard
          margin="0 0.75rem"
          bgColor="lightGold"
          color="black"
          onClick={() =>
            setUI((prev) => ({
              ...prev,
              modalName: artType,
              modalType
            }))
          }
        >
          Switch
        </BtnOnCard>
      </div>
    </div>
  );
}

export function PieceDetails(props) {
  const { pieceInfo, artType, change4, toMax } = props;
  const { name } = artifacts[pieceInfo.set][artType];
  const msType = pieceInfo.mainSType;
  const avaMSTypes = artMS[artType];
  const isFoP = artType === "flower" || artType === "plume";
  const msVals = avaMSTypes[msType];
  return (
    <>
      <ColoredBox className="px-3 pt-1" bgColor="gold">
        <Typography variant="h5" color="textSecondary" className="ellipsis">
          <b>{name}</b>
        </Typography>
      </ColoredBox>
      <div className="mt-2 pl-4">
        <div className="flex align-center">
          <ColoredBox bgColor="cardBg3" className="radius-50">
            <Select
              color="gold"
              fontSize="1.125rem"
              fontWeight={700}
              className="no-apr px-2 pt-2 pb-1 pointer"
              value={"+" + pieceInfo.level}
              onChange={props.enhance}
            >
              {artLvs("gold").map((lv) => (
                <Option key={lv}>+{lv}</Option>
              ))}
            </Select>
          </ColoredBox>
          <ArrowBtn direction="down" onClick={() => change4("down")} />
          <ArrowBtn direction="up" onClick={() => change4("up")} />
          <MaxBtn onClick={toMax}>MAX</MaxBtn>
        </div>

        <div className="mt-2">
          {isFoP ? (
            <Typography variant="h6" className="pl-5 pt-1">
              {msType}
            </Typography>
          ) : (
            <div className="py-1">
              <Select
                fontSize="1.125rem"
                color="white"
                className="no-apr bg-icon arrow-white pl-5"
                value={msType}
                onChange={props.changeMSType}
              >
                {Object.keys(avaMSTypes).map((type) => (
                  <Option key={type}>{type}</Option>
                ))}
              </Select>
            </div>
          )}
          <ColoredText variant="h3" color="gold" className="pl-5">
            {msVals[pieceInfo.level] + pctOf(msType)}
          </ColoredText>
        </div>
      </div>

      <div className="px-2">
        {pieceInfo.subS.map((stat, i) => (
          <SSLine
            key={i}
            stat={stat}
            changeSSType={(e) => props.changeSSType(e, i)}
            changeSSVal={(e) => props.changeSSVal(e, i, stat)}
          />
        ))}
      </div>
    </>
  );
}
