export const SayuBuffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: (_, __, { ATRs }) => (
      <>
        When Sayu triggers a Swirl reaction while active, she{" "}
        <LGreen>heals</LGreen> all your characters and nearby allies for{" "}
        <Green>300 HP</Green>. The healing is increased by <Green>1.2 HP</Green>{" "}
        for every point of <LGreen>Elemental Mastery</LGreen> she has. This
        effect can be triggered once every 2s.{" "}
        <Red>
          Total healing: {300 + Math.round(ATRs["Elemental Mastery"] * 1.2)}.
        </Red>
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "self"
  },
  {
    src: "Constellation 2",
    desc: () => (
      <>
         <LGreen>Press Kick DMG</LGreen> increased by <Green>3.3%</Green>.
        <br /> <LGreen>Hold Kick DMG</LGreen> increased by <Green>3.3%</Green>{" "}
        for every 0.5s Sayu in Fuufuu Windwheel state, up to <Green>66%</Green>{" "}
        (20 stacks).
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    maxStacks: 20
  },
  {
    src: "Constellation 6",
    desc: (_, __, { ATRs }) => {
      const [atkPct, healing] = SayuBuffs[2].bnValue(ATRs);
      return (
        <>
          Each point of Sayu's Elemental Mastery will:
          <br /> Increases <LGreen>DMG</LGreen> dealt by the Daruma's attacks
          by <Green>0.2%</Green> <LGreen>ATK</LGreen>, up to <Green>400%</Green>{" "}
          <LGreen>ATK</LGreen>.<br /> Increases <LGreen>HP restored</LGreen> by
          Daruma by <Green>3</Green>, up to <Green>6,000</Green> additional HP.{" "}
          <Red>
            DMG Bonus: {Math.round((ATRs.ATK * atkPct) / 100)} (base). Healing
            Bonus: {healing}.
          </Red>
        </>
      );
    },
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    bnValue: (ATRs) => {
      const EM = Math.min(ATRs["Elemental Mastery"], 2000);
      return [EM * 0.2, EM * 3];
    }
  }
];

