import weapons from "../data/weapons";
import artifacts from "../data/artifacts";
import buffsFrChar from "../data/characters/buffsFrChar";
import buffsFrArts from "../data/artifacts/buffsFrArts";
import { artMS } from "../data/artifacts/config";
import { tfmRxns, ampRxnTrigElmts } from "../data/elements";
import { flatSTypes, pctSTypes } from "../data/misc";
import { isValid } from "./helpers";

function initiate(char, charData, wp, wpData) {
  const ATRs = {};
  for (let type of flatSTypes.concat(pctSTypes)) {
    ATRs[type] = 0;
  }
  ATRs["CRIT Rate"] = 5;
  ATRs["CRIT DMG"] = 50;
  ATRs["Energy Recharge"] = 100;
  ATRs["Normal ATK SPD"] = ATRs["Charged ATK SPD"] = 100;
  const stats = charData.stats[char.level];
  for (let key in stats) {
    ATRs[key] = (ATRs[key] || 0) + stats[key];
  }
  ATRs["Base ATK"] += wpData.mainStat.value[wp.level];
  return {
    ATRs,
    bonuses: {
      AllDmg: 0,
      Elemental: { CD: 0 },
      Physical: {},
      NA: { DMG: 0, CR: 0 },
      CA: { DMG: 0, CR: 0 },
      PA: { DMG: 0 },
      ES: { DMG: 0, CR: 0 },
      EB: { DMG: 0, CR: 0 }
    }
  };
}

function addArtBnes(ATRs, bonuses, { sets, pieces }) {
  const artSBnes = { HP: 0, ATK: 0, DEF: 0 };
  for (let type in pieces) {
    const { set, level, mainSType, subS } = pieces[type];
    if (set) {
      artSBnes[mainSType] =
        (artSBnes[mainSType] || 0) + artMS[type][mainSType][level];
      for (let ss of subS) {
        artSBnes[ss.type] = (artSBnes[ss.type] || 0) + Number(ss.val);
      }
    }
  }
  ["HP", "ATK", "DEF"].forEach((type) => {
    const pctStats = artSBnes[type + "%"];
    if (pctStats) {
      artSBnes[type] += Math.round((ATRs["Base " + type] * pctStats) / 100);
    }
    delete artSBnes[type + "%"];
  });
  for (let type in artSBnes) {
    ATRs[type] += artSBnes[type];
  }
  const rxnBnes = {
    Superconduct: 0,
    Swirl: 0,
    "Electro-Charged": 0,
    Overloaded: 0,
    Shattered: 0,
    Melt: 0,
    Vaporize: 0
  };
  sets.forEach((set) => {
    for (let i = 0; i <= set.bonusLv; i++) {
      const { addBnes } = artifacts[set.name].setBonus[i];
      if (addBnes) addBnes(ATRs, bonuses, rxnBnes);
    }
  });
  return { artSBnes, rxnBnes };
}

function addWpSStats(ATRs, wpData, level) {
  wpData.subStats.forEach((stat) => {
    ATRs[stat.type] += stat.value[level];
  });
}

function addRsnBuffs(ATRs, bonuses, rsns) {
  rsns.forEach((rsn) => {
    if (rsn.activated) {
      if (rsn.name === "Fervent Flames") {
        ATRs["ATK%"] += 25;
      } else if (rsn.name === "Shattering Ice") {
        ATRs["CRIT Rate"] += 15;
      } else if (rsn.name === "Enduring Rock") {
        ATRs["Shield Strength"] += 15;
        bonuses.AllDmg += 15;
      }
    }
  });
}

function addArtBuffs(ATRs, bonuses, { sets, BC, subBC }, cond) {
  subBC.forEach((ctrl) => {
    if (isValid(ctrl, cond))
      buffsFrArts[ctrl.set][ctrl.index].addBnes(ATRs, bonuses, ctrl.value);
  });
  BC.forEach((ctrl) => {
    if (isValid(ctrl, cond))
      buffsFrArts[sets[0].name][ctrl.index].addBnes(ATRs, bonuses, ctrl.value);
  });
}

function addWpBuffs(
  ATRs,
  bonuses,
  wp,
  { final, addBnes, buffs },
  subWpBC,
  charData,
  cond
) {
  if (Boolean(final) === cond && addBnes) {
    addBnes(ATRs, bonuses, wp.refinement);
  }
  for (let wpType in subWpBC) {
    subWpBC[wpType].forEach((ctrl) => {
      if (isValid(ctrl, cond)) {
        const { addBnes } = weapons[wpType][ctrl.name].buffs[ctrl.index];
        addBnes(ATRs, bonuses, ctrl.refinement, ctrl.inputs, charData);
      }
    });
  }
  wp.BC.forEach((ctrl) => {
    if (isValid(ctrl, cond)) {
      const { addBnes } = buffs[ctrl.index];
      addBnes(ATRs, bonuses, wp.refinement, ctrl.inputs, charData);
    }
  });
}

function addCharBuffs(ATRs, bonuses, rxnBnes, char, charBC, party, cond) {
  if (!cond) {
    party.forEach((tm) => {
      tm.BC.forEach((ctrl) => {
        const { addBnes } = buffsFrChar[tm.name][ctrl.index];
        if (ctrl.activated)
          addBnes(ATRs, bonuses, rxnBnes, ctrl.inputs, false, char);
      });
    });
  }
  charBC.forEach((ctrl) => {
    const buff = buffsFrChar[char.name][ctrl.index];
    if (isValid(ctrl, cond) && buff.addBnes && buff.isGranted(char)) {
      buff.addBnes(ATRs, bonuses, rxnBnes, ctrl.value, true, char, charBC);
    }
  });
}

function calcFinalATRs(ATRs) {
  ["HP", "ATK", "DEF"].forEach((type) => {
    ATRs[type] += Math.round(
      ATRs["Base " + type] * (1 + ATRs[type + "%"] / 100)
    );
    delete ATRs[type + "%"];
  });
}

export function getRxnBnFrEM(EM) {
  return {
    amplifying: Math.round((2780 * EM) / (EM + 1400)) / 10,
    transformative: Math.round((16000 * EM) / (EM + 2000)) / 10,
    shield: Math.round((4440 * EM) / (EM + 1400)) / 10
  };
}

function calcFinalRxnBnes(ATRs, rxnBnes, { vision }) {
  const { amplifying, transformative } = getRxnBnFrEM(
    ATRs["Elemental Mastery"]
  );
  Object.keys(tfmRxns).forEach((rxn) => {
    rxnBnes[rxn] += transformative;
  });
  Object.keys(ampRxnTrigElmts).forEach((rxn) => {
    rxnBnes[rxn] += amplifying;
  });
  rxnBnes.Melt = (vision === "Pyro" ? 2 : 1.5) * (1 + rxnBnes.Melt / 100);
  rxnBnes.Vaporize =
    (vision === "Pyro" ? 1.5 : 2) * (1 + rxnBnes.Vaporize / 100);
}

export default function getStats(
  char,
  charData,
  charBC,
  wp,
  subWpBC,
  arts,
  rsns,
  party
) {
  const wpData = weapons[charData.weapon][wp.name];
  const { ATRs, bonuses } = initiate(char, charData, wp, wpData);
  const { artSBnes, rxnBnes } = addArtBnes(ATRs, bonuses, arts);
  addWpSStats(ATRs, wpData, wp.level);
  addRsnBuffs(ATRs, bonuses, rsns);
  addArtBuffs(ATRs, bonuses, arts, false);
  addWpBuffs(ATRs, bonuses, wp, wpData, subWpBC, charData, false);
  addCharBuffs(ATRs, bonuses, rxnBnes, char, charBC, party, false);
  calcFinalATRs(ATRs);

  addArtBuffs(ATRs, bonuses, arts, true);
  addWpBuffs(ATRs, bonuses, wp, wpData, subWpBC, charData, true);
  addCharBuffs(ATRs, bonuses, rxnBnes, char, charBC, party, true);
  bonuses.Elemental.DMG = ATRs[charData.vision + " DMG Bonus"];
  bonuses.Physical.DMG = ATRs["Physical DMG Bonus"];
  calcFinalRxnBnes(ATRs, rxnBnes, charData);
  return { ATRs, bonuses, artSBnes, rxnBnes };
}
