import { Green, LGreen, Red } from "../../../styledCpns/DataDisplay";
import { mapToLevels } from "../../misc";
import { multMult } from "../config";

export const HuTaoBuffs = [
  {
    src: "Elemental Skill",
    desc: () => (
      <>
        Increases Hu Tao's <LGreen>ATK</LGreen> based on her{" "}
        <LGreen>Max HP</LGreen>, and converts her <LGreen>attack DMG</LGreen> to{" "}
        <LGreen>Pyro DMG</LGreen>.
      </>
    ),
    isGranted: () => true,
    affect: "self",
    final: true,
    addBnes: (ATRs, _, __, ___, ____, char) => {
      const level = char["Elemental Skill"] + (char.constellation >= 3 ? 3 : 0);
      ATRs.ATK += Math.round((ATRs.HP * 3.84 * multMult[4][level]) / 100);
    },
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: false
  },
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        When a Paramita Papilio state ends, all allies in the party will have
        their <LGreen>CRIT Rate</LGreen> increased by <Green>12%</Green> for 8s.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "teammates",
    addBnes: (ATRs) => {
      ATRs["CRIT Rate"] += 12;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        When Hu Tao's HP is equal to or less than 50%, her{" "}
        <LGreen>Pyro DMG Bonus</LGreen> is increased by <Green>33%</Green>.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addBnes: (ATRs) => {
      ATRs["Pyro DMG Bonus"] += 33;
    }
  },
  {
    src: "Constellation 2",
    desc: (_, __, { ATRs }) => (
      <>
        Increases the <LGreen>Blood Blossom DMG</LGreen> by an amount equal to{" "}
        <Green>10%</Green> of Hu Tao's <LGreen>Max HP</LGreen>.{" "}
        <Red>DMG Bonus: {HuTaoBuffs[3].bnValue(ATRs)} (base).</Red>
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    bnValue: ({ HP }) => Math.round(HP / 10)
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        When Hu Tao's HP drops below 25%, or when she suffers a lethal strike,
        her <LGreen>CRIT Rate</LGreen> is increased by <Green>100%</Green> for
        10s.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    addBnes: (ATRs) => {
      ATRs["CRIT Rate"] += 100;
    }
  }
];

