import React, { useContext } from "react";
import { ArtsContext, FixedContext, ResultContext, deepCopy } from "../../App";
import { Typography } from "@material-ui/core";
import { Option } from "../../styledCpns/Inputs";
import { Setter, SetterSelect, Section, NoContent } from "../Modifiers.styled";
import buffsFrArts from "../../data/artifacts/buffsFrArts";
import { crystallizables } from "../../data/elements";

export default function ArtsBuff(props) {
  const { artsChest } = useContext(ArtsContext);
  const { setArtsChest, panel2 } = useContext(FixedContext);
  const { ATRs } = useContext(ResultContext);
  const content = [];
  const arts = artsChest[panel2];
  const name = arts.sets[0]?.name;

  arts.BC.forEach((ctrl, i) => {
    const { activated } = ctrl;
    const buff = buffsFrArts[name][ctrl.index];
    const { inputType } = buff;
    const setters = inputType && (
      <Setter>
        <Typography className="mr-3">
          {{ stacks: "Stacks", crystallize: "Element" }[inputType]}
        </Typography>
        <SetterSelect
          value={ctrl.value}
          onChange={(e) => {
            setArtsChest((prev) => {
              const newChest = deepCopy(prev);
              newChest[panel2].BC[i].value = e.target.value;
              return newChest;
            });
          }}
        >
          {{
            stacks: Array.from({ length: buff.maxStacks }, (_, i) => i + 1),
            crystallize: crystallizables
          }[inputType].map((opt) => (
            <Option key={opt}>{opt}</Option>
          ))}
        </SetterSelect>
      </Setter>
    );

    content.push(
      <Section
        key={name + i}
        checked={activated}
        handleCheck={() => {
          setArtsChest((prev) => {
            const newChest = deepCopy(prev);
            newChest[panel2].BC[i].activated = !activated;
            return newChest;
          });
        }}
        heading={name + " (self)"}
        desc={buff.desc({ ATRs })}
        setters={setters}
      />
    );
  });
  arts.subBC.forEach((ctrl, i) => {
    const { activated, set } = ctrl;
    const buff = buffsFrArts[set][ctrl.index];
    content.push(
      <Section
        key={set + i}
        checked={activated}
        handleCheck={() => {
          setArtsChest((prev) => {
            const newChest = deepCopy(prev);
            newChest[panel2].subBC[i].activated = !activated;
            return newChest;
          });
        }}
        heading={set}
        desc={buff.desc({ ATRs })}
      />
    );
  });
  return content.length ? content : <NoContent type="buffs" />;
}
