import React, { useState, useContext, useEffect } from "react";
import { FixedContext, UIContext } from "../../App";
import { Typography } from "@material-ui/core";
import { ColoredText, ColoredBox } from "../../styledCpns/DataDisplay";
import { BtnOnCard } from "../../styledCpns/Inputs";
import { pctOf } from "../../data/misc";
import { artMS } from "../../data/artifacts/config";
import artifacts from "../../data/artifacts";
import { setArt } from "../Modal/handlers";

export default function LdtShelf({ info, type, remove }) {
  const { set, level, mainSType, subS } = info;
  const { setArtsChest } = useContext(FixedContext);
  const { shownSt } = useContext(UIContext).ui;
  const [picked, setPicked] = useState(false);
  useEffect(() => {
    if (picked) setPicked(false);
  }, [info]);

  return (
    <div className="full-h flex-col justify-between">
      <div>
        <div className="flex-col">
          <ColoredBox className="px-3 pt-1" bgColor="gold">
            <Typography variant="h5" color="textSecondary" className="ellipsis">
              <b>{artifacts[set][type].name}</b>
            </Typography>
          </ColoredBox>
          <ColoredBox bgColor="cardBg3" className="ml-4 mt-2 mr-auto radius-50">
            <ColoredText variant="h6" color="gold" className="px-2 pt-2 pb-1">
              <b>+{level}</b>
            </ColoredText>
          </ColoredBox>
        </div>
        <div className="ml-4 px-5">
          <Typography variant="h5" align="right">
            {mainSType}
          </Typography>
          <ColoredText variant="h2" align="right" color="gold">
            {artMS[type][mainSType][level] + pctOf(mainSType)}
          </ColoredText>
        </div>
        <div className="mt-2 px-3">
          {subS.map((stat, i) => (
            <ColoredBox
              key={i}
              bgColor="cardBg2"
              className="mb-2 pt-1 px-3 flex align-center"
            >
              <Typography variant="h6" className="mr-2">
                {stat.type}
              </Typography>
              <Typography variant="h6">
                +{stat.val + pctOf(stat.type)}
              </Typography>
            </ColoredBox>
          ))}
        </div>
      </div>
      <Typography>{}</Typography>
      <div className="flex justify-center">
        <BtnOnCard color="white" bgColor="red" onClick={remove}>
          Remove
        </BtnOnCard>
        <BtnOnCard
          className="ml-3"
          bgColor="lightGold"
          onClick={() => {
            setArt(shownSt, type, setArtsChest, info);
            setPicked(true);
          }}
        >
          {picked ? "Picked" : "Pick"}
        </BtnOnCard>
      </div>
    </div>
  );
}
