import React, { useContext, useMemo, useCallback } from "react";
import {
  FixedContext,
  UIContext,
  PartyContext,
  WpContext,
  deepCopy
} from "../../App";
import { Box } from "@material-ui/core";
import { IconBtn } from "../../styledCpns/Inputs";
import { Img, ColoredBox } from "../../styledCpns/DataDisplay";
import { Pedestal } from "./styles";
import CopySection from "./CopySection";
import characters from "../../data/characters";
import { wikia } from "../../data/misc";
import { removeTm } from "./handlers";
import { getWpRange, getSubWpBC } from "../../helpers";

function getCopyList(parties, shownSt, stNames) {
  let thisHasAny = false;
  for (let tm of parties[shownSt]) {
    if (tm.name) {
      thisHasAny = true;
      break;
    }
  }
  let result = [];
  if (!thisHasAny) {
    result = stNames.filter((_, i) => {
      let hasNone = true;
      for (let tm of parties[i]) {
        if (tm.name) {
          hasNone = false;
          break;
        }
      }
      if (i === shownSt || hasNone) {
        return false;
      }
      return true;
    });
  }
  return result;
}

export default function PartyCard(props) {
  const { setUI, setParties, setSubWpBCs, setElmtCs } = useContext(
    FixedContext
  );
  const { shownSt, stNames } = useContext(UIContext).ui;
  const { wpsRack } = useContext(WpContext);
  const { parties } = useContext(PartyContext);
  const copyList = useMemo(() => getCopyList(parties, shownSt, stNames), [
    parties,
    shownSt,
    stNames
  ]);

  const copyParty = useCallback(
    (state) => {
      const pos = stNames.indexOf(state);
      const party = deepCopy(parties[pos]);
      setParties((prev) => {
        const parties = deepCopy(prev);
        parties[shownSt] = party;
        return parties;
      });
      const { name } = wpsRack[shownSt];
      setSubWpBCs((prev) => {
        const subWpBCs = deepCopy(prev);
        const wpRange = getWpRange(party);
        for (let wp of wpRange) {
          subWpBCs[shownSt][wp] = getSubWpBC(wp, name);
        }
        return subWpBCs;
      });
      setElmtCs((prev) => {
        const elmtCs = deepCopy(prev);
        elmtCs[shownSt] = deepCopy(prev[pos]);
        return elmtCs;
      });
    },
    [stNames, parties, shownSt, wpsRack]
  );

  return (
    <Pedestal>
      <Box className="px-2 flex justify-around" width="100%">
        {parties[shownSt].map((tm, tmI) => (
          <Box key={tmI} className="relative" width="4.5rem" height="4.5rem">
            {tm.name ? (
              <TmSlot tm={tm} tmI={tmI} />
            ) : (
              <EmptySlot tmI={tmI} setUI={setUI} />
            )}
          </Box>
        ))}
      </Box>
      {copyList.length ? (
        <div className="mt-4 px-3 flex justify-end">
          <CopySection copyList={copyList} copy={copyParty} />
        </div>
      ) : null}
    </Pedestal>
  );
}

function TmSlot({ tm, tmI }) {
  const { setUI, handlers } = useContext(FixedContext);
  const { shownSt } = useContext(UIContext).ui;
  const { parties } = useContext(PartyContext);
  return (
    <>
      <ColoredBox bgColor="cardBg3" className="radius-50 zoomin-on-hover">
        <Img
          src={wikia + characters[tm.name].icon + ".png"}
          alt="teammate"
          className="pointer"
          width="100%"
          borderRadius="inherit"
          onClick={() =>
            setUI((prev) => ({
              ...prev,
              modalName: "Characters",
              modalType: tmI
            }))
          }
        />
      </ColoredBox>
      <IconBtn
        icon="fas fa-times"
        position="absolute"
        bottom="-0.25rem"
        right="-0.625rem"
        color="white"
        bgColor="red"
        size="1.75rem"
        fontSize="0.875rem"
        className="glow-on-hover"
        onClick={() =>
          removeTm(
            deepCopy(parties[shownSt]),
            tmI,
            characters[tm.name],
            handlers.setTm
          )
        }
      />
    </>
  );
}

const EmptySlot = ({ setUI, tmI }) => (
  <IconBtn
    icon="fas fa-plus"
    color="rgb(255, 255, 255, 0.7)"
    bgColor="cardBg3"
    size="100%"
    fontSize="1.5rem"
    className="glow-on-hover"
    onClick={() =>
      setUI((prev) => ({
        ...prev,
        modalName: "Characters",
        modalType: tmI
      }))
    }
  />
);
