import React, { useContext } from "react";
import {
  FixedContext,
  CharContext,
  CharCsContext,
  ResultContext,
  deepCopy
} from "../../App";
import { Typography } from "@material-ui/core";
import {
  Setter,
  SetterSelect,
  SetterInput,
  Section,
  NoContent
} from "../Modifiers.styled";
import { Option } from "../../styledCpns/Inputs";
import buffsFrChar from "../../data/characters/buffsFrChar";
import { processNumInp } from "../../helpers";

export default function SelfBuff(props) {
  const { setCharCs, panel2 } = useContext(FixedContext);
  const { char } = useContext(CharContext);
  const { charCs } = useContext(CharCsContext);
  const { ATRs } = useContext(ResultContext);
  const content = [];
  charCs[panel2].BC.forEach((ctrl, i) => {
    const buff = buffsFrChar[char.name][ctrl.index];
    const { activated } = ctrl;
    if (buff.isGranted(char)) {
      const setters = buff.maxStacks && (
        <Setter>
          <Typography className="mr-3" align="right">
            {buff.alterLabel || "Stacks"}
          </Typography>
          {buff.maxStacks > 10 ? (
            <SetterInput
              type="text"
              value={ctrl.value}
              onChange={(e) =>
                setCharCs((prev) => {
                  const Cs = deepCopy(prev);
                  Cs[panel2].BC[i].value = processNumInp(
                    e.target.value,
                    ctrl.value,
                    buff.maxStacks
                  );
                  return Cs;
                })
              }
            ></SetterInput>
          ) : (
            <SetterSelect
              value={ctrl.value}
              onChange={(e) =>
                setCharCs((prev) => {
                  const Cs = deepCopy(prev);
                  Cs[panel2].BC[i].value = Number(e.target.value);
                  return Cs;
                })
              }
            >
              {Array.from({ length: buff.maxStacks }, (_, i) => i + 1).map(
                (opt) => (
                  <Option key={opt}>{opt}</Option>
                )
              )}
            </SetterSelect>
          )}
        </Setter>
      );

      content.push(
        <Section
          key={i}
          checked={activated}
          handleCheck={() => {
            setCharCs((prev) => {
              const Cs = deepCopy(prev);
              Cs[panel2].BC[i].activated = !activated;
              if (buff.canInfuse) {
                const { src, infuseRange: range, canBeOverrided } = buff;
                if (activated) {
                  for (let i in Cs[panel2].infs) {
                    if (Cs[panel2].infs[i].src === src) {
                      Cs[panel2].infs.splice(i, 1);
                      break;
                    }
                  }
                } else {
                  Cs[panel2].infs.push({ src, range, canBeOverrided });
                }
              }
              return Cs;
            });
          }}
          heading={buff.src}
          desc={buff.desc(true, char, { ATRs, charBC: charCs[panel2].BC })}
          setters={setters}
        />
      );
    }
  });
  return content.length ? content : <NoContent type="buffs" />;
}
