import styled from "styled-components";
import React from "react";
import { Typography, Collapse } from "@material-ui/core";
import { ColoredText } from "../styledCpns/DataDisplay";
import { Select } from "../styledCpns/Inputs";
import { colorCodes } from "../data/misc";

export const StyledCollapse = styled(Collapse)`
  padding: 0 1rem 0 0.5rem;
  & .mb-m1:first-child {
    margin-top: -0.5rem;
  }
`;

export const Setter = styled.div`
  margin: 0.5rem 0.5rem 0 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  :not(:last-child) {
    margin-bottom: 1rem;
  }
`;

export const SetterInput = styled.input`
  padding: 0.25rem 0.5rem;
  width: 4rem;
  border: none;
  border-radius: 0.25rem;
  font-size: 1rem;
  text-align: right;
  outline: none;
  &:focus {
    box-shadow: 0 0 0 3px ${colorCodes.lightGold};
  }
`;

export const SetterCheckbox = styled.input`
  transform: scale(1.4);
  margin-left: 0.1675rem;
`;

export const SetterSelect = styled((props) => (
  <Select bgColor="white" borderRadius="0.25rem" fontWeight={700} {...props} />
))`
  padding: 0 0.5rem;
`;

export const BuffCheckbox = styled.input`
  margin-right: 0.75rem;
  transform: scale(1.4);
`;

export const NoContent = (props) => (
  <Typography className="mt-4 mb-3" variant="body1" align="center">
    No {props.type} found
  </Typography>
);

export function Section(props) {
  return (
    <div className="pt-2">
      <div className="mb-1 flex align-center">
        <BuffCheckbox
          type="checkbox"
          checked={props.checked}
          onChange={props.handleCheck}
        />
        <ColoredText variant="subtitle1" color="lightGold">
          {props.heading}
        </ColoredText>
      </div>
      <Typography>{props.desc}</Typography>
      {props.setters}
    </div>
  );
}
