import React, { useContext } from "react";
import { FixedContext, PartyContext, deepCopy } from "../../App";
import { Typography } from "@material-ui/core";
import { Option } from "../../styledCpns/Inputs";
import {
  Setter,
  SetterSelect,
  SetterInput,
  Section,
  NoContent
} from "../Modifiers.styled";
import debuffsFrChar from "../../data/characters/debuffsFrChar";
import { swirlables } from "../../data/elements";
import { processNumInp } from "../../helpers";

export default function PartyDebuff(props) {
  const { setParties, panel2 } = useContext(FixedContext);
  const { parties } = useContext(PartyContext);
  const content = [];

  parties[panel2].forEach((tm, tmI) => {
    tm.DC.forEach((ctrl, j) => {
      const { activated, inputs } = ctrl;
      const { src, desc, inputTypes } = debuffsFrChar[tm.name][j];
      const setters =
        inputs &&
        inputs.map((input, k) => {
          let inpCpn;
          if (inputTypes[k] === "absorbtion") {
            inpCpn = (
              <SetterSelect
                value={input}
                onChange={(e) =>
                  setParties((prev) => {
                    const parties = deepCopy(prev);
                    parties[panel2][tmI].DC[j].inputs[k] = e.target.value;
                    return parties;
                  })
                }
              >
                {swirlables.map((opt) => (
                  <Option key={opt}>{opt}</Option>
                ))}
              </SetterSelect>
            );
          } else {
            inpCpn = (
              <SetterInput
                type="text"
                value={input}
                onChange={(e) =>
                  setParties((prev) => {
                    const parties = deepCopy(prev);
                    parties[panel2][tmI].DC[j].inputs[k] = processNumInp(
                      e.target.value,
                      input,
                      inputTypes[k] === "level" ? 13 : 1 //
                    );
                    return parties;
                  })
                }
              />
            );
          }
          return (
            <Setter key={k}>
              <Typography className="mr-3">
                {inputTypes[k] === "absorption"
                  ? "Element Absorbed"
                  : src + " level"}
              </Typography>
              {inpCpn}
            </Setter>
          );
        });

      content.push(
        <Section
          key={tmI + j}
          checked={activated}
          handleCheck={() =>
            setParties((prev) => {
              const parties = deepCopy(prev);
              parties[panel2][tmI].DC[j].activated = !activated;
              return parties;
            })
          }
          heading={src}
          desc={desc(false, inputs)}
          setters={setters}
        />
      );
    });
  });
  return content.length ? content : <NoContent type="debuffs" />;
}
