import React, { useState, useContext, useEffect, useRef } from "react";
import { aHasB, ArtsContext, FixedContext, UIContext } from "../../App";
import { Box, Typography, Collapse } from "@material-ui/core";
import { Img } from "../../styledCpns/DataDisplay";
import { CloseBtn } from "../../styledCpns/Inputs";
import {
  ModalInner,
  ModalGridItem,
  VisionIcon,
  StyledFilter,
  FilterBtn,
  BtmBar
} from "./styles";
import CopySection from "../Setups/CopySection";
import {
  colorCodes,
  dsArtType,
  wikia,
  visionIcons,
  wpIcons
} from "../../data/misc";

export default function ModalContent(props) {
  const { setUI } = useContext(FixedContext);
  const { artsChest } = useContext(ArtsContext);
  const { ui } = useContext(UIContext);
  const { modalName, modalType } = ui;
  const [filterOn, setFilterOn] = useState(false);
  const copyList = ui.stNames.filter((_, i) => {
    if (
      (aHasB(["equip", "change"], modalType) && i === props.index) ||
      !artsChest[i].pieces[modalName]?.set
    ) {
      return false;
    }
    return true;
  });

  const modalRef = useRef();
  useEffect(() => {
    const closeModal = (e) => {
      if (
        modalRef.current &&
        !modalRef.current.contains(e.target) &&
        modalName
      ) {
        setUI((prev) => {
          const newUI = { ...prev };
          newUI.modalName = null;
          return newUI;
        });
      }
    };
    const closeModalWithEsc = (e) => {
      if (e.key === "Escape") {
        closeModal(e);
      }
    };
    document.addEventListener("click", closeModal);
    document.addEventListener("keydown", closeModalWithEsc);
    return () => {
      document.removeEventListener("click", closeModal);
      document.removeEventListener("keydown", closeModalWithEsc);
    };
  }, [modalName]);

  const isChar = modalName === "Characters";
  return (
    <ModalInner ref={modalRef}>
      <div className="p-2 heading">
        <div className="heading_inner full-h flex align-center cmn-shadow relative">
          <div className="full-w">
            <Typography variant="h4" align="center">
              {dsArtType[modalName] || modalName}
            </Typography>
          </div>
          <CloseBtn
            color="white"
            bgColor="black"
            onClick={() => setUI((prev) => ({ ...prev, modalName: null }))}
          />
          {isChar && (
            <>
              <FilterBtn handleClick={() => setFilterOn(!filterOn)} />
              <Filter
                filterOn={filterOn}
                setFilterOn={setFilterOn}
                filter={props.filter}
                setFilter={props.setFilter}
              />
            </>
          )}
        </div>
      </div>
      <div className="px-3 pt-2 pb-3 body">
        <div className="pr-3 full-h overflow-auto">
          <div
            className={"flex-wrap" + (dsArtType[modalName] ? " mb-5 pb-3" : "")}
          >
            {props.data.map((item, i) => (
              <ModalGridItem
                key={i}
                className={isChar ? "w-vision" : "wo-vision"}
              >
                <Box
                  borderRadius="0.625rem"
                  bgcolor={colorCodes[item.rarity]}
                  boxShadow={3}
                  className={
                    "pointer zoomin-on-hover flex overflow-hidden " +
                    (isChar ? "pt-3" : "p-1")
                  }
                  onClick={() => props.choose(item.name)}
                >
                  <Img src={wikia + item.icon + ".png"} alt="" width="100%" />
                </Box>
                {isChar && (
                  <VisionIcon
                    src={wikia + visionIcons[item.vision] + ".png"}
                    alt="vision"
                  />
                )}
              </ModalGridItem>
            ))}
          </div>
        </div>
      </div>

      {dsArtType[modalName] && copyList.length && (
        <BtmBar className="flex justify-end align-center">
          <CopySection copyList={copyList} copy={props.copyArt} />
        </BtmBar>
      )}
    </ModalInner>
  );
}

export function Filter(props) {
  const { setFilter, setFilterOn } = props;
  const { type, value } = props.filter;
  return (
    <StyledFilter className="cmn-shadow">
      <Collapse in={props.filterOn}>
        <div className="flex-wrap justify-around">
          <div className="filter_menu vision_menu custom-sb">
            {Object.entries(visionIcons).map((pair, i) => {
              const chosen = type === "vision" && value === pair[0];
              return (
                <Img
                  key={i}
                  width="2rem"
                  className={"my-3 filter_icon" + (chosen ? " chosen" : "")}
                  src={wikia + pair[1] + ".png"}
                  alt="vision"
                  onClick={() => {
                    setFilter(chosen, { type: "vision", value: pair[0] });
                    setFilterOn(false);
                  }}
                />
              );
            })}
          </div>
          <div className="filter_menu wp_menu align-center custom-sb">
            {Object.entries(wpIcons).map((pair, i) => {
              const chosen = type === "weapon" && value === pair[0];
              return (
                <Img
                  key={i}
                  width="2.25rem"
                  height="2.25rem"
                  className={"my-wp filter_icon" + (chosen ? " chosen" : "")}
                  src={wikia + pair[1] + ".png"}
                  alt="vision"
                  onClick={() => {
                    setFilter(chosen, { type: "weapon", value: pair[0] });
                    setFilterOn(false);
                  }}
                />
              );
            })}
          </div>
        </div>
      </Collapse>
    </StyledFilter>
  );
}
