export const VentiBuffs = [
  {
    src: "Constellation 4",
    desc: () => (
      <>
        When Venti picks up an Elemental Orb or Particle, he receives a{" "}
        <Green>25%</Green> <LGreen>Anemo DMG Bonus</LGreen> for 10s.
      </>
    ),
    isGranted: (char) => char.constellation >= 4,
    affect: "self",
    addBnes: (ATRs) => {
      ATRs["Anemo DMG Bonus"] += 25;
    }
  }
];

