import React, { useContext } from "react";
import { FixedContext } from "../../App";
import { Typography } from "@material-ui/core";
import { BtnOnCard, IconBtn } from "../../styledCpns/Inputs";
import { StyledLdt } from "./styles";
import artifacts from "../../data/artifacts";
import { wikia } from "../../data/misc";

export default function Ldt(props) {
  const { ldt, store, i, coord, setCoord } = props;
  return (
    <StyledLdt>
      <div className="btn_bar">
        <BtnOnCard bgColor="red" color="white" onClick={props.remove}>
          Remove
        </BtnOnCard>
        <BtnOnCard bgColor="lightGold" onClick={props.pick}>
          Pick
        </BtnOnCard>
      </div>
      <div className="grow-1">
        <Typography
          className={"mx-3 " + (ldt.name ? "my-1" : "my-2")}
          variant="h5"
          align="center"
        >
          {ldt.name}
        </Typography>
        <div className="line flex-wrap justify-center">
          {["flower", "plume", "sands", "goblet", "circlet"].map((type, j) => {
            const artDt = artifacts[ldt.arts?.[type]?.set]?.[type];
            return (
              <div key={j} className="ldt_item">
                {artDt ? (
                  <Inner
                    icon={artDt.icon}
                    bg="gold"
                    chosen={
                      store === coord.store &&
                      i === coord.i &&
                      coord.type === type
                    }
                    handler={() => setCoord({ store, i, type })}
                  />
                ) : (
                  <Empty store={store} type={type} i={i} />
                )}
              </div>
            );
          })}
        </div>
      </div>
    </StyledLdt>
  );
}

function Inner({ icon, bg, chosen, handler }) {
  return (
    <div className="ldt_inner flex" onClick={handler}>
      <img
        className={"item_img pointer " + bg + (chosen ? " glowing" : "")}
        src={wikia + icon + ".png"}
        alt=""
      />
    </div>
  );
}

function Empty({ store, type, i }) {
  const { setUI } = useContext(FixedContext);
  return (
    <div className="ldt_inner full-h">
      <IconBtn
        icon={store === "Teapot" ? "" : "fas fa-plus"}
        fontSize="1.75rem"
        size="100%"
        color="rgb(255, 255, 255, 0.7)"
        bgColor="cardBg3"
        onClick={
          store === "Teapot"
            ? null
            : () =>
                setUI((prev) => ({
                  ...prev,
                  modalName: type,
                  modalType: "archive/" + i
                }))
        }
      />
    </div>
  );
}
