import styled from "styled-components";
import { IconBtn } from "../../styledCpns/Inputs";
import { colorCodes } from "../../data/misc";

export function LoadBtn({ act, setLoadCtrl, className, ...others }) {
  return (
    <IconBtn
      {...others}
      icon={`fas fa-${act}load`}
      fontSize="1rem"
      size="2rem"
      bgColor="lightGold"
      className={className + " pointer glow-on-hover cmn-shadow"}
      onClick={() => setLoadCtrl(act)}
    />
  );
}

export const StyledMenu = styled.div`
  position: absolute;
  top: calc(100% + 0.5rem);
  left: 0;
  z-index: 2;
  .dropdown {
    width: 9rem;
    border: 2px solid black;
    border-radius: 0.25rem;
    padding: 0.25rem;
    background: white;
    color: black;
  }
  .desc {
    padding: 0.5rem;
    background: #ded9d6;
    font-size: 1.125rem;
    font-weight: 700;
    cursor: pointer;
  }
  .active {
    background: ${colorCodes.green};
  }
  .desc:hover {
    background: ${colorCodes.lightGold};
  }
  .desc:not(:last-child) {
    margin-bottom: 0.25rem;
  }
`;

export const StyledChoices = styled.div`
  border-radius: 1rem;
  background: white;
  .big-icon {
    width: 3rem;
    margin: 0 0.25rem;
  }
`;

export const StyledOverview = styled.div`
  max-height: 100%;
  flex-grow: 1;
  .hover-effect:hover {
    transform: scale(1.05);
  }
  .hover-effect:not(.glowing):hover {
    box-shadow: 0 0 0 2.5px white;
  }
  .add_btn {
    min-height: 5.375rem;
  }
  .item {
    border-radius: 0.375rem;
    background: ${colorCodes.cardBg3};
  }
  .rfm {
    position: absolute;
    top: 0.25rem;
    left: 0.25rem;
    border-radius: 0.25rem;
    padding: 0 0.25rem;
    background: ${colorCodes.cardBg1};
    font-weight: 700;
  }
  .rfm.max {
    color: ${colorCodes.orange};
  }
  .img_wrapper {
    border-radius: 0.375rem 0.375rem 1.5rem 0;
    overflow: hidden;
  }
  .img_wrapper:hover > img {
    transform: scale(1.2);
  }
  .gold {
    background: ${colorCodes.gold};
  }
  .purple {
    background: ${colorCodes.purple};
  }
  .blue {
    background: ${colorCodes.blue};
  }
  .level {
    padding: 0.375rem 0 0.25rem;
    text-align: center;
    font-size: 0.875rem;
    font-weight: 700;
  }
  @media (min-width: 420px) {
    font-size: 1rem;
  }
  @media (max-width: 769px) {
    min-width: 100%;
    font-size: 1rem;
    .level {
      font-size: 1rem;
    }
  }
`;

export const PossGridItem = styled.div`
  padding: 0.25rem 0.5rem 0.25rem 0;
  flex-grow: 0;
  max-width: 33%;
  flex-basis: 33%;
  @media (min-width: 360px) {
    padding: 0.5rem 1rem 0.5rem 0;
  }
  @media (min-width: 440px) {
    max-width: 25%;
    flex-basis: 25%;
  }
  @media (min-width: 610px) {
    max-width: 20%;
    flex-basis: 20%;
  }
  @media (min-width: 769px) {
    max-width: 25%;
    flex-basis: 25%;
  }
  @media (min-width: 1025px) {
    max-width: 16.666667%;
    flex-basis: 16.666667%;
  }
`;

export const StyledDetails = styled.div`
  min-width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  .inner {
    width: 100%;
    max-width: 19.25rem;
    border-radius: 0.5rem;
    padding-top: 0.75rem;
    padding-bottom: 0.75rem;
    background: ${colorCodes.cardBg1};
    box-shadow: rgb(0 0 0 / 20%) 0px 3px 3px -2px,
      rgb(0 0 0 / 14%) 0px 3px 4px 0px, rgb(0 0 0 / 12%) 0px 1px 8px 0px;
  }
  .embryo {
    height: calc(100% - 2.75rem);
  }
  @media (min-width: 610px) {
    min-width: ${(props) => (props.forChar ? "16.75rem" : "18.25rem")};
    max-width: ${(props) => (props.forChar ? "16.75rem" : "18.25rem")};
  }
`;

export const StyledLdt = styled.div`
  margin-bottom: 0.5rem;
  padding: 0.5rem;
  border-radius: 1rem;
  background: ${colorCodes.cardBg1};
  .ldt_item {
    width: 4.5rem;
    min-width: 4.5rem;
    min-height: 4.5rem;
  }
  .ldt_inner {
    padding: 0.25rem;
  }
  .btn_bar {
    padding: 0.5rem 0.5rem 0;
    display: flex;
    justify-content: space-between;
  }
  .item_img {
    border-radius: 0.75rem;
    width: 100%;
  }
  @media (min-width: 391px) {
    .line {
      padding: 0 1.25rem;
    }
  }
  @media (min-width: 440px) {
    .ldt_item {
      width: 5rem;
      min-width: 5rem;
      min-height: 5rem;
    }
  }
  @media (min-width: 769px) {
    .ldt_inner {
      padding: 0.375rem;
    }
  }
  @media (min-width: 1025px) {
    display: flex;
    .ldt_inner {
      padding: 0.5rem;
    }
    .btn_bar {
      flex-direction: column;
      justify-content: flex-start;
    }
    .btn_bar > button:first-child {
      margin-bottom: 0.75rem;
    }
  }
`;
