import React, { useContext } from "react";
import { FixedContext, CharContext } from "../../App";
import { Typography, Box } from "@material-ui/core";
import { ColoredText, Img, StarLine } from "../../styledCpns/DataDisplay";
import { Select, Option, IconBtn } from "../../styledCpns/Inputs";
import { levels, wikia } from "../../data/misc";

export default function GeneralInfo(props) {
  const { setChar, setUI } = useContext(FixedContext);
  const { char, charData } = useContext(CharContext);
  return (
    <div className="flex relative">
      <Box height="8.5rem" className="mr-3">
        <Img
          src={wikia + charData.icon + ".png"}
          alt="character"
          width="6.25rem"
          className="pointer"
          onClick={() =>
            setUI((prev) => ({
              ...prev,
              modalName: "Characters",
              modalType: "switch"
            }))
          }
        />
      </Box>
      <Box width="11rem">
        <ColoredText variant="h1" color={charData.vision}>
          {char.name}
        </ColoredText>
        <StarLine rarity={charData.rarity} />
        <div className="flex">
          <Typography variant="h6" className="mr-1">
            <b>Level</b>
          </Typography>
          <Select
            color={charData.vision}
            fontSize="1.125rem"
            fontWeight={700}
            align="right"
            value={char.level}
            onChange={(e) =>
              setChar((prev) => {
                const level = e.target.value;
                const maxLv = Number(level.slice(-2));
                return {
                  ...prev,
                  level,
                  ascension: maxLv === 20 ? 0 : (maxLv - 30) / 10
                };
              })
            }
          >
            {levels.map((level) => (
              <Option key={level}>{level}</Option>
            ))}
          </Select>
        </div>
      </Box>
      <IconBtn
        icon="fas fa-info-circle"
        position="absolute"
        bottom="1rem"
        right={0}
        fontSize="1.5rem"
        color="lightGold"
        bgColor="transparent"
        onClick={() => setUI((prev) => ({ ...prev, introOn: true }))}
      />
    </div>
  );
}
