import React, { useState } from "react";
import { Card, LimitedBox } from "../styledCpns/Layout";
import { MainSelect } from "../styledCpns/Inputs";
import Buff from "./Buff";
import Debuff from "./Debuff";
import Target from "./Target";

export default function Modifiers(props) {
  const [tab, setTab] = useState("Buffs");
  return (
    <Card className="p-4" bgColor="cardBg1">
      <MainSelect
        padding="ms-pad"
        tab={tab}
        setTab={(e) => setTab(e.target.value)}
        options={["Buffs", "Debuffs", "Target"]}
      />
      <LimitedBox height="34rem" className="mt-3 custom-sb">
        {
          {
            Buffs: <Buff />,
            Debuffs: <Debuff />,
            Target: <Target />
          }[tab]
        }
      </LimitedBox>
    </Card>
  );
}
