export const RoyalSeries = {
  buffs: [
    {
      affect: "self",
      labels: ["Stacks"],
      inputTypes: ["stacks"],
      values: [5],
      addBnes: (ATRs, _, wpRfm, inputs) => {
        ATRs["CRIT Rate"] += (6 + wpRfm * 2) * inputs[0];
      }
    }
  ],
  pasvName: "Focus",
  pasvDescs: (wpRfm) => [
    {
      buff: (
        <>
          Upon dealing damage to an opponent, increases{" "}
          <LGreen>CRIT Rate</LGreen> by <Green>{6 + wpRfm * 2}%</Green>. Max{" "}
          <Green>5</Green> stacks. A CRIT hit removes all existing stacks.
        </>
      )
    }
  ]
};

