import { deepCopy, aHasB } from "../App";
import debuffsFrChar from "../data/characters/debuffsFrChar";
import weapons from "../data/weapons";
import debuffsFrArts from "../data/artifacts/debuffsFrArts";
import { baseRxnDmg, tfmRxns, ampRxnTrigElmts } from "../data/elements";
import { getXtraNaLv } from "../helpers";
import {
  addElmtPnes,
  getDefMult,
  getResMult,
  calcBase,
  isInfused
} from "./helpers";

function getRdMult(char, charDC, party, artsDC, elmtC, { Level, ...rest }) {
  const rdMult = deepCopy(rest);
  rdMult.DEF = 0;
  charDC.forEach((ctrl, i) => {
    const debuff = debuffsFrChar[char.name][i];
    if (debuff.isGranted(char) && ctrl.activated) {
      debuff.addPnes(rdMult, true, char, ctrl.inputs);
    }
  });
  party.forEach((tm) => {
    if (tm.name) {
      tm.DC.forEach((ctrl, i) => {
        const debuff = debuffsFrChar[tm.name][i];
        if (ctrl.activated) {
          debuff.addPnes(rdMult, false, null, ctrl.inputs);
        }
      });
    }
  });
  artsDC.forEach((ctrl) => {
    if (ctrl.activated) {
      const debuff = debuffsFrArts[ctrl.set][ctrl.index];
      debuff.addPnes(rdMult, ctrl.input);
    }
  });
  addElmtPnes(rdMult, elmtC);

  const { DEF, ...others } = rdMult;
  rdMult.DEF = getDefMult(Number(char.level.split("/")[0]), Level, rdMult.DEF);
  for (let RES in others) {
    rdMult[RES] = getResMult(rdMult[RES] / 100);
  }
  return rdMult;
}

function getBase(ATRs, piece, level, { xtraLv = 0, xtraMult = 0 }) {
  const lv = level + xtraLv;
  if (Array.isArray(piece.baseMult)) {
    return piece.baseMult.map((mult, i) =>
      calcBase(
        ATRs,
        { ...piece, baseMult: mult },
        lv,
        Array.isArray(xtraMult) ? xtraMult[i] : xtraMult
      )
    );
  }
  return calcBase(ATRs, piece, lv, xtraMult);
}

function getTotalDBn(tlBn, [atkType, elmtType], bonuses, infusions, ATRs) {
  let result = (tlBn.xtraDBn || 0) + (bonuses[atkType]?.DMG || 0);
  const inf = infusions[atkType];
  if (isInfused(atkType, elmtType, inf)) {
    result += ATRs[inf + " DMG Bonus"];
  } else {
    result += bonuses[elmtType]?.DMG || 0;
  }
  return 1 + (bonuses.AllDmg + result) / 100;
}

function getRxnMult(ampRxn, [atkType, elmtType], infusions, rxnBnes) {
  if (
    ampRxn &&
    (elmtType === "Elemental" ||
      aHasB(ampRxnTrigElmts[ampRxn], infusions[atkType]))
  ) {
    return rxnBnes[ampRxn];
  }
  return 1;
}

function getReduction(rdMult, [atkType, elmtType], vision, infusions) {
  const { DEF } = rdMult;
  const inf = infusions[atkType];
  if (elmtType === "Elemental") {
    return DEF * rdMult[vision + " RES"];
  } else if (elmtType === "Physical" && !aHasB(["NA", "CA", "PA"], atkType)) {
    return DEF * rdMult["Physical RES"];
  } else if (elmtType === "Various") {
    return DEF;
  }
  return DEF * rdMult[inf + " RES"];
}

function increaseCrit(result, bonus) {
  result.CR += bonus?.CR || 0;
  result.CD += bonus?.CD || 0;
}

function getCrit(
  { CR, CD },
  { xtraCR = 0, xtraCD = 0 },
  [atkType, elmtType],
  vision,
  infusions,
  bonuses
) {
  const result = { CR: CR + xtraCR, CD: CD + xtraCD };
  increaseCrit(result, bonuses[atkType]);
  const inf = infusions[atkType];
  if (isInfused(atkType, elmtType, inf) && inf === vision) {
    increaseCrit(result, bonuses.Elemental);
  } else {
    increaseCrit(result, bonuses[elmtType]);
  }
  result.CR = Math.min(result.CR, 1);
  return result;
}

export function getAtkDmg(
  char,
  charC,
  party,
  { vision, weapon, actvTalents },
  wp,
  artsDC,
  ATRs,
  bonuses,
  rxnBnes,
  infusions,
  elmtC,
  target
) {
  const CD = ATRs["CRIT DMG"] / 100;
  const CR = ATRs["CRIT Rate"] / 100;
  const rdMult = getRdMult(char, charC.DC, party, artsDC, elmtC, target);
  const { ampRxn } = elmtC;

  const finalResult = {};
  actvTalents.forEach((tl) => {
    const { type } = tl;
    let level = char[type];
    if (type === "Normal Attack") {
      level += getXtraNaLv(char, party);
    }
    finalResult[type] = {};

    tl.stats.forEach((piece) => {
      const { dmgTypes, getTlBn } = piece;
      const tlBn = getTlBn ? piece.getTlBn(char, charC.BC, ATRs) : {};
      const base = getBase(ATRs, piece, level, tlBn);
      let [totalDBn, rxnMult, reduction, crit] = [1, 1, 1, null];
      if (dmgTypes) {
        totalDBn = getTotalDBn(tlBn, dmgTypes, bonuses, infusions, ATRs);
        rxnMult = getRxnMult(ampRxn, dmgTypes, infusions, rxnBnes);
        reduction = getReduction(rdMult, dmgTypes, vision, infusions);
        crit = getCrit({ CR, CD }, tlBn, dmgTypes, vision, infusions, bonuses);
      }
      const isArr = Array.isArray(base);

      finalResult[type][piece.name] = {
        "Non-crit": isArr
          ? base.map(
              (b) => (b + (tlBn.xtraFlat || 0)) * totalDBn * rxnMult * reduction
            )
          : (base + (tlBn.xtraFlat || 0)) * totalDBn * rxnMult * reduction,
        get Crit() {
          return dmgTypes
            ? isArr
              ? this["Non-crit"].map((nc) => nc * (1 + crit.CD))
              : this["Non-crit"] * (1 + crit.CD)
            : 0;
        },
        get Average() {
          return dmgTypes
            ? isArr
              ? this["Non-crit"].map((nc) => nc * (1 + crit.CR * crit.CD))
              : this["Non-crit"] * (1 + crit.CR * crit.CD)
            : this["Non-crit"];
        }
      };
    });
  });

  finalResult["Reactions DMG"] = {};
  for (let rxn in tfmRxns) {
    const charLv = char.level.split("/")[0];
    let base =
      baseRxnDmg[charLv] * tfmRxns[rxn].mult * (1 + rxnBnes[rxn] / 100);
    const { dmgType } = tfmRxns[rxn];
    if (dmgType !== "Various") base *= rdMult[dmgType + " RES"];
    finalResult["Reactions DMG"][rxn] = {
      "Non-crit": base,
      Crit: 0,
      get Average() {
        return this["Non-crit"];
      }
    };
  }
  const wpData = weapons[weapon][wp.name];
  if (wpData.pasvProcD) {
    finalResult["Weapon Attack Proc"] = {};
    const wpAtkProc = finalResult["Weapon Attack Proc"];
    wpData.pasvProcD.forEach((dmg) => {
      const base =
        ATRs[dmg.baseSType] *
        dmg.multiplier[wp.refinement] *
        (1 + bonuses[dmg.dmgType].DMG / 100) *
        rdMult.DEF *
        rdMult["Physical RES"];
      wpAtkProc[dmg.name] = {
        "Non-crit": base,
        get Crit() {
          return this["Non-crit"] * (1 + CD);
        },
        get Average() {
          return base * (1 + (CR > 1 ? 1 : CR) * CD);
        }
      };
    });
  }
  return finalResult;
}
