import styled from "styled-components";
import { colorCodes } from "../../data/misc";

export const Tab = styled.div`
  flex-grow: 1;
  padding: calc(4 / 296 * 100%);
  width: 20%;
  border: 1px solid transparent;
  display: flex;
  &.chosen.fill {
    background-color: rgba(0, 0, 0, 0.5);
    border-color: #ffd700;
  }
  &.chosen.empty {
    background-color: rgba(0, 0, 0, 0.5);
    border-color: gray;
  }
  &.idle.empty {
    background-color: rgba(255, 255, 255, 0.3);
  }
`;

export const ArrowBtn = styled(({ direction, ...other }) => (
  <button {...other}>
    <i className={"fas fa-arrow-circle-" + direction}></i>
  </button>
))`
  margin-left: 1rem;
  font-size: 1.75rem;
  display: flex;
  border: none;
  color: ${colorCodes.orange};
  background-color: transparent;
`;

export const MaxBtn = styled((props) => <button {...props} />)`
  margin-left: 1rem;
  padding: 0.25rem 0.375rem;
  border: none;
  border-radius: 0.25rem;
  background-color: ${colorCodes.orange};
  font-size: 1rem;
  font-weight: 700;
`;

export const SSVal = styled.input`
  margin-left: 3px;
  padding: 3px 0;
  width: 2.5rem;
  border: none;
  background-color: transparent;
  color: white;
  font-size: 1.0625rem;
  text-align: right;
  outline: none;
`;
