import { Green, LGreen, Red } from "../../../styledCpns/DataDisplay";
import { mapToLevels } from "../../misc";
import { mediumPAs, multMult } from "../config";
import characters from "../index";

const MusouIsshinBnMult = [
  0,
  0.73,
  0.78,
  0.84,
  0.91,
  0.96,
  1.02,
  1.09,
  1.16,
  1.23,
  1.31,
  1.38,
  1.45,
  1.54
];

const countResolve = (energyCost, level) =>
  Math.round(energyCost * Math.min(Math.ceil(14.5 + level * 0.5), 20)) / 100;

export const RaidenBuffs = [
  {
    src: "Elemental Skill",
    desc: (toSelf, char, { inputs }) => (
      <>
        Eye of Stormy Judgment increases <LGreen>Elemental Burst DMG</LGreen>{" "}
        based on the Energy Cost of the Elemental Burst during the Eye's
        duration.{" "}
        <Red>
          Elemental Burst DMG: {RaidenBuffs[0].bnValue(toSelf, char, inputs)}%.
        </Red>
      </>
    ),
    isGranted: () => true,
    affect: "party",
    labels: ["Elemental Skill Level"],
    inputs: [1],
    inputTypes: ["text"],
    maxs: [13],
    addBnes: (_, bonuses, __, inputs, toSelf, char) => {
      bonuses.EB.DMG += RaidenBuffs[0].bnValue(toSelf, char, inputs);
    },
    bnValue: (toSelf, char, inputs) => {
      const level = toSelf
        ? char["Elemental Skill"] + (char.constellation >= 5 ? 3 : 0)
        : inputs[0];
      for (let tl of characters[char.name].actvTalents) {
        if (tl.type === "Elemental Burst") {
          const result = tl.energyCost * Math.min(0.21 + level / 100, 0.3);
          return Math.round(result * 10) / 10;
        }
      }
    }
  },
  {
    src: "Elemental Burst",
    desc: (_, char, { ATRs, charBC }) => {
      const result = RaidenBuffs[1].bnValue(char, charBC);
      return (
        <>
           When nearby party members (excluding the Raiden Shogun herself) use
          their Elemental Bursts, the Raiden Shogun will build up Resolve stacks
          based on the Energy Cost of these Elemental Bursts.
          <br /> The <LGreen>DMG</LGreen> dealt by{" "}
          <LGreen>Musou no Hitotachi</LGreen> and{" "}
          <LGreen>Musou Isshin's attacks</LGreen> will be increased based on the{" "}
          <LGreen>number of Chakra Desiderata's Resolve stacks</LGreen> (up to
          60) consumed when this skill is used.{" "}
          <Red>
            Total Resolve: {result[0]}. Musou no Hitotachi DMG Bonus:{" "}
            {Math.round((ATRs.ATK * result[1]) / 100)} (base). Musou Isshin's
            attacks DMG Bonus: {Math.round((ATRs.ATK * result[2]) / 100)}{" "}
            (base).
          </Red>
        </>
      );
    },
    isGranted: () => true,
    affect: "self",
    alterLabel: "Total Energy Cost",
    maxStacks: 240,
    bnValue: (char, charBC) => {
      const lv = char["Elemental Burst"] + (char.constellation >= 3 ? 3 : 0);
      let stacks = countResolve(charBC[1].value, lv);
      if (charBC[3].activated) stacks += RaidenBuffs[3].bnValue(char, charBC);
      stacks = Math.min(Math.round(stacks * 100) / 100, 60);
      return [
        stacks,
        3.89 * multMult[2][lv] * stacks,
        MusouIsshinBnMult[lv] * stacks
      ];
    },
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: false
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: (_, __, { ATRs }) => (
      <>
        Each <Green>1%</Green> above 100% <LGreen>Energy Recharge</LGreen> that
        the Raiden Shogun possesses grants her <Green>0.4%</Green>{" "}
        <LGreen>Electro DMG Bonus</LGreen>.{" "}
        <Red>Electro DMG Bonus: {RaidenBuffs[2].bnValue(ATRs)}%.</Red>
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addBnes: (ATRs) => {
      ATRs["Electro DMG Bonus"] += RaidenBuffs[2].bnValue(ATRs);
    },
    bnValue: (ATRs) => Math.round((ATRs["Energy Recharge"] - 100) * 4) / 10
  },
  {
    src: "Constellation 1",
    desc: (_, char, { charBC }) => (
      <>
        When Electro characters use their Elemental Bursts, the{" "}
        <LGreen>Resolve</LGreen> gained is increased by <Green>80%</Green>. When
        characters of other Elemental Types use their Elemental Bursts, the{" "}
        <LGreen>Resolve</LGreen> gained is increased by <Green>20%</Green>.{" "}
        <Red>Extra Resolve: {RaidenBuffs[3].bnValue(char, charBC)}.</Red>
      </>
    ),
    isGranted: (char) => char.constellation >= 1,
    affect: "self",
    alterLabel: "Energy Cost from Electro Characters (part of total)",
    maxStacks: 240,
    bnValue: (char, [_, EBctrl, __, C1ctrl]) => {
      const otherEC = EBctrl.value - C1ctrl.value;
      if (otherEC < 0) return 0;
      const lv = char["Elemental Burst"] + (char.constellation >= 3 ? 3 : 0);
      return (
        Math.round(
          countResolve(C1ctrl.value, lv) * 8 + countResolve(otherEC, lv) * 2
        ) / 10
      );
    }
  },
  {
    src: "Constellation 4",
    desc: () => (
      <>
        When the Musou Isshin state expires, all nearby party members gain{" "}
        <Green>30%</Green> <LGreen>bonus ATK</LGreen> for 10s.
      </>
    ),
    isGranted: (char) => char.constellation >= 4,
    affect: "teammates",
    addBnes: (ATRs) => {
      ATRs["ATK%"] += 30;
    }
  }
];

