import React, { useState } from "react";
import { ModalCarpet, ModalInner, BtmBar } from "../Modal/styles";
import { Select, Option } from "../../styledCpns/Inputs";
import { LoadBtn } from "./styles";
import Teapot from "./Teapot";
import Inventory, { cmpLv } from "./Inventory";
import LoadCtrl from "./LoadCtrl";
import characters from "../../data/characters";

export default function Possessions({ poss, teapot, inv }) {
  const [loadCtrl, setLoadCtrl] = useState(null);
  const [sortCate, setSortCate] = useState("Sort by");
  const cates = ["Sort by", "Name", "Level", "Rarity"];
  teapot = teapot.sort((a, b) => {
    return {
      Name: a.name.localeCompare(b.name),
      Level: cmpLv(a, b),
      Rarity: characters[a.name].rarity.localeCompare(characters[b.name].rarity)
    }[sortCate];
  });
  return (
    <ModalCarpet className="not-center-y">
      <ModalInner>
        {
          {
            Teapot: <Teapot teapot={teapot} />,
            Inventory: (
              <Inventory inv={inv} teapot={teapot} setLoadCtrl={setLoadCtrl} />
            )
          }[poss]
        }
        {poss === "Teapot" && (
          <BtmBar className="px-4 flex justify-between">
            <LoadBtn act="down" setLoadCtrl={setLoadCtrl} />
            <Select
              className="px-2"
              borderRadius="0.25rem"
              bgColor="white"
              fontSize="1.125rem"
              value={sortCate}
              onChange={(e) => setSortCate(e.target.value)}
            >
              {cates.map((ft, i) => (
                <Option key={i} disabled={!i}>
                  {ft}
                </Option>
              ))}
            </Select>
            <LoadBtn act="up" setLoadCtrl={setLoadCtrl} />
          </BtmBar>
        )}
      </ModalInner>
      {loadCtrl && (
        <LoadCtrl
          poss={poss}
          teapot={teapot}
          inv={inv}
          loadCtrl={loadCtrl}
          setLoadCtrl={setLoadCtrl}
        />
      )}
    </ModalCarpet>
  );
}
