import styled from "styled-components";
import React, { useContext } from "react";
import {
  FixedContext,
  UIContext,
  WpContext,
  ResultContext,
  deepCopy
} from "../../App";
import { Box, Typography } from "@material-ui/core";
import { Select, Option } from "../../styledCpns/Inputs";
import { ColoredText, Img, Ul } from "../../styledCpns/DataDisplay";
import { levels, pctOf, wikia, colorCodes } from "../../data/misc";
import { getWpData } from "../../helpers";

const Group = styled.div`
  background-color: ${colorCodes.cardBg2};
  padding-right: 0.5rem;
  padding-left: 0.5rem;
`;

export default function Weapons() {
  const { setWpsRack, setUI } = useContext(FixedContext);
  const { wpsRack } = useContext(WpContext);
  const { curSt } = useContext(UIContext).ui;
  return (
    <WeaponDetails
      wp={wpsRack[curSt]}
      openModal={() =>
        setUI((prev) => ({
          ...prev,
          modalName: "Weapons",
          modalType: "equip"
        }))
      }
      upgrade={(e) =>
        setWpsRack((prev) => {
          const newRack = deepCopy(prev);
          newRack[curSt].level = e.target.value;
          return newRack;
        })
      }
      refine={(e) =>
        setWpsRack((prev) => {
          const newRack = deepCopy(prev);
          newRack[curSt].refinement = Number(e.target.value);
          return newRack;
        })
      }
      showMore={true}
    />
  );
}

export function WeaponDetails({ wp, openModal, upgrade, refine, showMore }) {
  const { ATRs } = useContext(ResultContext);
  const wpData = getWpData(wp.name);
  const HiliText = (props) => <ColoredText color={wpData.rarity} {...props} />;

  const LeftSec = (
    <div className="grow-1 flex-col justify-between">
      <Group className="grow-1 flex-wrap align-center">
        <Typography variant="h6" className="mr-1">
          <strong>Level</strong>
        </Typography>
        <Select
          color={wpData.rarity}
          fontSize="1.125rem"
          fontWeight={700}
          align="right"
          value={wp.level}
          onChange={upgrade}
        >
          {levels.map((level) => (
            <Option key={level}>{level}</Option>
          ))}
        </Select>
      </Group>
      {wpData.subStats.map((stat) => (
        <Group
          key={stat.type}
          className="grow-1 mt-1 pt-1 flex-col justify-center"
        >
          <Typography variant="subtitle1">{stat.type}</Typography>
          <HiliText variant="h3">
            {stat.value[wp.level] + pctOf(stat.type)}
          </HiliText>
        </Group>
      ))}
      <Group className="grow-1 mt-1 pt-1 flex-col justify-center">
        <Typography variant="subtitle1">Base ATK</Typography>
        <HiliText variant="h2">{wpData.mainStat.value[wp.level]}</HiliText>
      </Group>
    </div>
  );

  const RightSec = (
    <div className="ml-2 flex-col align-center">
      <Box
        p="2px"
        border={5}
        borderRadius="8px"
        borderColor={colorCodes[wpData.rarity]}
        bgcolor="#e5f1ff"
        className="pointer"
        onClick={openModal}
      >
        <Img src={wikia + wpData.icon + ".png"} alt="" width="6.25rem" />
      </Box>
      <Group className="mt-2 py-1 flex-col align-center">
        <Typography variant="subtitle1" align="center">
          Refinement Rank
        </Typography>
        <Select
          color={wpData.rarity}
          fontSize="1.125rem"
          fontWeight={700}
          value={wp.refinement}
          onChange={refine}
        >
          {[1, 2, 3, 4, 5].map((level) => (
            <Option key={level}>{level}</Option>
          ))}
        </Select>
      </Group>
    </div>
  );
  return (
    <div className="mt-1">
      <HiliText variant="h2" color={wpData.rarity}>
        {wp.name}
      </HiliText>
      <div className="mt-2 flex">
        {LeftSec}
        {RightSec}
      </div>
      <div className="mt-2">
        <ColoredText variant="h6" color="orange">
          <strong>{wpData.pasvName}</strong>
        </ColoredText>
        <Ul className="pl-0" position="inside">
          {wpData
            .pasvDescs(wp.refinement, { ATRs, showMore })
            .map((desc, i) => (
              <Typography key={i} component="li" variant="body1">
                {desc.bonus} {desc.buff} {desc.others}
              </Typography>
            ))}
        </Ul>
      </div>
    </div>
  );
}
