import { deepCopy } from "../../App";
import {
  initCharCs,
  initWp,
  initArts,
  initParty,
  initElmtC,
  initTarget
} from "../../data/initConfig";
import { getWpRange } from "../../helpers";

export function removeTm(party, tmI, { vision, weapon }, setTm) {
  party[tmI] = {
    name: "",
    BC: [],
    DC: []
  };
  setTm(party, vision, getWpRange(party), false, weapon);
}

export function applySettings(charName, tempSts, curSt, shownSt, CBs) {
  const names = [],
    checked = [],
    oldIs = [],
    charCs = [],
    wps = [],
    subWps = [],
    arts = [],
    parties = [],
    elmtCs = [],
    targets = [];
  let cur = curSt;
  let shown = shownSt;
  tempSts.forEach((st, i) => {
    if (i < cur) curSt--;
    if (i < shown) shownSt--;
    names.push(st.name);
    if (st.checked) checked.push(i);
    if (st.isOld) {
      oldIs.push(st.index);
    } else {
      charCs.push(initCharCs(charName));
      wps.push(initWp({ name: st.wp }));
      subWps.push({});
      arts.push(initArts());
      parties.push(initParty());
      elmtCs.push(initElmtC());
      targets.push(initTarget());
    }
  });
  CBs.setCharCs((prev) => oldIs.map((i) => prev[i]).concat(charCs));
  CBs.setWpsRack((prev) => oldIs.map((i) => prev[i]).concat(wps));
  CBs.setSubWpBCs((prev) => oldIs.map((i) => prev[i]).concat(subWps));
  CBs.setArtsChest((prev) => oldIs.map((i) => prev[i]).concat(arts));
  CBs.setParties((prev) => oldIs.map((i) => prev[i]).concat(parties));
  CBs.setElmtCs((prev) => oldIs.map((i) => prev[i]).concat(elmtCs));
  CBs.setUI((prev) => {
    const newUI = deepCopy(prev);
    newUI.stNames = names;
    newUI.cmpSts = checked;
    newUI.curSt = curSt;
    newUI.shownSt = shownSt;
    return newUI;
  });
}
