import styled from "styled-components";
import React, { useContext, useState } from "react";
import { FixedContext } from "../../App";
import { Typography } from "@material-ui/core";
import { OptionsWrapper } from "./LoadCtrl";
import { colorCodes } from "../../data/misc";

const ChooseBtn = styled.button`
  padding: 0.25rem 0.75rem;
  border: 0.125rem solid black;
  border-radius: 0.5rem;
  background-color: ${colorCodes.lightGold};
  font-size: 1rem;
`;

function loadLS(setTeapot, setInv, setLoadCtrl, setErr) {
  let data = localStorage.getItem("GDC_Data");
  if (data) {
    data = JSON.parse(data);
    setTeapot(data.Teapot);
    setInv(data.Inventory);
    setLoadCtrl(null);
  } else {
    setErr(true);
  }
}

function loadFile(setTeapot, setInv, setLoadCtrl) {
  const file = document.getElementById("choose-btn").files[0];
  const reader = new FileReader();
  if (file?.type.match(/text.*/)) {
    reader.onload = function (event) {
      const data = JSON.parse(event.target.result);
      setTeapot(data.Teapot);
      setInv(data.Inventory);
      setLoadCtrl(null);
    };
    reader.readAsText(file);
  }
}

export default function Upload({ setLoadCtrl }) {
  const { setTeapot, setInv } = useContext(FixedContext);
  const [err, setErr] = useState(false);
  return (
    <OptionsWrapper>
      {
        <>
          <div
            className="option p-3"
            onClick={() => loadLS(setTeapot, setInv, setLoadCtrl, setErr)}
          >
            <Typography variant="h6" align="center">
              <b>Upload from Local Storage</b>
            </Typography>
            {err && (
              <Typography className="mt-1" align="center">
                <i>There's no data in your Local Storage. Save it first.</i>
              </Typography>
            )}
          </div>
          <div className="option px-3 py-2 large flex-col align-center">
            <Typography variant="h6">
              <b>Upload a .txt file</b>
            </Typography>
            <input
              id="choose-btn"
              style={{ display: "none" }}
              type="file"
              accept="text/*"
              onChange={() => loadFile(setTeapot, setInv, setLoadCtrl)}
            />
            <ChooseBtn
              className="mt-1"
              onClick={() => {
                document.getElementById("choose-btn").click();
              }}
            >
              Choose File
            </ChooseBtn>
          </div>
        </>
      }
    </OptionsWrapper>
  );
}
