import React, { useState } from "react";
import { Typography } from "@material-ui/core";
import { BtnOnCard } from "../../styledCpns/Inputs";
import { Ul } from "../../styledCpns/DataDisplay";
import { OptionsWrapper } from "./LoadCtrl";

function save(data, setLoadCtrl) {
  localStorage.setItem("GDC_Data", data);
  setLoadCtrl(null);
}

function trySave(data, setLoadCtrl, setWarning) {
  if (localStorage.getItem("GDC_Data")) {
    setWarning(true);
  } else {
    save(data, setLoadCtrl);
  }
}

function download(data, setLoadCtrl) {
  const textBlob = new Blob([data], { type: "text/plain" });
  let newLink = document.createElement("a");
  newLink.download = "GDC_Data";
  if (window.webkitURL != null) {
    newLink.href = window.webkitURL.createObjectURL(textBlob);
  } else {
    newLink.href = window.URL.createObjectURL(textBlob);
    newLink.style.display = "none";
    document.body.appendChild(newLink);
  }
  newLink.click();
  setLoadCtrl(null);
}

export default function Download({ teapot, inv, setLoadCtrl }) {
  const data = JSON.stringify({ Teapot: teapot, Inventory: inv });
  const [warning, setWarning] = useState(false);
  return (
    <OptionsWrapper>
      {warning ? (
        <div className="pt-3 px-3 flex-col">
          <Typography variant="h6">
            Do you want to replace your data in Local Storage?
          </Typography>
          <BtnOnCard
            className="mx-auto mt-2 pb-1"
            bgColor="lightGold"
            onClick={() => save(data, setLoadCtrl)}
          >
            Proceed
          </BtnOnCard>
        </div>
      ) : (
        <>
          <div
            className="option px-3 py-1"
            onClick={() => trySave(data, setLoadCtrl, setWarning)}
          >
            <Typography variant="h6">
              <b>Save to Local Storage</b>
            </Typography>
            <Ul position="inside">
              <Typography component="li">
                Your data stays in this Browser only and will be lost if you
                clear it.
              </Typography>
            </Ul>
          </div>
          <div
            className="option px-3 py-1"
            onClick={() => download(data, setLoadCtrl)}
          >
            <Typography variant="h6">
              <b>Download a .txt file</b>
            </Typography>
            <Ul position="inside">
              <Typography component="li">
                Please DO NOT modify this file if you don't understand how it
                works.
              </Typography>
            </Ul>
          </div>
        </>
      )}
    </OptionsWrapper>
  );
}
