import { Green, LGreen } from "../../../styledCpns/DataDisplay";
import { mapToLevels } from "../../misc";
import { heavyPAs, multMult } from "../config";

export const NoelleBuffs = [
  {
    src: "Elemental Burst",
    desc: () => (
      <>
        Converts <LGreen>attack DMG to Geo DMG</LGreen> that cannot be
        overridden by any other elemental infusion. Increased{" "}
        <LGreen>ATK</LGreen> that scales based on her <LGreen>DEF</LGreen>.
      </>
    ),
    isGranted: () => true,
    affect: "self",
    final: true,
    addBnes: (ATRs, _, __, ___, ____, char, charBC) => {
      const level = char["Elemental Burst"] + (char.constellation >= 5 ? 3 : 0);
      let mult = 40 * multMult[2][level];
      if (NoelleBuffs[2].isGranted(char) && charBC[2].activated) mult += 50;
      ATRs.ATK += Math.round((ATRs.DEF * mult) / 100);
    },
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: false
  },
  {
    src: "Constellation 2",
    desc: () => (
      <>
        Increases her <LGreen>Charged Attack DMG</LGreen> by <Green>15%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addBnes: (_, bonuses) => {
      bonuses.CA.DMG += 15;
    }
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        <LGreen>Sweeping Time</LGreen> increases Noelle's <LGreen>ATK</LGreen>{" "}
        by an additional <Green>50%</Green> of her <LGreen>DEF</LGreen>.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "self"
  }
];

