export const KleeBuffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        Explosive Spark is consumed by the next <LGreen>Charged Attacks</LGreen>
        , which costs no Stamina and deals <Green>50%</Green> increased{" "}
        <LGreen>DMG</LGreen>.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "self",
    addBnes: (_, bonuses) => {
      bonuses.CA.DMG += 50;
    }
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        When Sparks 'n' Splash is used, all party members will gain a{" "}
        <Green>10%</Green> <LGreen>Pyro DMG Bonus</LGreen> for 25s.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "party",
    addBnes: (ATRs) => {
      ATRs["Pyro DMG Bonus"] += 10;
    }
  }
];

