import { aHasB } from "../../../App";
import { Green, LGreen } from "../../../styledCpns/DataDisplay";
import { mapToLevels } from "../../misc";
import { heavyPAs } from "../config";
import characters from "../index";

export const ChongyunBuffs = [
  {
    src: "Elemental Skill",
    desc: () => (
      <>
        Within Chonghua Frost Field, all Sword, Claymore and Polearm-wielding
        characters' weapons will be <LGreen>infused with Cryo</LGreen>.
      </>
    ),
    isGranted: () => true,
    affect: "party",
    canInfuse: ({ weapon }) => aHasB(["sword", "claymore", "polearm"], weapon),
    infuseElmt: "Cryo",
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: true
  },
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        Sword, Claymore, or Polearm-wielding characters within the field created
        by Spirit Blade: Chonghua's Layered Frost have their{" "}
        <LGreen>Normal ATK SPD</LGreen> increased by <Green>8%</Green>.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "party",
    addBnes: (ATRs, _, __, ___, ____, char) => {
      const { weapon } = characters[char.name];
      if (aHasB(["sword", "claymore", "polearm"], weapon))
        ATRs["Normal ATK SPD"] += 8;
    }
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        <LGreen>Spirit Blade: Cloud-Parting Star</LGreen> deals{" "}
        <Green>15%</Green> <LGreen>more DMG</LGreen> to opponents with a lower
        percentage of their Max HP remaining than Chongyun.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    addBnes: (_, bonuses) => {
      bonuses.EB.DMG += 15;
    }
  }
];

