import styled from "styled-components";
import React from "react";
import { CloseBtn } from "../../styledCpns/Inputs";
import { ModalCarpet } from "../Modal/styles";
import Download from "./Download";
import Upload from "./Upload";
import { colorCodes } from "../../data/misc";

const CrtlInner = styled.div`
  width: 80%;
  max-height: 80vh;
  background-color: #ebebeb;
  border-radius: 0.75rem;
  @media (min-width: 569px) {
    width: auto;
    max-width: 484px;
  }
`;

export default function LoadCtrl(props) {
  const { poss, teapot, inv, loadCtrl, setLoadCtrl } = props;
  return (
    <ModalCarpet>
      <CrtlInner className="pt-5 pb-3 relative">
        <CloseBtn
          color="white"
          bgColor="red"
          onClick={() => setLoadCtrl(null)}
        />
        {
          {
            down: (
              <Download
                poss={poss}
                teapot={teapot}
                inv={inv}
                setLoadCtrl={setLoadCtrl}
              />
            ),
            up: <Upload poss={poss} setLoadCtrl={setLoadCtrl} />
          }[loadCtrl]
        }
      </CrtlInner>
    </ModalCarpet>
  );
}

export const OptionsWrapper = styled.div`
  color: black;
  h6,
  p,
  li {
    cursor: default;
  }
  .option:last-child {
    border-top: 1px solid black;
  }
  .option:hover {
    background-color: ${colorCodes.green};
  }
`;
