import React, { useState, useContext } from "react";
import { FixedContext } from "../../App";
import { Typography } from "@material-ui/core";
import { Img } from "../../styledCpns/DataDisplay";
import { IconBtn, CloseBtn } from "../../styledCpns/Inputs";
import { ModalCarpet } from "../Modal/styles";
import {
  StyledOverview,
  PossGridItem,
  StyledDetails,
  StyledChoices
} from "./styles";
import Forge from "./Forge";
import weapons from "../../data/weapons";
import artifacts from "../../data/artifacts";
import { wikia, wpIcons, artsIcons } from "../../data/misc";

export default function Items({ data, isWp }) {
  const [chosenI, setChosenI] = useState(0);
  const [choosing, setChoosing] = useState(false);
  return (
    <>
      <StyledOverview className="pl-2 flex-wrap grow-1 overflow-auto hide-sb">
        <PossGridItem>
          <div className="add_btn cmn-shadow item pointer full-h">
            <IconBtn
              icon="fas fa-plus"
              color="rgba(255, 255, 255, 0.8)"
              bgColor="transparent"
              size="100%"
              fontSize="2rem"
              className="glow-on-hover"
              onClick={() => setChoosing(true)}
            />
          </div>
        </PossGridItem>
        {data.map((item, i) => {
          let { icon, rarity } = isWp
            ? weapons[item.type][item.name]
            : artifacts[item.set][item.type];
          if (!isWp) rarity = "gold";
          return (
            <PossGridItem key={i}>
              <div
                className={
                  "item pointer relative hover-effect " +
                  (item.index === chosenI ? "glowing" : "cmn-shadow")
                }
                onClick={() => setChosenI(item.index)}
              >
                {isWp && (
                  <p className={"rfm" + (item.refinement === 5 ? " max" : "")}>
                    {item.refinement}
                  </p>
                )}
                <div className={"img_wrapper " + rarity}>
                  <Img src={wikia + icon + ".png"} alt="" width="100%" />
                </div>
                <p className="level">{(isWp ? "Lv. " : "+") + item.level}</p>
              </div>
            </PossGridItem>
          );
        })}
      </StyledOverview>
      <StyledDetails forChar={false}>
        <div className={"inner" + (isWp ? " px-3" : "")}>
          <ItemsForge
            isWp={isWp}
            data={data}
            chosenI={chosenI}
            setChosenI={setChosenI}
          />
        </div>
      </StyledDetails>
      {choosing && <Choices isWp={isWp} setChoosing={setChoosing} />}
    </>
  );
}

function ItemsForge({ isWp, data, chosenI, setChosenI }) {
  let info;
  for (let item of data) {
    if (item.index === chosenI) {
      info = item;
      break;
    }
  }
  return data.length && info ? (
    <Forge
      forItems={true}
      isWp={isWp}
      info={info}
      index={chosenI}
      type={info.type}
      setPosition={() => setChosenI(-1)}
    />
  ) : null;
}

function Choices({ isWp, setChoosing }) {
  const { setUI } = useContext(FixedContext);
  return (
    <ModalCarpet>
      <StyledChoices className="p-2 relative">
        <CloseBtn
          color="white"
          bgColor="red"
          onClick={() => setChoosing(false)}
        />
        <Typography variant="h3" align="center" color="textSecondary">
          Choose a type
        </Typography>
        <div className="mt-3 mb-2 flex">
          {Object.entries(isWp ? wpIcons : artsIcons).map((pair, i) => (
            <img
              key={i}
              className="big-icon pointer"
              src={wikia + pair[1] + ".png"}
              alt={pair[0]}
              onClick={() => {
                setUI((prev) => ({
                  ...prev,
                  modalName: isWp ? "Weapons" : pair[0],
                  modalType: "create" + (isWp ? `/${pair[0]}` : "")
                }));
                setChoosing(false);
              }}
            />
          ))}
        </div>
      </StyledChoices>
    </ModalCarpet>
  );
}
