import React from "react";
import { Img } from "../../styledCpns/DataDisplay";
import { Tab } from "./styles";
import ArtPiece from "./ArtPiece";
import ArtSlot from "./ArtSlot";
import artifacts from "../../data/artifacts";
import { artsIcons, wikia } from "../../data/misc";

export default function ArtsDetails(props) {
  const { arts, tab, setTab, modalType, artsIndex } = props;
  return (
    <div className="mt-1">
      <div className="flex">
        {Object.keys(arts).map((type) => (
          <Tab
            key={type}
            className={
              "pointer " +
              (type === tab ? " chosen" : "idle") +
              (arts[type].set ? " fill" : " empty")
            }
            onClick={() => setTab(type === tab ? null : type)}
          >
            {arts[type].set ? (
              <Img
                key={type}
                src={wikia + artifacts[arts[type].set][type].icon + ".png"}
                alt=""
                width="100%"
              />
            ) : (
              <Img
                key={type}
                src={wikia + artsIcons[type] + ".png"}
                alt=""
                width="100%"
              />
            )}
          </Tab>
        ))}
      </div>
      {tab &&
        (arts[tab].set ? (
          <ArtPiece
            pieceInfo={arts[tab]}
            index={artsIndex}
            artType={tab}
            modalType={modalType}
          />
        ) : (
          <ArtSlot artType={tab} modalType={modalType} />
        ))}
    </div>
  );
}
