import { Green, LGreen, Red } from "../../../styledCpns/DataDisplay";
import { TravelerInfo, TravelerNCPAs } from "../config";

export const ElectroMCBuffs = [
  {
    src: "Elemental Skill",
    desc: (toSelf, _, { inputs, ATRs, charBC }) => {
      const { bnValue } = ElectroMCBuffs[0];
      let result;
      if (toSelf) {
        result = bnValue(charBC[1].activated, ATRs["Energy Recharge"]);
        if (charBC[0].activated && charBC[1].activated) {
          result = Math.round(result * 9.1) / 10;
        }
      } else {
        bnValue(...inputs);
      }
      return (
        <>
          Increases <Green>20%</Green> <LGreen>Energy Recharge</LGreen> during
          the Abundance Amulet's duration. <Red>Total bonus: {result}%</Red>.
        </>
      );
    },
    isGranted: () => true,
    affect: "party",
    labels: ["A4 Passive Talent", "Energy Recharge"],
    inputs: [false, 100],
    inputTypes: ["check", "text"],
    maxs: [null, 999],
    addBnes: (ATRs, _, __, inputs, toSelf, ___, charBC) => {
      const { bnValue } = ElectroMCBuffs[0];
      ATRs["Energy Recharge"] += toSelf
        ? bnValue(charBC[1].activated, ATRs["Energy Recharge"])
        : bnValue(...inputs);
    },
    bnValue: (boosted, ER) => 20 + (boosted ? Math.round(ER) / 10 : 0)
  },
  {
    src: "A4 Passive Talent",
    desc: () => (
      <>
        Increases the <LGreen>Energy Recharge</LGreen> effect granted by
        Lightning Blade's Abundance Amulet by <Green>10%</Green> of the
        Traveler's <LGreen>Energy Recharge</LGreen>.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self"
  }
];

