import { aHasB } from "../App";
import { multMult } from "../data/characters/config";

export const isValid = ({ final, activated }, cond) =>
  Boolean(final) === cond && activated;

export function addElmtPnes(rdMult, { rsns, superconduct }) {
  for (let rsn of rsns) {
    if (rsn.name === "Enduring Rock" && rsn.activated) {
      rdMult["Geo RES"] -= 20;
      break;
    }
  }
  if (superconduct) rdMult["Physical RES"] -= 40;
}

export function getDefMult(charLv, targetLv, defRd) {
  return (
    Math.round(
      ((charLv + 100) / ((1 - defRd / 100) * (targetLv + charLv + 200))) * 1000
    ) / 1000
  );
}

export function getResMult(RES) {
  return RES < 0 ? 1 - RES / 2 : RES >= 0.75 ? 1 / (4 * RES + 1) : 1 - RES;
}

export function calcBase(
  ATRs,
  { baseSType, baseMult, multType, baseFlat, flatType },
  level,
  xtraMult
) {
  let result =
    ATRs[baseSType || "ATK"] *
    ((baseMult * multMult[multType][level] + xtraMult) / 100);
  if (baseFlat) {
    result += baseFlat * multMult[flatType][level];
  }
  return result;
}

export function isInfused(atkType, elmtType, inf) {
  return (
    aHasB(["NA", "CA", "PA"], atkType) &&
    elmtType === "Physical" &&
    inf !== "Physical"
  );
}
