import styled from "styled-components";
import { colorCodes } from "../../data/misc";

export const Pedestal = styled.div`
  margin-bottom: 0.5rem;
  padding: 0.75rem 0;
  border: 2px solid white;
  border-radius: 1rem;
  background-color: ${colorCodes.cardBg1};
`;

export const InputText = styled.input`
  max-width: calc(80% - 0.8rem);
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
  font-size: 1rem;
  outline: none;
`;

export const CheckBox = styled.input`
  position: absolute;
  right: 1.25rem;
  transform: scale(2);
`;

export const AddBtn = styled((props) => (
  <button {...props}>
    <i className="fas fa-plus"></i>
  </button>
))`
  margin-top: 1.5rem;
  padding: 0.5rem 0 0.375rem;
  border: none;
  border-radius: 1rem;
  background-color: ${colorCodes.orange};
  .fas {
    color: black;
    font-size: 1rem;
  }
`;
