import { Green, LGreen } from "../../../styledCpns/DataDisplay";
import { mapToLevels } from "../../misc";
import { lightPAs } from "../config";

export const YanfeiBuffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        When Yanfei's Charged Attack consumes Scarlet Seals, each Scarlet Seal
        will increase her <LGreen>Pyro DMG Bonus</LGreen> by <Green>5%</Green>.
        This effect lasts for 6s.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "self",
    maxStacks: 4,
    addBnes: (ATRs, _, __, stacks) => {
      ATRs["Pyro DMG Bonus"] += 5 * stacks;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        When Yanfei's Charged Attack deals a CRIT Hit to opponents, she will
        deal an additional instance of <LGreen>AoE Pyro DMG</LGreen> equal to{" "}
        <Green>80%</Green> of her <LGreen>ATK</LGreen>. This DMG counts as
        Charged Attack DMG.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self"
  },
  {
    src: "Constellation 2",
    desc: () => (
      <>
        Increases Yanfei's <LGreen>Charged Attack CRIT Rate</LGreen> by{" "}
        <Green>20%</Green> against enemies below 50% HP.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addBnes: (_, bonuses) => {
      bonuses.CA.CR += 0.2;
    }
  }
];

