import React, { useContext, useState } from "react";
import { FixedContext, UIContext, ArtsContext, deepCopy } from "../../App";
import { Box, Typography } from "@material-ui/core";
import {
  MainSelect,
  BtnOnCard,
  IconBtn,
  CloseBtn
} from "../../styledCpns/Inputs";
import { Card, LimitedBox } from "../../styledCpns/Layout";
import { Img } from "../../styledCpns/DataDisplay";
import { ModalCarpet } from "../Modal/styles";
import PartyCard from "./PartyCard";
import WeaponCard from "./WeaponCard";
import ArtifactsCard from "./ArtifactsCard";
import Settings from "./Settings";
import { colorCodes, wikia } from "../../data/misc";

export default function Setups(props) {
  const { setUI, setPoss } = useContext(FixedContext);
  const { curSt, shownSt, stNames } = useContext(UIContext).ui;
  const { pieces } = useContext(ArtsContext).artsChest[shownSt];
  const [settingsOn, setSettingsOn] = useState(false);
  const [typing, setTyping] = useState(false);
  let hasAny = false;
  for (let type in pieces) {
    if (pieces[type].set) {
      hasAny = true;
      break;
    }
  }
  const changecurSt = () => {
    setUI((prev) => {
      const newUI = deepCopy(prev);
      newUI.curSt = shownSt;
      if (prev.cmpSts.length === 1) {
        newUI.cmpSts[0] = shownSt;
      } else {
        const pos = prev.cmpSts.indexOf(shownSt);
        if (pos === -1) {
          newUI.cmpSts.unshift(shownSt);
        } else {
          [newUI.cmpSts[0], newUI.cmpSts[pos]] = [
            newUI.cmpSts[pos],
            newUI.cmpSts[0]
          ];
        }
      }
      return newUI;
    });
  };
  return (
    <Card className="px-4 pt-4 pb-3 relative" bgColor="cardBg2">
      {settingsOn ? (
        <Settings setSettingsOn={setSettingsOn} />
      ) : (
        <>
          <div className="mb-3 flex-col relative">
            <Box height="1.875rem" className="flex align-center">
              {shownSt === curSt ? (
                <Typography variant="h6">
                  <i>Currently in use</i>
                </Typography>
              ) : (
                <BtnOnCard
                  bgColor="lightGold"
                  className="mx-auto"
                  onClick={changecurSt}
                >
                  USE
                </BtnOnCard>
              )}
            </Box>
            <IconBtn
              icon="fas fa-cog"
              position="absolute"
              right={0}
              top={0}
              size="1.75rem"
              fontSize="1.125rem"
              bgColor="lightGold"
              onClick={() => setSettingsOn(true)}
            />
          </div>
          <MainSelect
            className="mb-3"
            padding="ms-pad"
            tab={stNames[shownSt]}
            setTab={(e) =>
              setUI((prev) => ({
                ...prev,
                shownSt: prev.stNames.indexOf(e.target.value)
              }))
            }
            options={stNames}
          />
          <LimitedBox height="27.375rem" className="hide-sb">
            <PartyCard />
            <WeaponCard />
            <ArtifactsCard />
          </LimitedBox>
          <div className="mt-3 flex align-end">
            {hasAny && (
              <BtnOnCard onClick={() => setTyping(true)}>
                Save Artifacts {">>"}
              </BtnOnCard>
            )}
            <Img
              width="2.5rem"
              className="pointer ml-auto"
              src={wikia + "a/a0/Icon_Inventory.png"}
              alt="inventory"
              onClick={() => setPoss("Inventory")}
            />
          </div>
        </>
      )}
      {typing && <TypeBox setTyping={setTyping} />}
    </Card>
  );
}

function TypeBox({ setTyping }) {
  const [temp, setTemp] = useState("");
  const { setInv } = useContext(FixedContext);
  const { shownSt } = useContext(UIContext).ui;
  const { artsChest } = useContext(ArtsContext);
  return (
    <ModalCarpet>
      <Box
        bgcolor={colorCodes.cardBg2}
        borderRadius="0.75rem"
        className="pt-4 px-3 pb-2 flex-col relative"
      >
        <CloseBtn
          color="white"
          bgColor="red"
          onClick={() => setTyping(false)}
        />
        <Typography
          component="input"
          variant="h6"
          className="mt-4 mb-3 px-2 py-1"
          placeholder="Enter loadout's name"
          value={temp}
          onChange={(e) => setTemp(e.target.value)}
        />
        <BtnOnCard
          className="mx-auto mb-1"
          bgColor="lightGold"
          onClick={() => {
            setInv((prev) => {
              const inv = deepCopy(prev);
              inv.Loadouts.unshift({
                name: temp,
                arts: artsChest[shownSt].pieces
              });
              return inv;
            });
            setTyping(false);
          }}
        >
          Proceed
        </BtnOnCard>
      </Box>
    </ModalCarpet>
  );
}
