export const AmberBuffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        Increases the <LGreen>CRIT Rate</LGreen> of <LGreen>Fiery Rain</LGreen>{" "}
        by <Green>10%</Green>.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "self",
    addBnes: (_, bonuses) => {
      bonuses.EB.CR += 0.1;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        Aimed Shot hits on weak spots increase <LGreen>ATK</LGreen> by{" "}
        <Green>15%</Green> for 10s.
      </>
    ),
    affect: "self",
    isGranted: (char) => char.ascension >= 4,
    addBnes: (ATRs) => {
      ATRs["ATK%"] += 15;
    }
  },
  {
    src: "Constellation 2",
    desc: () => (
      <>
        <LGreen>Baron Bunny</LGreen>'s explosion via manual detonation deals{" "}
        <Green>200%</Green> <LGreen>additional DMG</LGreen>.
      </>
    ),
    affect: "self",
    isGranted: (char) => char.constellation >= 2,
    addBnes: (_, bonuses) => {
      bonuses.ES.DMG += 200;
    }
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        Fiery Rain increases all party members' <LGreen>ATK</LGreen> by{" "}
        <Green>15%</Green> for 10s.
      </>
    ),
    affect: "party",
    isGranted: (char) => char.constellation >= 6,
    addBnes: (ATRs) => {
      ATRs["ATK%"] += 15;
    }
  }
];

