import { deepCopy } from "../../App";
import characters from "../../data/characters";
import { initWp, initArtPiece } from "../../data/initConfig";
import buffsFrArts from "../../data/artifacts/buffsFrArts";
import {
  getArtSets,
  getTmBC,
  getCharDC,
  getMainArtsBC,
  getWpRange
} from "../../helpers";

export function getCharsData(action, charName, party, filter) {
  let result = [];
  for (let name in characters) {
    const { icon, rarity, vision, weapon } = characters[name];
    if (action === "switch") {
      result.push({ name, icon, rarity, vision, weapon });
    } else if (name !== charName) {
      let existed = false;
      for (let tm of party) {
        if (name === tm.name) {
          existed = true;
          break;
        }
      }
      if (!existed) {
        result.push({ name, icon, rarity, vision, weapon });
      }
    }
  }
  if (filter.type.length) {
    result = result.filter((char) => char[filter.type] === filter.value);
  }
  return result;
}

export function addTm(party, tmI, { name, vision, weapon }, setTm) {
  const oldWp = characters[party[tmI].name]?.weapon;
  const wpRange = getWpRange(party);
  party[tmI] = {
    name,
    BC: getTmBC(name),
    DC: getCharDC(name, false)
  };
  setTm(party, vision, wpRange, true, weapon, oldWp);
}

export function addTempSt(name, setTempSts) {
  setTempSts((prev) => {
    const sts = deepCopy(prev);
    let max = 1;
    for (let st of sts) {
      const words = st.name.split(" ");
      const num = Number(words[words.length - 1]);
      if (!isNaN(num)) {
        max = Math.max(num + 1, max + 1);
      }
    }
    sts.push({
      name: "Set-up " + max,
      wp: name,
      checked: false
    });
    return sts;
  });
}

export function setWp(name, index, setWpsRack) {
  setWpsRack((prev) => {
    const rack = deepCopy(prev);
    rack[index] = initWp({ name });
    return rack;
  });
}

export function setArt(index, artType, setArtsChest, info) {
  setArtsChest((prev) => {
    const chest = deepCopy(prev);
    const arts = chest[index];
    arts.pieces[artType] = initArtPiece(info);
    let oldLv = arts.sets[0]?.bonusLv;
    arts.sets = getArtSets(arts.pieces);
    const set = arts.sets[0];
    if (set) {
      if (set.bonusLv && oldLv === 0) {
        arts.BC = getMainArtsBC(set.name);
        let pos;
        for (let i in arts.subBC) {
          if (set.name === arts.subBC[i].set) {
            pos = i;
            break;
          }
        }
        if (pos !== undefined) {
          arts.subBC.splice(pos, 1);
        }
      } else if (!set.bonusLv && oldLv && buffsFrArts[set.name]) {
        arts.BC = [];
        for (let buff of buffsFrArts[set.name]) {
          if (buff.affect !== "self") {
            arts.subBC.push({ set: set.name, activated: false, index });
          }
        }
      }
    }
    return chest;
  });
}

export function createWp(name, type, setInv) {
  setInv((prev) => {
    const inv = deepCopy(prev);
    inv.Weapons.unshift({ name, type, level: "1/20", refinement: 1 });
    return inv;
  });
}

export function createArt(set, mainSType, type, setInv) {
  setInv((prev) => {
    const inv = deepCopy(prev);
    inv.Artifacts.unshift({ type, ...initArtPiece({ set, mainSType }) });
    return inv;
  });
}

export function archiveArt(index, set, mainSType, type, setInv) {
  setInv((prev) => {
    const inv = deepCopy(prev);
    inv.Loadouts[index].arts[type] = initArtPiece({ set, mainSType });
    return inv;
  });
}
