import { tfmRxns } from "./data/elements";
import characters from "./data/characters";
import weapons from "./data/weapons";
import buffsFrChar from "./data/characters/buffsFrChar";
import debuffsFrChar from "./data/characters/debuffsFrChar";
import buffsFrArts from "./data/artifacts/buffsFrArts";
import { rsnNames, infusePrior } from "./data/elements";
import { elmtDmgBnes } from "./data/misc";
import { aHasB } from "./App";

export function incAllNAs(bonuses, val) {
  ["NA", "CA", "PA"].forEach((type) => {
    bonuses[type].DMG += val;
  });
}

export function incAllElmtDmgBnes(ATRs, val) {
  elmtDmgBnes.forEach((type) => {
    ATRs[type] += val;
  });
}

export function getNamesForTables({ actvTalents, weapon }, wpsRack, cmpLdts) {
  const result = actvTalents.map((tl) => ({
    atkName: tl.type,
    fieldNames: tl.stats.map(({ name }) => name)
  }));
  result.push({
    atkName: "Reactions DMG",
    fieldNames: Object.keys(tfmRxns)
  });
  if (cmpLdts.length === 1) {
    const { pasvProcD } = weapons[weapon][wpsRack[cmpLdts[0]].name];
    if (pasvProcD) {
      const node = { atkName: "Weapon Attack Proc", fieldNames: [] };
      pasvProcD.forEach((dmg) => {
        node.fieldNames.push(dmg.name);
      });
      result.push(node);
    }
  }
  return result;
}

export function processNumInp(inp, before, max) {
  if (inp === "") {
    return 0;
  } else if (inp.slice(-1) === ".") {
    return inp;
  } else {
    inp = parseFloat(inp);
    if (!isNaN(inp) && inp <= max) {
      return Math.round(inp * 10) / 10;
    }
    return before;
  }
}

export function addFinal({ final }, node) {
  if (final) node.final = true;
}

export function getSelfBC(charName) {
  const result = [];
  if (buffsFrChar[charName]) {
    buffsFrChar[charName].forEach((buff, index) => {
      if (buff.affect !== "teammates") {
        const node = { activated: false, index };
        if (buff.maxStacks) node.value = 1;
        addFinal(buff, node);
        result.push(node);
      }
    });
  }
  return result;
}

export function getTmBC(tmName) {
  const result = [];
  if (tmName.length && buffsFrChar[tmName]) {
    buffsFrChar[tmName].forEach((buff, index) => {
      if (buff.affect !== "self") {
        const node = { activated: false, index };
        if (buff.labels) {
          node.inputs = [...buff.inputs];
        }
        result.push(node);
      }
    });
  }
  return result;
}

export function getCharDC(name, frSelf) {
  const result = [];
  if (debuffsFrChar[name]) {
    debuffsFrChar[name].forEach((debuff) => {
      const node = { activated: false };
      if (debuff.inputTypes) {
        node.inputs = [];
        debuff.inputTypes.forEach((type) => {
          if (frSelf) {
            if (type === "absorption") {
              node.inputs.push("Pyro");
            }
          } else {
            if (type === "level") {
              node.inputs.push(1);
            } else if (type === "absorption") {
              node.inputs.push("Pyro");
            }
          }
        });
      }
      result.push(node);
    });
  }
  return result;
}

export function getWpData(name) {
  for (let type in weapons) {
    if (weapons[type][name]) return weapons[type][name];
  }
}

export function getMainWpBC(name) {
  let result = [];
  const { buffs } = getWpData(name);
  if (buffs) {
    buffs.forEach((buff, index) => {
      if (buff.affect !== "teammates") {
        const node = { activated: false, index };
        if (buff.inputTypes) {
          node.inputs = [];
          buff.inputTypes.forEach((type, j) => {
            if (type === "stacks") {
              node.inputs.push(1);
            } else if (type === "check") {
              node.inputs.push(false);
            } else if (type === "choices") {
              node.inputs.push(buff.values[j][0]);
            }
          });
        }
        addFinal(buff, node);
        result.push(node);
      }
    });
  }
  return result;
}

export function getSubWpBC(wpType) {
  const result = [];
  const wps = weapons[wpType];
  for (let name in wps) {
    const { buffs } = wps[name];
    if (buffs) {
      buffs.forEach((buff, index) => {
        if (buff.affect !== "self") {
          const node = { name, activated: false, refinement: 1, index };
          if (buff.inputTypes) {
            node.inputs = [];
            buff.inputTypes.forEach((type, j) => {
              if (type === "choices") {
                node.inputs.push(buff.values[j][0]);
              }
            });
          }
          result.push(node);
        }
      });
    }
  }
  return result;
}

export function getArtSets(arts = {}) {
  const sets = [];
  const count = {};
  for (let key in arts) {
    if (arts[key].set) {
      const name = arts[key].set;
      count[name] = (count[name] || 0) + 1;
      if (count[name] === 2) {
        sets.push({ name, bonusLv: 0 });
      } else if (count[name] === 4) {
        sets[0].bonusLv = 1;
      }
    }
  }
  return sets;
}

export function getMainArtsBC(setName) {
  const result = [];
  if (buffsFrArts[setName]) {
    buffsFrArts[setName].forEach((buff, index) => {
      if (buff.affect !== "teammates") {
        const node = { activated: false, index };
        if (buff.inputType === "stacks") {
          node.value = 1;
        } else if (buff.inputType === "crystallize") {
          node.value = "Pyro";
        }
        addFinal(buff, node);
        result.push(node);
      }
    });
  }
  return result;
}

export function getSubArtsBC(mainSet) {
  const result = [];
  for (let set in buffsFrArts) {
    if (set !== mainSet) {
      buffsFrArts[set].forEach((buff, index) => {
        if (buff.affect !== "self") {
          const node = { set, activated: false, index };
          addFinal(buff, node);
          result.push(node);
        }
      });
    }
  }
  return result;
}

export function getSubArtsDC() {
  return [
    { set: "Viridescent Venerer", activated: false, index: 0, input: "Pyro" }
  ];
}

export function getRsns(party, selfVs) {
  const result = [];
  const count = {};
  count[selfVs] = 1;
  party.forEach((tm) => {
    if (tm.name) {
      const { vision } = characters[tm.name];
      count[vision] = (count[vision] || 0) + 1;
      if (count[vision] === 2 && rsnNames[vision]) {
        result.push(rsnNames[vision]);
      }
    }
  });
  return result;
}

export function getWpRange(party) {
  const result = [];
  for (let tm of party) {
    if (tm.name) {
      const { weapon } = characters[tm.name];
      if (result.indexOf(weapon) === -1) {
        result.push(weapon);
      }
    }
  }
  return result;
}

function getSTGestInf(type, infs, vision, party) {
  const contenders = ["Physical"];
  for (let inf of infs) {
    if (aHasB(inf.range, type)) {
      if (!inf.canBeOverrided) return vision;
      else if (!aHasB(contenders, vision)) contenders.unshift(vision);
    }
  }
  for (let tm of party) {
    if (tm.inf) {
      for (let i in contenders) {
        if (
          infusePrior.indexOf(tm.inf.elmt) < infusePrior.indexOf(contenders[i])
        ) {
          contenders.splice(i, 0, tm.inf.elmt);
          break;
        }
      }
    }
  }
  return contenders[0];
}

export function getFinalInfs({ infs }, { vision }, party) {
  const result = {};
  for (let type of ["NA", "CA", "PA"]) {
    result[type] = getSTGestInf(type, infs, vision, party);
  }
  return result;
}

export function getXtraNaLv(char, party) {
  if (char.name === "Tartaglia") return 1;
  for (let tm of party) {
    if (tm.name === "Tartaglia") return 1;
  }
  return 0;
}
