import React, { useContext } from "react";
import { FixedContext } from "../../App";
import { Typography } from "@material-ui/core";
import { ColoredBox } from "../../styledCpns/DataDisplay";
import { BtnOnCard } from "../../styledCpns/Inputs";
import { dsArtType } from "../../data/misc";

export default function ArtSlot({ artType, modalType }) {
  const { setUI } = useContext(FixedContext);
  return (
    <div className="flex-col">
      <ColoredBox className="px-4 pt-1" bgColor="cardBg3">
        <Typography variant="h5">
          <strong>{dsArtType[artType]}</strong>
        </Typography>
      </ColoredBox>
      <Typography variant="h6" align="center" className="my-4 ellipsis">
        Not equipping any artifact
      </Typography>
      <BtnOnCard
        margin="0 0.75rem"
        bgColor="lightGold"
        color="black"
        className="mx-auto"
        onClick={() =>
          setUI((prev) => ({ ...prev, modalName: artType, modalType }))
        }
      >
        Switch
      </BtnOnCard>
    </div>
  );
}
