import styled from "styled-components";
import { IconBtn } from "../../styledCpns/Inputs";
import { colorCodes } from "../../data/misc";

export const ModalCarpet = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  z-index: 4;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  @media (min-width: 769px) {
    &.not-center-y {
      align-items: flex-start;
    }
  }
`;

export const ModalInner = styled.div`
  width: 90vw;
  height: 85vh;
  border-radius: 0.625rem;
  background-color: ${colorCodes.cardBg2};
  position: relative;
  .heading {
    height: 12%;
  }
  .heading_inner {
    border-radius: 0.5rem 0.5rem 0 0;
    background-color: ${colorCodes.orange};
    color: black;
  }
  .body {
    height: 88%;
  }
  .living_room {
    padding-bottom: 3.5rem;
  }
  @media (min-width: 440px) {
    width: 404.01px;
    .big-icon {
      width: 4rem;
    }
  }
  @media (min-width: 610px) {
    width: 538.8px;
  }
  @media (min-width: 769px) {
    margin-top: 10vh;
    height: 75vh;
    width: 693.62px;
  }
  @media (min-width: 1025px) {
    width: 943.2px;
  }
`;

export const ModalGridItem = styled.div`
  flex-grow: 0;
  max-width: 33%;
  flex-basis: 33%;
  position: relative;
  &.w-vision {
    padding: 0.625rem 0.625rem 0.25rem 0.25rem;
  }
  &.wo-vision {
    padding: 0.25rem;
  }
  @media (min-width: 360px) {
    &.w-vision {
      padding: 0.75rem 0.75rem 0.375rem 0.375rem;
    }
    &.wo-vision {
      padding: 0.5rem;
    }
  }
  @media (min-width: 440px) {
    max-width: 25%;
    flex-basis: 25%;
  }
  @media (min-width: 610px) {
    max-width: 20%;
    flex-basis: 20%;
    &.w-vision {
      padding: 0.875rem 0.875rem 0.675rem 0.675rem;
    }
  }
  @media (min-width: 769px) {
    max-width: 16.66667%;
    flex-basis: 16.66667%;
  }
  @media (min-width: 1025px) {
    max-width: 12.5%;
    flex-basis: 12.5%;
  }
`;

export const VisionIcon = styled.img`
  position: absolute;
  width: 30%;
  top: 0.25rem;
  right: 0.25rem;
  padding: 4px;
  border-radius: 50%;
  background-color: black;
`;

export const FilterBtn = styled(({ handleClick, ...others }) => (
  <IconBtn
    icon="fas fa-filter"
    position="absolute"
    top="50%"
    left="10%"
    size="1.75rem"
    fontSize="0.825rem"
    color="lightGold"
    bgColor="black"
    className="glow-on-hover"
    {...others}
    onClick={handleClick}
  />
))`
  margin-top: -0.825rem;
`;

export const StyledFilter = styled.div`
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 3;
  width: 100%;
  padding: 0 0.75rem;
  border-radius: 0 0 0.5rem 0.5rem;
  background-color: ${colorCodes.cardBg3};
  .filter_menu {
    display: flex;
    overflow: auto;
  }
  .vision_menu {
    width: 20rem;
  }
  .wp_menu {
    width: 15.25rem;
  }
  .filter_icon {
    cursor: pointer;
    border-radius: 50%;
  }
  .filter_icon:not(:last-child) {
    margin-right: 1rem;
  }
  .filter_icon.chosen {
    border: 2px solid white;
  }
  .my-wp {
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
  @media (min-width: 400px) {
    .my-wp {
      margin-top: 0.5rem;
    }
  }
  @media (min-width: 1025px) {
    .my-wp {
      margin-bottom: 0.5rem;
    }
  }
`;

export const BtmBar = styled.div`
  position: absolute;
  right: 0;
  bottom: 0;
  width: 100%;
  padding: 0.75rem 1rem;
  background-color: black;
  border-bottom-left-radius: inherit;
  border-bottom-right-radius: inherit;
`;
