import styled from "styled-components";
import React, { useState, useContext } from "react";
import { FixedContext } from "../../App";
import { Box, Typography } from "@material-ui/core";
import { CloseBtn, BtnOnCard } from "../../styledCpns/Inputs";
import { ColoredText, Img } from "../../styledCpns/DataDisplay";
import { LimitedBox } from "../../styledCpns/Layout";
import { FilterBtn } from "../Modal/styles";
import { StyledOverview, PossGridItem, StyledDetails } from "./styles";
import { Filter } from "../Modal/ModalContent";
import characters from "../../data/characters";
import weapons from "../../data/weapons";
import { colorCodes, wikia } from "../../data/misc";
import { initArts, initWp } from "../../data/initConfig";
import { getArtSets } from "../../helpers";

export default function Teapot({ teapot }) {
  const { setPoss } = useContext(FixedContext);
  const [filterOn, setFilterOn] = useState(false);
  const [filter, setFilter] = useState({ type: "", value: "" });
  const [chosenI, setChosenI] = useState(0);
  let data = teapot;
  if (filter.type) {
    data = data.filter((char) => {
      const charData = characters[char.name];
      return charData[filter.type] === filter.value;
    });
  }
  return (
    <div className="full-h">
      <div className="p-2 heading">
        <div className="heading_inner full-h flex align-center cmn-shadow relative">
          <div className="full-w">
            <Typography variant="h4" align="center">
              My Teapot
            </Typography>
          </div>
          <CloseBtn color="white" onClick={() => setPoss(null)} />
          <FilterBtn handleClick={() => setFilterOn(!filterOn)} />
          <Filter
            filterOn={filterOn}
            setFilterOn={setFilterOn}
            filter={filter}
            setFilter={(chosen, newFilter) => {
              setFilter(chosen ? { type: "" } : newFilter);
              setChosenI(0);
            }}
          />
        </div>
      </div>
      <div className="px-3 pt-2 body living_room">
        <div className="full-h overflow-auto">
          <div className="pb-3 full-h flex align-start">
            <StyledOverview className="pl-2 flex-wrap grow-1 overflow-auto hide-sb">
              {teapot.length ? (
                data.map((char, i) => {
                  const { rarity, icon } = characters[char.name];
                  const chosen = chosenI === i;
                  return (
                    <PossGridItem key={i}>
                      <Box
                        borderRadius="0.625rem"
                        bgcolor={colorCodes[rarity]}
                        boxShadow={3}
                        className={
                          "pt-3 pointer zoomin-on-hover flex overflow-hidden" +
                          (chosen ? " glowing" : "")
                        }
                        onClick={() => setChosenI(i)}
                      >
                        <Img src={wikia + icon + ".png"} alt="" width="100%" />
                      </Box>
                    </PossGridItem>
                  );
                })
              ) : (
                <div className="full-w pt-5 pr-5">
                  <Typography variant="h4" align="center">
                    No one lives here. Invite a character to your Teapot!
                  </Typography>
                </div>
              )}
            </StyledOverview>
            <StyledDetails forChar={true}>
              <div className="inner px-3 full-h flex-col align-center">
                {Boolean(data.length) && <Info info={data[chosenI]} />}
              </div>
            </StyledDetails>
          </div>
        </div>
      </div>
    </div>
  );
}

const HLS = ({ C, ...others }) => (
  <ColoredText component="span" variant="subtitle1" color={C} {...others} />
);

const Typo = styled(Typography)`
  margin-top: 0.25rem;
`;

function Info({ info }) {
  const { wp, arts, ...char } = info;
  const { setPoss, handlers } = useContext(FixedContext);
  const { vision, weapon } = characters[char.name];
  const { rarity } = weapons[weapon][wp.name];
  let numOfArts = 0;
  for (let type in arts) {
    if (arts[type].set) numOfArts++;
  }
  return (
    <>
      <LimitedBox height="calc(100% - 1.75rem)" className="custom-sb">
        <ColoredText variant="h1" align="center" color={vision}>
          {char.name}
        </ColoredText>
        <div className="px-2">
          <Typo>
            Level: <HLS C={vision}>{char.level}</HLS>
          </Typo>
          <Typo>
            Talent:{" "}
            <HLS C={vision}>
              {char["Normal Attack"]} / {char["Elemental Skill"]} /{" "}
              {char["Elemental Burst"]}
            </HLS>
          </Typo>
          <Typo>
            Constellation: <HLS C={vision}>{char.constellation}</HLS>
          </Typo>
          <Typo>
            Weapon: <HLS C={rarity}>{wp.name}</HLS>
          </Typo>
          <Typo className="pl-3">
            Level <HLS C={rarity}>{wp.level}</HLS>
          </Typo>
          <Typo className="pl-3">
            Refinement Rank <HLS C={rarity}>{wp.refinement}</HLS>
          </Typo>
          <Typo>Artifacts: ({numOfArts})</Typo>
          {getArtSets(arts).map((set, i) => (
            <Typo key={i} className="pl-3">
              <HLS C="gold">{set.name}</HLS> ({(set.bonusLv + 1) * 2})
            </Typo>
          ))}
        </div>
      </LimitedBox>
      <BtnOnCard
        className="mt-2 mx-auto"
        bgColor="lightGold"
        onClick={() => {
          handlers.initSession(char, initWp(wp), initArts(arts));
          setPoss(null);
        }}
      >
        Deploy
      </BtnOnCard>
    </>
  );
}
