export const DionaBuffs = [
  {
    src: "Constellation 2",
    desc: () => (
      <>
        Increases <LGreen>Icy Paws' DMG</LGreen> by <Green>15%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addBnes: (_, bonuses) => {
      bonuses.ES.DMG += 15;
    }
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        Characters within Signature Mix's radius will have their{" "}
        <LGreen>Elemental Mastery</LGreen> increased by <Green>200</Green> when
        HP is above 50%.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "party",
    addBnes: (ATRs) => {
      ATRs["Elemental Mastery"] += 200;
    }
  }
];

