import React from "react";
import { Select, Option } from "../../styledCpns/Inputs";
import { artSSTypes } from "../../data/artifacts/config";
import { ColoredText, ColoredBox } from "../../styledCpns/DataDisplay";
import { pctOf } from "../../data/misc";
import { SSVal } from "./styles";

export default function SSLine(props) {
  const { stat } = props;
  return (
    <ColoredBox bgColor="cardBg2" className="mt-2 pt-1 flex align-center">
      <Select
        color="white"
        fontSize="1.0625rem"
        className="pl-5 pr-2 no-apr bg-icon arrow-white ss-select"
        value={stat.type}
        onChange={props.changeSSType}
      >
        {artSSTypes.map((type) => (
          <Option key={type}>{type}</Option>
        ))}
      </Select>
      <ColoredText variant="body1">+</ColoredText>
      <SSVal value={stat.val} onChange={props.changeSSVal} />
      <span className="ml-1">{pctOf(stat.type)}</span>
    </ColoredBox>
  );
}
