import React, { useState } from "react";
import { CollapseHeading } from "../../styledCpns/DataDisplay";
import SelfBuff from "./SelfBuff";
import ElmtBuff from "./ElmtBuff";
import PartyBuff from "./PartyBuff";
import WpBuff from "./WpBuff";
import ArtsBuff from "./ArtsBuff";
import { StyledCollapse } from "../Modifiers.styled";

export default function Buff(props) {
  const [expanding, setExpanding] = useState([]);
  return (
    <div>
      {["Resonance & Reactions", "Self", "Party", "Weapons", "Artifacts"].map(
        (cate, i) => (
          <div key={cate} className={expanding[i] ? "mb-4" : ""}>
            <CollapseHeading
              className={expanding[i] ? "open" : "close"}
              mb="0.75rem"
              onClick={() =>
                setExpanding((prev) => {
                  const newEpd = [...prev];
                  newEpd[i] = !newEpd[i];
                  return newEpd;
                })
              }
            >
              From {cate}
            </CollapseHeading>
            <StyledCollapse in={expanding[i]}>
              {
                {
                  "Resonance & Reactions": <ElmtBuff />,
                  Self: <SelfBuff />,
                  Party: <PartyBuff />,
                  Weapons: <WpBuff />,
                  Artifacts: <ArtsBuff />
                }[cate]
              }
            </StyledCollapse>
          </div>
        )
      )}
    </div>
  );
}
