import React, { useContext } from "react";
import { CharContext, ResultContext } from "../../App";
import { Typography } from "@material-ui/core";
import { Table } from "../../styledCpns/DataDisplay";
import { BtnOnCard } from "../../styledCpns/Inputs";
import { pctOf, iptSTypes } from "../../data/misc";

export default function Attributes(props) {
  const { charData } = useContext(CharContext);
  const { ATRs } = useContext(ResultContext);
  const iptATRs = [...iptSTypes];
  iptATRs.splice(7, 0, charData.vision + " DMG Bonus");
  return (
    <div className="flex-col">
      <div className="px-2">
        <Table>
          {iptATRs.map((type) => (
            <div key={type} className="flex justify-between">
              <Typography variant="subtitle1" className="pl-3 pt-1">
                {type}
              </Typography>
              <Typography
                variant="subtitle1"
                align="right"
                className="pr-3 pt-1"
              >
                {Math.round(ATRs[type] * 10) / 10 + pctOf(type)}
              </Typography>
            </div>
          ))}
        </Table>
      </div>
      <BtnOnCard
        margin="0.75rem auto 0"
        bgColor="lightGold"
        color="black"
        onClick={() => props.setBoardType("ATRs")}
      >
        Details
      </BtnOnCard>
    </div>
  );
}
