import { Green, LGreen } from "../../../styledCpns/DataDisplay";
import { mapToLevels } from "../../misc";
import { bowCAs, lightPAs, multMult } from "../config";

export const YoimiyaBuffs = [
  {
    src: "Elemental Skill",
    desc: (_, char) => (
      <>
        Arrows fired by Yoimiya's <LGreen>Normal Attack</LGreen> will be Blazing
        Arrows, and their <LGreen>DMG</LGreen> will be increased by{" "}
        <Green>{YoimiyaBuffs[0].bnValue(char)}%</Green> and converted to{" "}
        <LGreen>Pyro DMG</LGreen>.
      </>
    ),
    isGranted: () => true,
    affect: "self",
    addBnes: (_, bonuses, __, ___, _ph1, char) => {
      bonuses.NA.DMG += YoimiyaBuffs[0].bnValue(char);
    },
    bnValue: (char) => {
      const level = char["Elemental Skill"] + (char.constellation >= 3 ? 3 : 0);
      return Math.round(37.91 * multMult[5][level] * 100) / 100;
    },
    canInfuse: () => true,
    infuseRange: ["NA"],
    canBeOverrided: false
  },
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        During Niwabi Fire-Dance, shots from Yoimiya's Normal Attack will
        increase her <LGreen>Pyro DMG Bonus</LGreen> by <Green>2%</Green> on
        hit. This effect lasts for 3s and can have a maximum of{" "}
        <Green>10</Green> stacks.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "self",
    maxStacks: 10,
    addBnes: (ATRs, _, __, stacks) => {
      ATRs["Pyro DMG Bonus"] += 2 * stacks;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        Using Ryuukin Saxifrage causes nearby party members to gain a{" "}
        <Green>10%</Green> <LGreen>ATK</LGreen> increase for 15s. Additionally,
        a further ATK Bonus will be added on based on the number of "Tricks of
        the Trouble-Maker" stacks Yoimiya possesses when using Ryuukin
        Saxifrage. Each stack increases this <LGreen>ATK Bonus</LGreen> by{" "}
        <Green>1%</Green>.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "teammates",
    labels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [10],
    addBnes: (ATRs, _, __, stacks) => {
      ATRs["ATK%"] += 10 + stacks;
    }
  },
  {
    src: "Constellation 1",
    desc: () => (
      <>
        When an opponent affected by Aurous Blaze is defeated within its
        duration, Yoimiya's <LGreen>ATK</LGreen> is increased by{" "}
        <Green>20%</Green> for 20s.
      </>
    ),
    isGranted: (char) => char.constellation >= 1,
    affect: "self",
    addBnes: (ATRs) => {
      ATRs["ATK%"] += 20;
    }
  },
  {
    src: "Constellation 2",
    desc: () => (
      <>
        When Yoimiya's Pyro DMG scores a CRIT Hit, she will gain a{" "}
        <Green>25%</Green> <LGreen>Pyro DMG Bonus</LGreen> for 6s. (Can be
        triggered off-field.)
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addBnes: (ATRs) => {
      ATRs["Pyro DMG Bonus"] += 25;
    }
  }
];

