import React, { useContext } from "react";
import { CharContext, FixedContext } from "../../App";
import { Typography } from "@material-ui/core";
import { Img } from "../../styledCpns/DataDisplay";
import { wikia } from "../../data/misc";

export default function Constellation() {
  const { char, charData } = useContext(CharContext);
  const { setChar } = useContext(FixedContext);
  const handleClick = (i) => {
    setChar((prev) => ({
      ...prev,
      constellation: prev.constellation === i + 1 ? i : i + 1
    }));
  };
  return (
    <div>
      {charData.constellation.map((cons, i) => (
        <div
          key={i}
          className={
            "flex align-center" +
            (char.constellation >= i + 1 ? "" : " unavailable")
          }
        >
          <Img
            className="my-2 mr-3 pointer"
            width="3.25rem"
            src={wikia + cons.image + ".png"}
            alt=""
            onClick={() => handleClick(i)}
          />
          <Typography
            className="pointer"
            variant="h6"
            onClick={() => handleClick(i)}
          >
            <strong>{cons.name}</strong>
          </Typography>
        </div>
      ))}
    </div>
  );
}
