import { Green, LGreen, Red, Electro } from "../../../styledCpns/DataDisplay";
import { mapToLevels } from "../../misc";
import { bowCAs, lightPAs, multMult } from "../config";
import characters from "../index";

export const SaraBuffs = [
  {
    src: "Elemental Skill",
    desc: (toSelf, _, { inputs }) => (
      <>
        Grants the active character within its AoE an <LGreen>ATK Bonus</LGreen>{" "}
        based on Kujou Sara's <LGreen>Base ATK</LGreen>.{" "}
        {toSelf ? "" : <Red>ATK Bonus: {SaraBuffs[0].bnValue(...inputs)}.</Red>}
      </>
    ),
    isGranted: () => true,
    affect: "party",
    labels: ["Base ATK", "Elemental Skill Level"],
    inputs: [16, 1],
    inputTypes: ["text", "text"],
    maxs: [9999, 13],
    addBnes: (ATRs, _, __, inputs, toSelf, char) => {
      const bnValue = SaraBuffs[0];
      ATRs.ATK += toSelf
        ? bnValue(
            ATRs["Base ATK"],
            char["Elemental Skill"] + (char.constellation >= 5 ? 3 : 0)
          )
        : bnValue(...inputs);
    },
    bnValue: (baseATK, level) =>
      Math.round(baseATK * ((42.96 * multMult[2][level]) / 100))
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        The <Electro>Electro DMG</Electro> of characters who have had their ATK
        increased by Tengu Juurai has its <LGreen>Crit DMG</LGreen> increased by{" "}
        <Green>60%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "party",
    addBnes: (_, bonuses, __, ___, toSelf, { name }) => {
      if (toSelf || characters[name].vision === "Electro")
        bonuses.Elemental.CD += 0.6;
    }
  }
];

